import React from 'react';
import { useAuthStore } from '../../stores/authStore';
import { X, LogOut, User } from 'lucide-react';
import './Navigation.css';

function Navigation({ activeTab, setActiveTab, isOpen, onClose }) {
  const { userData, isAdmin, isSubAdmin, isApprover, isPTScheduleEditor, logout } = useAuthStore();

  const tabs = [
    { id: 'dashboard', label: '대시보드', show: true },
    { id: 'calendar', label: '일정 캘린더', show: true },
    { id: 'annualLeave', label: '연차 관리', show: !isAdmin },
    { id: 'ptSchedule', label: 'PT 스케줄', show: true },
    { id: 'approval', label: '연차 승인', show: isApprover && !isAdmin },
    { id: 'department', label: '조직 구성', show: true },
    { id: 'notes', label: '📚 노트', show: true },
    { id: 'messenger', label: '💬 메신저', show: true },
    { id: 'profile', label: '👤 내 프로필', show: true },
    { id: 'settings', label: '⚙️ 설정', show: true },
    { id: 'admin-leave', label: '사원연차 관리', show: isAdmin || isSubAdmin },
    { id: 'approval-line', label: '결재선 관리', show: isAdmin || isSubAdmin },
    { id: 'admin', label: '사원 관리', show: isAdmin || isSubAdmin },
    { id: 'slack-settings', label: 'Slack 설정', show: isAdmin || isSubAdmin },
  ];

  const handleTabClick = (tabId) => {
    setActiveTab(tabId);
    // 모바일에서 메뉴 선택 시 자동으로 닫기
    if (window.innerWidth <= 768) {
      onClose();
    }
  };

  const handleLogout = async () => {
    if (confirm('로그아웃 하시겠습니까?')) {
      await logout();
    }
  };

  return (
    <>
      {/* 오버레이 (모바일/태블릿) */}
      {isOpen && (
        <div className="nav-overlay" onClick={onClose}></div>
      )}
      
      {/* 사이드바 네비게이션 */}
      <nav className={`navigation-sidebar ${isOpen ? 'open' : ''}`}>
        <div className="nav-header">
          <h2>메뉴</h2>
          <button className="nav-close-btn" onClick={onClose} title="메뉴 닫기">
            <X size={24} />
          </button>
        </div>
        
        {/* 사용자 프로필 섹션 (모바일 전용) */}
        <div className="nav-user-profile">
          <div className="nav-user-avatar">
            <User size={24} />
          </div>
          <div className="nav-user-info">
            <div className="nav-user-name">{userData?.name || userData?.email}</div>
            <div className="nav-user-badges">
              {isAdmin && <span className="nav-badge badge-admin">관리자</span>}
              {isSubAdmin && !isAdmin && <span className="nav-badge badge-subadmin">부관리자</span>}
              {isApprover && !isAdmin && <span className="nav-badge badge-approver">승인자</span>}
              {isPTScheduleEditor && <span className="nav-badge badge-pt">PT편집자</span>}
            </div>
          </div>
        </div>
        
        <div className="nav-items">
          {tabs.filter(tab => tab.show).map(tab => (
            <button
              key={tab.id}
              className={`nav-item ${activeTab === tab.id ? 'active' : ''}`}
              onClick={() => handleTabClick(tab.id)}
            >
              {tab.label}
            </button>
          ))}
        </div>
        
        {/* 로그아웃 버튼 (최하단) */}
        <div className="nav-footer">
          <button className="nav-logout-btn" onClick={handleLogout}>
            <LogOut size={18} />
            <span>로그아웃</span>
          </button>
        </div>
      </nav>
    </>
  );
}

export default Navigation;
