import React, { useState, useEffect } from 'react';
import { db } from '../config/firebase';
import { 
  collection, 
  query, 
  where, 
  getDocs, 
  doc,
  updateDoc,
  addDoc,
  serverTimestamp
} from 'firebase/firestore';
import { useAuthStore } from '../stores/authStore';
import './ApprovalManagement.css';

function ApprovalManagement() {
  const { user } = useAuthStore();
  
  const [pendingRequests, setPendingRequests] = useState([]);
  const [myEmployees, setMyEmployees] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (user) {
      loadMyEmployees();
    }
  }, [user]);

  // 담당 사원 목록 로드
  const loadMyEmployees = async () => {
    try {
      const q = query(
        collection(db, 'employees'),
        where('approver', '==', user.uid)
      );
      const snapshot = await getDocs(q);

      const employees = [];
      snapshot.forEach(docSnapshot => {
        employees.push({ id: docSnapshot.id, ...docSnapshot.data() });
      });

      setMyEmployees(employees);

      if (employees.length > 0) {
        loadPendingRequests(employees.map(emp => emp.id));
      } else {
        setLoading(false);
      }
    } catch (error) {
      console.error('담당 사원 목록 로드 실패:', error);
      setLoading(false);
    }
  };

  // 승인 대기 목록 로드
  const loadPendingRequests = async (employeeIds) => {
    try {
      // pending 조회
      const pendingQuery = query(
        collection(db, 'leaveUsage'),
        where('approvalStatus', '==', 'pending')
      );
      const pendingSnapshot = await getDocs(pendingQuery);

      // deletion-pending 조회
      const deletionQuery = query(
        collection(db, 'leaveUsage'),
        where('approvalStatus', '==', 'deletion-pending')
      );
      const deletionSnapshot = await getDocs(deletionQuery);

      const requests = [];

      // pending 처리
      for (const docSnapshot of pendingSnapshot.docs) {
        const data = docSnapshot.data();
        if (employeeIds.includes(data.userId)) {
          const userDoc = await getDocs(
            query(collection(db, 'employees'), where('__name__', '==', data.userId))
          );
          
          const userName = !userDoc.empty ? userDoc.docs[0].data().name : data.userId;
          
          requests.push({
            id: docSnapshot.id,
            ...data,
            userName,
            requestType: 'approval'
          });
        }
      }

      // deletion-pending 처리
      for (const docSnapshot of deletionSnapshot.docs) {
        const data = docSnapshot.data();
        if (employeeIds.includes(data.userId)) {
          const userDoc = await getDocs(
            query(collection(db, 'employees'), where('__name__', '==', data.userId))
          );
          
          const userName = !userDoc.empty ? userDoc.docs[0].data().name : data.userId;
          
          requests.push({
            id: docSnapshot.id,
            ...data,
            userName,
            requestType: 'deletion'
          });
        }
      }

      // 날짜 기준 내림차순 정렬
      requests.sort((a, b) => (b.date || '').localeCompare(a.date || ''));
      
      setPendingRequests(requests);
      setLoading(false);
    } catch (error) {
      console.error('승인 대기 목록 로드 실패:', error);
      setLoading(false);
    }
  };

  // 연차 승인/반려
  const handleApproval = async (requestId, userId, date, status) => {
    try {
      if (!confirm(`이 연차를 ${status === 'approved' ? '승인' : '반려'}하시겠습니까?`)) {
        return;
      }

      // leaveUsage 업데이트
      await updateDoc(doc(db, 'leaveUsage', requestId), {
        approvalStatus: status,
        approvedBy: user.uid,
        approvedAt: serverTimestamp()
      });

      // schedules 업데이트
      const scheduleQuery = query(
        collection(db, 'schedules'),
        where('userId', '==', userId),
        where('date', '==', date)
      );
      const scheduleSnapshot = await getDocs(scheduleQuery);

      scheduleSnapshot.forEach(async (docSnapshot) => {
        await updateDoc(doc(db, 'schedules', docSnapshot.id), {
          approvalStatus: status,
          approvedBy: user.uid,
          approvedAt: serverTimestamp()
        });
      });

      // 신청자에게 알림 생성
      await addDoc(collection(db, 'notifications'), {
        userId: userId,
        title: status === 'approved' ? '연차 승인' : '연차 반려',
        message: `${date} 연차 신청이 ${status === 'approved' ? '승인' : '반려'}되었습니다.`,
        type: 'leave-result',
        read: false,
        createdAt: serverTimestamp()
      });

      alert(status === 'approved' ? '승인되었습니다.' : '반려되었습니다.');
      loadMyEmployees();
    } catch (error) {
      console.error('처리 실패:', error);
      alert('처리 실패: ' + error.message);
    }
  };

  // 취소 승인
  const handleDeletionApproval = async (requestId, userId, date) => {
    try {
      if (!confirm('이 연차 취소를 승인하시겠습니까?')) {
        return;
      }

      // cancelled 상태로 변경
      await updateDoc(doc(db, 'leaveUsage', requestId), {
        approvalStatus: 'cancelled',
        cancelledAt: serverTimestamp()
      });

      // schedules 업데이트
      const scheduleQuery = query(
        collection(db, 'schedules'),
        where('userId', '==', userId),
        where('date', '==', date)
      );
      const scheduleSnapshot = await getDocs(scheduleQuery);

      scheduleSnapshot.forEach(async (docSnapshot) => {
        await updateDoc(doc(db, 'schedules', docSnapshot.id), {
          approvalStatus: 'cancelled',
          cancelledAt: serverTimestamp()
        });
      });

      // 신청자에게 알림 생성
      await addDoc(collection(db, 'notifications'), {
        userId: userId,
        title: '연차 취소 승인',
        message: `${date} 연차 취소가 승인되었습니다.`,
        type: 'leave-cancellation',
        read: false,
        createdAt: serverTimestamp()
      });

      alert('취소가 승인되었습니다.');
      loadMyEmployees();
    } catch (error) {
      console.error('처리 실패:', error);
      alert('처리 실패: ' + error.message);
    }
  };

  // 취소 반려
  const handleDeletionRejection = async (requestId, userId, date) => {
    try {
      if (!confirm('이 연차 취소를 반려하시겠습니까?')) {
        return;
      }

      // approved 상태로 되돌림
      await updateDoc(doc(db, 'leaveUsage', requestId), {
        approvalStatus: 'approved',
        deletionRejectedAt: serverTimestamp()
      });

      // schedules 업데이트
      const scheduleQuery = query(
        collection(db, 'schedules'),
        where('userId', '==', userId),
        where('date', '==', date)
      );
      const scheduleSnapshot = await getDocs(scheduleQuery);

      scheduleSnapshot.forEach(async (docSnapshot) => {
        await updateDoc(doc(db, 'schedules', docSnapshot.id), {
          approvalStatus: 'approved',
          deletionRejectedAt: serverTimestamp()
        });
      });

      // 신청자에게 알림 생성
      await addDoc(collection(db, 'notifications'), {
        userId: userId,
        title: '연차 취소 반려',
        message: `${date} 연차 취소가 반려되었습니다.`,
        type: 'leave-cancellation-rejected',
        read: false,
        createdAt: serverTimestamp()
      });

      alert('취소가 반려되었습니다.');
      loadMyEmployees();
    } catch (error) {
      console.error('처리 실패:', error);
      alert('처리 실패: ' + error.message);
    }
  };

  // 날짜 포맷
  const formatDate = (timestamp) => {
    if (!timestamp) return '-';
    const date = timestamp.toDate ? timestamp.toDate() : new Date(timestamp);
    return date.toLocaleDateString('ko-KR', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  if (loading) {
    return <div className="approval-container">로딩 중...</div>;
  }

  return (
    <div className="approval-container">
      <div className="section-header">
        <h2>연차 승인 관리</h2>
      </div>

      {/* 담당 사원 목록 */}
      <div className="employee-section">
        <h3>담당 사원 목록</h3>
        {myEmployees.length > 0 ? (
          <div className="employee-tags">
            {myEmployees.map(emp => (
              <span key={emp.id} className="employee-tag">
                {emp.name}
              </span>
            ))}
          </div>
        ) : (
          <p className="no-employees">담당 사원이 없습니다. 관리자에게 문의하세요.</p>
        )}
      </div>

      {/* 승인 대기 목록 */}
      <h3 className="list-title">승인 대기 목록</h3>
      
      {pendingRequests.length > 0 ? (
        <div className="approval-list">
          {pendingRequests.map(request => (
            <div key={request.id} className="approval-card">
              <div className="approval-card-content">
                {/* 사원 정보 */}
                <div className="info-block">
                  <div className="info-label">사원명</div>
                  <div className="info-value employee-name">{request.userName}</div>
                </div>

                {/* 연차 정보 */}
                <div className="info-block">
                  <div className="info-label">
                    {request.requestType === 'deletion' ? '🗑️ 삭제 요청' : '연차 내용'}
                  </div>
                  <div className="info-value">
                    {request.requestType === 'deletion' && (
                      <span className="deletion-badge">취소 요청</span>
                    )}
                    {request.type}
                    <span className="leave-amount">
                      ({request.amount > 0 ? `${request.amount}일` : '무급'})
                    </span>
                  </div>
                  <div className="leave-date">
                    📅 {request.date}
                  </div>
                </div>

                {/* 신청일 */}
                <div className="info-block">
                  <div className="info-label">신청일</div>
                  <div className="info-value request-date">
                    {formatDate(request.createdAt)}
                  </div>
                </div>

                {/* 승인/반려 버튼 */}
                <div className="approval-buttons">
                  {request.requestType === 'deletion' ? (
                    <>
                      <button
                        className="btn approve-btn"
                        onClick={() => handleDeletionApproval(request.id, request.userId, request.date)}
                      >
                        ✓ 삭제 승인
                      </button>
                      <button
                        className="btn reject-btn"
                        onClick={() => handleDeletionRejection(request.id, request.userId, request.date)}
                      >
                        ✕ 삭제 반려
                      </button>
                    </>
                  ) : (
                    <>
                      <button
                        className="btn approve-btn"
                        onClick={() => handleApproval(request.id, request.userId, request.date, 'approved')}
                      >
                        ✓ 승인
                      </button>
                      <button
                        className="btn reject-btn"
                        onClick={() => handleApproval(request.id, request.userId, request.date, 'rejected')}
                      >
                        ✕ 반려
                      </button>
                    </>
                  )}
                </div>
              </div>
            </div>
          ))}
        </div>
      ) : (
        <div className="empty-state">
          <div className="empty-icon">📋</div>
          <div className="empty-text">승인 대기중인 연차 신청이 없습니다.</div>
        </div>
      )}
    </div>
  );
}

export default ApprovalManagement;
