import React, { useState, useEffect } from 'react';
import { db } from '../config/firebase';
import { collection, getDocs, doc, updateDoc } from 'firebase/firestore';
import { GitBranch, Save, AlertCircle, CheckCircle, Users } from 'lucide-react';
import './ApprovalLine.css';

function ApprovalLine() {
  const [employees, setEmployees] = useState([]);
  const [approvers, setApprovers] = useState([]);
  const [approvalLines, setApprovalLines] = useState({});
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    loadData();
  }, []);

  const loadData = async () => {
    setLoading(true);
    try {
      const snapshot = await getDocs(collection(db, 'employees'));
      const employeeList = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));

      // 이름순 정렬
      employeeList.sort((a, b) => (a.name || '').localeCompare(b.name || ''));

      // 승인자 목록 추출 (isApprover 또는 isAdmin인 사람)
      const approverList = employeeList.filter(
        emp => emp.isApprover || emp.isAdmin || emp.isSubAdmin
      );

      // 현재 결재선 설정
      const lines = {};
      employeeList.forEach(emp => {
        lines[emp.id] = emp.approver || '';
      });

      setEmployees(employeeList);
      setApprovers(approverList);
      setApprovalLines(lines);
    } catch (error) {
      console.error('데이터 로드 실패:', error);
      alert('데이터를 불러오는데 실패했습니다.');
    }
    setLoading(false);
  };

  const handleApproverChange = (employeeId, approverId) => {
    setApprovalLines({
      ...approvalLines,
      [employeeId]: approverId
    });
  };

  const saveApprovalLines = async () => {
    if (!window.confirm('결재선을 저장하시겠습니까?')) {
      return;
    }

    setSaving(true);
    try {
      const updates = [];
      
      for (const [employeeId, approverId] of Object.entries(approvalLines)) {
        const employeeRef = doc(db, 'employees', employeeId);
        updates.push(
          updateDoc(employeeRef, {
            approver: approverId || null
          })
        );
      }

      await Promise.all(updates);
      alert('결재선이 저장되었습니다.');
      loadData();
    } catch (error) {
      console.error('저장 실패:', error);
      alert('저장에 실패했습니다: ' + error.message);
    }
    setSaving(false);
  };

  // 부서별 그룹화
  const groupByDepartment = () => {
    const groups = {};
    employees.forEach(emp => {
      const dept = emp.department || '미지정';
      if (!groups[dept]) {
        groups[dept] = [];
      }
      groups[dept].push(emp);
    });
    return groups;
  };

  // 승인자 이름 찾기
  const getApproverName = (approverId) => {
    if (!approverId) return '-';
    const approver = approvers.find(a => a.id === approverId);
    return approver ? approver.name : '(삭제된 사용자)';
  };

  // 자기 자신이 승인자인지 체크
  const isSelfApprover = (employeeId) => {
    return approvalLines[employeeId] === employeeId;
  };

  if (loading) {
    return (
      <div className="approval-line-page">
        <div className="loading-state">결재선 정보를 불러오는 중...</div>
      </div>
    );
  }

  const departmentGroups = groupByDepartment();

  return (
    <div className="approval-line-page">
      {/* 헤더 */}
      <div className="page-header">
        <div className="header-left">
          <GitBranch size={28} />
          <div>
            <h1>결재선 관리</h1>
            <p>사원별 연차 승인자를 지정합니다</p>
          </div>
        </div>

        <button 
          className="btn-primary" 
          onClick={saveApprovalLines}
          disabled={saving}
        >
          <Save size={18} />
          {saving ? '저장 중...' : '결재선 저장'}
        </button>
      </div>

      {/* 안내 메시지 */}
      <div className="info-banner">
        <AlertCircle size={20} />
        <div>
          <strong>결재선 안내:</strong>
          <ul>
            <li>각 사원의 연차 신청을 승인할 승인자를 지정합니다</li>
            <li>승인자로 지정할 수 있는 사람: 관리자, 부관리자, 승인자 권한이 있는 사원</li>
            <li>승인자가 없으면 해당 사원의 연차는 자동 승인되지 않습니다</li>
            <li><strong>주의:</strong> 자기 자신을 승인자로 지정하면 안 됩니다</li>
          </ul>
        </div>
      </div>

      {/* 승인자 통계 */}
      <div className="stats-grid">
        <div className="stat-card">
          <div className="stat-icon">
            <Users size={24} />
          </div>
          <div className="stat-content">
            <h3>전체 사원</h3>
            <div className="stat-value">{employees.length}</div>
            <p className="stat-label">명</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon">
            <CheckCircle size={24} />
          </div>
          <div className="stat-content">
            <h3>승인자 지정</h3>
            <div className="stat-value">
              {Object.values(approvalLines).filter(a => a).length}
            </div>
            <p className="stat-label">명 설정됨</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon">
            <GitBranch size={24} />
          </div>
          <div className="stat-content">
            <h3>가능한 승인자</h3>
            <div className="stat-value">{approvers.length}</div>
            <p className="stat-label">명 등록됨</p>
          </div>
        </div>
      </div>

      {/* 부서별 결재선 */}
      <div className="department-sections">
        {Object.entries(departmentGroups).map(([dept, empList]) => (
          <div key={dept} className="department-section">
            <div className="section-header">
              <h2>{dept}</h2>
              <span className="employee-count">{empList.length}명</span>
            </div>

            <div className="approval-grid">
              {empList.map(emp => (
                <div 
                  key={emp.id} 
                  className={`approval-card ${isSelfApprover(emp.id) ? 'warning' : ''}`}
                >
                  <div className="employee-info">
                    <div className="avatar">
                      {(emp.name || '?')[0]}
                    </div>
                    <div className="employee-details">
                      <h3>{emp.name || '이름 없음'}</h3>
                      <p>{emp.position || '직급 미지정'}</p>
                      {(emp.isAdmin || emp.isSubAdmin || emp.isApprover) && (
                        <div className="role-badges">
                          {emp.isAdmin && <span className="role-badge admin">관리자</span>}
                          {emp.isSubAdmin && <span className="role-badge sub-admin">부관리자</span>}
                          {emp.isApprover && <span className="role-badge approver">승인자</span>}
                        </div>
                      )}
                    </div>
                  </div>

                  <div className="approver-select">
                    <label>승인자</label>
                    <select
                      value={approvalLines[emp.id] || ''}
                      onChange={(e) => handleApproverChange(emp.id, e.target.value)}
                      className={isSelfApprover(emp.id) ? 'error' : ''}
                    >
                      <option value="">선택 안 함</option>
                      {approvers.map(approver => (
                        <option 
                          key={approver.id} 
                          value={approver.id}
                          disabled={approver.id === emp.id}
                        >
                          {approver.name}
                          {approver.isAdmin && ' (관리자)'}
                          {approver.isSubAdmin && ' (부관리자)'}
                          {approver.isApprover && ' (승인자)'}
                          {approver.id === emp.id && ' (자기 자신 - 선택 불가)'}
                        </option>
                      ))}
                    </select>
                    {isSelfApprover(emp.id) && (
                      <small className="error-text">⚠️ 자기 자신을 승인자로 지정할 수 없습니다</small>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        ))}
      </div>

      {/* 승인자 없는 사원 경고 */}
      {Object.values(approvalLines).filter(a => !a).length > 0 && (
        <div className="warning-banner">
          <AlertCircle size={20} />
          <div>
            <strong>경고:</strong> 승인자가 지정되지 않은 사원이 {Object.values(approvalLines).filter(a => !a).length}명 있습니다.
            승인자가 없으면 연차 승인 프로세스가 작동하지 않습니다.
          </div>
        </div>
      )}
    </div>
  );
}

export default ApprovalLine;
