import React, { useState } from 'react';
import './ScheduleModal.css';

function ScheduleModal({ date, currentSchedule, onSave, onClose, userName }) {
  const [selectedType, setSelectedType] = useState(currentSchedule || '출근');

  const scheduleTypes = [
    '출근',
    '재택',
    '연차',
    '반차(오전)',
    '반차(오후)',
    '병가',
    '경조사',
    '공가',
    '여름휴가',
    '겨울휴가'
  ];

  const handleSave = () => {
    onSave(date, selectedType);
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3>일정 등록</h3>
          <button className="modal-close-btn" onClick={onClose}>✕</button>
        </div>

        <div className="modal-body">
          <div className="modal-info">
            <p><strong>날짜:</strong> {date}</p>
            <p><strong>이름:</strong> {userName.split('@')[0]}</p>
          </div>

          <div className="schedule-type-grid">
            {scheduleTypes.map(type => (
              <button
                key={type}
                className={`schedule-type-btn ${selectedType === type ? 'active' : ''}`}
                onClick={() => setSelectedType(type)}
              >
                {type}
              </button>
            ))}
          </div>

          <div className="modal-notice">
            <p>💡 연차/반차/휴가는 승인 대기 상태로 등록됩니다.</p>
          </div>
        </div>

        <div className="modal-footer">
          <button className="modal-btn cancel-btn" onClick={onClose}>
            취소
          </button>
          <button className="modal-btn save-btn" onClick={handleSave}>
            저장
          </button>
        </div>
      </div>
    </div>
  );
}

export default ScheduleModal;
