import React, { useState, useEffect, useRef } from 'react';
import { 
  collection, query, where, getDocs, addDoc, updateDoc, 
  doc, orderBy, onSnapshot, serverTimestamp, deleteDoc, setDoc, getDoc
} from 'firebase/firestore';
import { db } from '../config/firebase';
import { useAuthStore } from '../stores/authStore';
import { 
  MessageCircle, Send, Search, User, Circle, Hash, Lock,
  Plus, MoreVertical, Smile, Paperclip, MessageSquare,
  X, Trash2, ChevronDown, ChevronRight, ArrowLeft
} from 'lucide-react';
import './Messenger.css';

const Messenger = () => {
  const { user, userData } = useAuthStore();
  
  // 채널 관련
  const [channels, setChannels] = useState([]);
  const [selectedChannel, setSelectedChannel] = useState(null);
  const [showCreateChannel, setShowCreateChannel] = useState(false);
  const [newChannelName, setNewChannelName] = useState('');
  const [newChannelDesc, setNewChannelDesc] = useState('');
  const [newChannelPrivate, setNewChannelPrivate] = useState(false);
  const [channelsExpanded, setChannelsExpanded] = useState(true);
  
  // DM 관련
  const [employees, setEmployees] = useState([]);
  const [filteredEmployees, setFilteredEmployees] = useState([]);
  const [selectedUser, setSelectedUser] = useState(null);
  const [onlineUsers, setOnlineUsers] = useState(new Set());
  const [dmsExpanded, setDmsExpanded] = useState(true);
  
  // 메시지 관련
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [unreadCounts, setUnreadCounts] = useState({});
  const [loading, setLoading] = useState(true);
  const [showMobileChat, setShowMobileChat] = useState(false);  // 모바일 채팅 표시
  
  // 스레드 관련
  const [selectedThread, setSelectedThread] = useState(null);
  const [threadMessages, setThreadMessages] = useState([]);
  const [showThreadPanel, setShowThreadPanel] = useState(false);
  const [threadReply, setThreadReply] = useState('');
  
  const messagesEndRef = useRef(null);
  const threadEndRef = useRef(null);
  const messageInputRef = useRef(null);
  const threadInputRef = useRef(null);

  // 초기 로드
  useEffect(() => {
    if (user) {
      loadEmployees();
      loadChannels();
      setUserOnline();
      
      return () => setUserOffline();
    }
  }, [user]);

  // 온라인 사용자 리스닝
  useEffect(() => {
    if (!user) return;
    
    const presenceRef = collection(db, 'presence');
    const unsubscribe = onSnapshot(presenceRef, (snapshot) => {
      const online = new Set();
      snapshot.forEach((doc) => {
        if (doc.data().online) {
          online.add(doc.id);
        }
      });
      setOnlineUsers(online);
    });
    
    return unsubscribe;
  }, [user]);

  // 검색 필터링
  useEffect(() => {
    filterEmployees();
  }, [employees, searchTerm]);

  // 메시지 리스닝
  useEffect(() => {
    let unsubscribe;
    
    if (selectedUser) {
      unsubscribe = listenToDirectMessages();
      markMessagesAsRead();
    } else if (selectedChannel) {
      unsubscribe = listenToChannelMessages();
    }
    
    return () => {
      if (unsubscribe) {
        unsubscribe();
      }
    };
  }, [selectedUser, selectedChannel]);

  // 스레드 리스닝
  useEffect(() => {
    if (selectedThread) {
      listenToThreadMessages();
    }
  }, [selectedThread]);

  // 자동 스크롤
  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  useEffect(() => {
    scrollThreadToBottom();
  }, [threadMessages]);

  const setUserOnline = async () => {
    if (!user) return;
    
    try {
      await setDoc(doc(db, 'presence', user.uid), {
        online: true,
        lastSeen: serverTimestamp()
      });
    } catch (error) {
      console.error('온라인 상태 설정 실패:', error);
    }
  };

  const setUserOffline = async () => {
    if (!user) return;
    
    try {
      await setDoc(doc(db, 'presence', user.uid), {
        online: false,
        lastSeen: serverTimestamp()
      });
    } catch (error) {
      console.error('오프라인 상태 설정 실패:', error);
    }
  };

  const loadEmployees = async () => {
    if (!user) return;
    
    try {
      setLoading(true);
      const employeesRef = collection(db, 'employees');
      const querySnapshot = await getDocs(employeesRef);
      
      const employeeList = [];
      querySnapshot.forEach((doc) => {
        const empData = { id: doc.id, ...doc.data() };
        if (doc.id !== user.uid) {
          employeeList.push(empData);
        }
      });

      employeeList.sort((a, b) => (a.name || '').localeCompare(b.name || ''));
      setEmployees(employeeList);
      loadUnreadCounts(employeeList);
    } catch (error) {
      console.error('사원 목록 로드 실패:', error);
    } finally {
      setLoading(false);
    }
  };

  const loadChannels = async () => {
    if (!user) return;
    
    try {
      const channelsRef = collection(db, 'channels');
      const q = query(channelsRef, orderBy('createdAt', 'desc'));
      
      const unsubscribe = onSnapshot(q, (snapshot) => {
        const channelList = [];
        snapshot.forEach((doc) => {
          const channelData = { id: doc.id, ...doc.data() };
          
          if (!channelData.private || channelData.members?.includes(user.uid)) {
            channelList.push(channelData);
          }
        });
        
        setChannels(channelList);
      });
      
      return unsubscribe;
    } catch (error) {
      console.error('채널 목록 로드 실패:', error);
    }
  };

  const loadUnreadCounts = async (employeeList) => {
    if (!user) return;

    const counts = {};
    
    for (const emp of employeeList) {
      try {
        const messagesRef = collection(db, 'directMessages');
        const q = query(
          messagesRef,
          where('receiverId', '==', user.uid),
          where('senderId', '==', emp.id),
          where('read', '==', false)
        );
        
        const querySnapshot = await getDocs(q);
        counts[emp.id] = querySnapshot.size;
      } catch (error) {
        counts[emp.id] = 0;
      }
    }
    
    setUnreadCounts(counts);
  };

  const filterEmployees = () => {
    if (!searchTerm) {
      setFilteredEmployees(employees);
      return;
    }

    const term = searchTerm.toLowerCase();
    const filtered = employees.filter(emp =>
      emp.name?.toLowerCase().includes(term) ||
      emp.department?.toLowerCase().includes(term) ||
      emp.email?.toLowerCase().includes(term)
    );
    
    setFilteredEmployees(filtered);
  };

  const listenToDirectMessages = () => {
    if (!user || !selectedUser) return;

    const messagesRef = collection(db, 'directMessages');
    const chatId = getChatId(user.uid, selectedUser.id);
    
    const q = query(
      messagesRef,
      where('chatId', '==', chatId),
      where('parentId', '==', null), // 메인 메시지만
      orderBy('timestamp', 'asc')
    );

    const unsubscribe = onSnapshot(q, (snapshot) => {
      const messageList = [];
      snapshot.forEach((doc) => {
        messageList.push({
          id: doc.id,
          ...doc.data()
        });
      });
      
      setMessages(messageList);
    });

    return unsubscribe;
  };

  const listenToChannelMessages = () => {
    if (!user || !selectedChannel) return;

    const messagesRef = collection(db, 'channelMessages');
    
    const q = query(
      messagesRef,
      where('channelId', '==', selectedChannel.id),
      where('parentId', '==', null),
      orderBy('timestamp', 'asc')
    );

    const unsubscribe = onSnapshot(q, (snapshot) => {
      const messageList = [];
      snapshot.forEach((doc) => {
        messageList.push({
          id: doc.id,
          ...doc.data()
        });
      });
      
      setMessages(messageList);
    });

    return unsubscribe;
  };

  const listenToThreadMessages = () => {
    if (!selectedThread) return;

    const collectionName = selectedUser ? 'directMessages' : 'channelMessages';
    const messagesRef = collection(db, collectionName);
    
    const q = query(
      messagesRef,
      where('parentId', '==', selectedThread.id),
      orderBy('timestamp', 'asc')
    );

    const unsubscribe = onSnapshot(q, (snapshot) => {
      const replies = [];
      snapshot.forEach((doc) => {
        replies.push({
          id: doc.id,
          ...doc.data()
        });
      });
      
      setThreadMessages(replies);
    });

    return unsubscribe;
  };

  const getChatId = (userId1, userId2) => {
    return [userId1, userId2].sort().join('_');
  };

  const sendDirectMessage = async () => {
    if (!newMessage.trim() || !selectedUser) return;

    try {
      const messageData = {
        chatId: getChatId(user.uid, selectedUser.id),
        senderId: user.uid,
        senderName: userData?.name || userData?.email || user.email,
        receiverId: selectedUser.id,
        receiverName: selectedUser.name,
        message: newMessage.trim(),
        timestamp: serverTimestamp(),
        read: false,
        parentId: null,
        replyCount: 0
      };

      await addDoc(collection(db, 'directMessages'), messageData);
      
      setNewMessage('');
      messageInputRef.current?.focus();
    } catch (error) {
      console.error('메시지 전송 실패:', error);
      alert('메시지 전송에 실패했습니다.');
    }
  };

  const sendChannelMessage = async () => {
    if (!newMessage.trim() || !selectedChannel) return;

    try {
      const messageData = {
        channelId: selectedChannel.id,
        senderId: user.uid,
        senderName: userData?.name || userData?.email || user.email,
        message: newMessage.trim(),
        timestamp: serverTimestamp(),
        parentId: null,
        replyCount: 0
      };

      await addDoc(collection(db, 'channelMessages'), messageData);
      
      setNewMessage('');
      messageInputRef.current?.focus();
    } catch (error) {
      console.error('메시지 전송 실패:', error);
      alert('메시지 전송에 실패했습니다.');
    }
  };

  const sendThreadReply = async () => {
    if (!threadReply.trim() || !selectedThread) return;

    try {
      const collectionName = selectedUser ? 'directMessages' : 'channelMessages';
      
      const replyData = {
        senderId: user.uid,
        senderName: userData?.name || userData?.email || user.email,
        message: threadReply.trim(),
        timestamp: serverTimestamp(),
        parentId: selectedThread.id
      };

      // DM 스레드의 경우 추가 필드
      if (selectedUser) {
        replyData.chatId = selectedThread.chatId;
        replyData.receiverId = selectedThread.senderId === user.uid ? selectedThread.receiverId : selectedThread.senderId;
        replyData.receiverName = selectedThread.senderId === user.uid ? selectedThread.receiverName : selectedThread.senderName;
      } else {
        replyData.channelId = selectedThread.channelId;
      }

      await addDoc(collection(db, collectionName), replyData);
      
      // 답글 개수 업데이트
      const parentRef = doc(db, collectionName, selectedThread.id);
      const parentDoc = await getDoc(parentRef);
      if (parentDoc.exists()) {
        const currentCount = parentDoc.data().replyCount || 0;
        await updateDoc(parentRef, {
          replyCount: currentCount + 1
        });
      }
      
      setThreadReply('');
      threadInputRef.current?.focus();
    } catch (error) {
      console.error('답글 전송 실패:', error);
      alert('답글 전송에 실패했습니다.');
    }
  };

  const sendMessage = () => {
    if (selectedUser) {
      sendDirectMessage();
    } else if (selectedChannel) {
      sendChannelMessage();
    }
  };

  const markMessagesAsRead = async () => {
    if (!user || !selectedUser) return;

    try {
      const messagesRef = collection(db, 'directMessages');
      const q = query(
        messagesRef,
        where('receiverId', '==', user.uid),
        where('senderId', '==', selectedUser.id),
        where('read', '==', false)
      );

      const querySnapshot = await getDocs(q);
      
      const updatePromises = [];
      querySnapshot.forEach((docSnap) => {
        updatePromises.push(
          updateDoc(doc(db, 'directMessages', docSnap.id), { read: true })
        );
      });

      await Promise.all(updatePromises);
      
      setUnreadCounts(prev => ({
        ...prev,
        [selectedUser.id]: 0
      }));
    } catch (error) {
      console.error('읽음 처리 실패:', error);
    }
  };

  const createChannel = async () => {
    if (!newChannelName.trim()) {
      alert('채널 이름을 입력하세요.');
      return;
    }

    try {
      const channelData = {
        name: newChannelName.trim(),
        description: newChannelDesc.trim(),
        private: newChannelPrivate,
        createdBy: user.uid,
        createdByName: userData?.name || userData?.email || user.email,
        members: [user.uid],
        createdAt: serverTimestamp()
      };

      await addDoc(collection(db, 'channels'), channelData);
      
      setShowCreateChannel(false);
      setNewChannelName('');
      setNewChannelDesc('');
      setNewChannelPrivate(false);
      
      alert('채널이 생성되었습니다!');
    } catch (error) {
      console.error('채널 생성 실패:', error);
      alert('채널 생성에 실패했습니다.');
    }
  };

  const deleteChannel = async (channelId) => {
    if (!confirm('정말 이 채널을 삭제하시겠습니까?')) return;

    try {
      await deleteDoc(doc(db, 'channels', channelId));
      
      if (selectedChannel?.id === channelId) {
        setSelectedChannel(null);
      }
      
      alert('채널이 삭제되었습니다.');
    } catch (error) {
      console.error('채널 삭제 실패:', error);
      alert('채널 삭제에 실패했습니다.');
    }
  };

  const openThread = (message) => {
    setSelectedThread(message);
    setShowThreadPanel(true);
    setThreadReply('');
  };

  const closeThread = () => {
    setSelectedThread(null);
    setShowThreadPanel(false);
    setThreadMessages([]);
    setThreadReply('');
  };

  const selectUser = (emp) => {
    setSelectedUser(emp);
    setSelectedChannel(null);
    closeThread();
    // 모바일에서는 채팅방으로 전환
    if (window.innerWidth <= 768) {
      setShowMobileChat(true);
    }
  };

  const selectChannel = (channel) => {
    setSelectedChannel(channel);
    setSelectedUser(null);
    closeThread();
    // 모바일에서는 채팅방으로 전환
    if (window.innerWidth <= 768) {
      setShowMobileChat(true);
    }
  };

  // 메시지 실시간 로드
  useEffect(() => {
    if (!selectedChannel && !selectedUser) {
      setMessages([]);
      return;
    }

    let unsubscribe;

    const loadMessages = async () => {
      try {
        if (selectedChannel) {
          // 채널 메시지 로드
          const messagesRef = collection(db, 'channels', selectedChannel.id, 'messages');
          const q = query(messagesRef, orderBy('createdAt', 'asc'));
          
          unsubscribe = onSnapshot(q, (snapshot) => {
            const msgs = snapshot.docs.map(doc => ({
              id: doc.id,
              ...doc.data()
            }));
            setMessages(msgs);
            setTimeout(scrollToBottom, 100);
          });
        } else if (selectedUser) {
          // DM 메시지 로드
          const chatId = [user.uid, selectedUser.id].sort().join('_');
          const messagesRef = collection(db, 'directMessages', chatId, 'messages');
          const q = query(messagesRef, orderBy('createdAt', 'asc'));
          
          unsubscribe = onSnapshot(q, (snapshot) => {
            const msgs = snapshot.docs.map(doc => ({
              id: doc.id,
              ...doc.data()
            }));
            setMessages(msgs);
            setTimeout(scrollToBottom, 100);
          });
        }
      } catch (error) {
        console.error('메시지 로드 실패:', error);
      }
    };

    loadMessages();

    return () => {
      if (unsubscribe) {
        unsubscribe();
      }
    };
  }, [selectedChannel, selectedUser, user]);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  const scrollThreadToBottom = () => {
    threadEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const handleThreadKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendThreadReply();
    }
  };

  const formatTime = (timestamp) => {
    if (!timestamp) return '';
    
    const date = timestamp.toDate ? timestamp.toDate() : new Date(timestamp);
    const now = new Date();
    const diff = now - date;
    
    if (diff < 60000) return '방금 전';
    if (diff < 3600000) return `${Math.floor(diff / 60000)}분 전`;
    
    if (date.toDateString() === now.toDateString()) {
      return date.toLocaleTimeString('ko-KR', { hour: '2-digit', minute: '2-digit' });
    }
    
    const yesterday = new Date(now);
    yesterday.setDate(yesterday.getDate() - 1);
    if (date.toDateString() === yesterday.toDateString()) {
      return '어제';
    }
    
    return date.toLocaleDateString('ko-KR', { month: 'short', day: 'numeric' });
  };

  const isUserOnline = (userId) => {
    return onlineUsers.has(userId);
  };

  return (
    <div className={`messenger-page-v3 ${showMobileChat ? 'mobile-chat-active' : ''}`}>
      {/* 좌측 사이드바 - 슬랙 스타일 */}
      <div className="messenger-sidebar-v3">
        <div className="sidebar-header-v3">
          <h2>💬 메신저</h2>
        </div>

        {/* 검색 */}
        <div className="sidebar-search">
          <Search size={18} />
          <input
            type="text"
            placeholder="검색..."
            value={searchTerm}
            onChange={e => setSearchTerm(e.target.value)}
          />
        </div>

        {/* 채널 섹션 (상단) */}
        <div className="sidebar-section">
          <div 
            className="section-header"
            onClick={() => setChannelsExpanded(!channelsExpanded)}
          >
            {channelsExpanded ? <ChevronDown size={16} /> : <ChevronRight size={16} />}
            <span>채널</span>
            <button
              className="btn-icon-tiny"
              onClick={(e) => {
                e.stopPropagation();
                setShowCreateChannel(true);
              }}
              title="채널 추가"
            >
              <Plus size={16} />
            </button>
          </div>

          {channelsExpanded && (
            <div className="section-content">
              {channels.length === 0 ? (
                <div className="empty-hint">
                  <p>채널이 없습니다</p>
                  <button
                    className="btn-create-hint"
                    onClick={() => setShowCreateChannel(true)}
                  >
                    첫 채널 만들기
                  </button>
                </div>
              ) : (
                channels.map(channel => (
                  <div
                    key={channel.id}
                    className={`channel-item-v3 ${selectedChannel?.id === channel.id ? 'active' : ''}`}
                    onClick={() => selectChannel(channel)}
                  >
                    <div className="channel-icon-v3">
                      {channel.private ? <Lock size={14} /> : <Hash size={14} />}
                    </div>
                    <span className="channel-name">{channel.name}</span>
                    {channel.createdBy === user.uid && (
                      <button
                        className="btn-delete-tiny"
                        onClick={(e) => {
                          e.stopPropagation();
                          deleteChannel(channel.id);
                        }}
                        title="삭제"
                      >
                        <Trash2 size={12} />
                      </button>
                    )}
                  </div>
                ))
              )}
            </div>
          )}
        </div>

        {/* DM 섹션 (하단) */}
        <div className="sidebar-section">
          <div 
            className="section-header"
            onClick={() => setDmsExpanded(!dmsExpanded)}
          >
            {dmsExpanded ? <ChevronDown size={16} /> : <ChevronRight size={16} />}
            <span>다이렉트 메시지</span>
          </div>

          {dmsExpanded && (
            <div className="section-content">
              {loading ? (
                <div className="loading-hint">로딩 중...</div>
              ) : filteredEmployees.length === 0 ? (
                <div className="empty-hint">
                  <p>대화 상대가 없습니다</p>
                </div>
              ) : (
                filteredEmployees.map(emp => (
                  <div
                    key={emp.id}
                    className={`dm-item-v3 ${selectedUser?.id === emp.id ? 'active' : ''}`}
                    onClick={() => selectUser(emp)}
                  >
                    <div className="dm-avatar">
                      <User size={16} />
                      <Circle 
                        size={8} 
                        className={`status-dot ${isUserOnline(emp.id) ? 'online' : 'offline'}`}
                        fill="currentColor"
                      />
                    </div>
                    <span className="dm-name">{emp.name || '이름 없음'}</span>
                    {unreadCounts[emp.id] > 0 && (
                      <span className="unread-badge-v3">{unreadCounts[emp.id]}</span>
                    )}
                  </div>
                ))
              )}
            </div>
          )}
        </div>
      </div>

      {/* 중앙 채팅 영역 */}
      <div className="messenger-chat-v3">
        {!selectedUser && !selectedChannel ? (
          <div className="chat-empty">
            <MessageCircle size={64} />
            <h3>대화를 시작하세요</h3>
            <p>좌측에서 채널 또는 대화 상대를 선택하세요</p>
          </div>
        ) : (
          <>
            <div className="chat-header-v3">
              {/* 모바일 뒤로가기 버튼 */}
              <button
                className="mobile-back-btn"
                onClick={() => setShowMobileChat(false)}
                title="목록으로"
              >
                <ArrowLeft size={20} />
              </button>
              
              <div className="chat-user-info-v3">
                {selectedUser ? (
                  <>
                    <div className="chat-avatar-v3">
                      <User size={28} />
                      <Circle 
                        size={10} 
                        className={`status-indicator ${isUserOnline(selectedUser?.id) ? 'online' : 'offline'}`}
                        fill="currentColor"
                      />
                    </div>
                    <div>
                      <h3>{selectedUser?.name}</h3>
                      <p className="chat-status">
                        {isUserOnline(selectedUser?.id) ? '🟢 온라인' : '⚪ 오프라인'}
                      </p>
                    </div>
                  </>
                ) : (
                  <>
                    <div className="channel-avatar-v3">
                      {selectedChannel?.private ? <Lock size={24} /> : <Hash size={24} />}
                    </div>
                    <div>
                      <h3>{selectedChannel?.name}</h3>
                      <p>{selectedChannel?.description || '채널 설명 없음'}</p>
                    </div>
                  </>
                )}
              </div>

              <div className="chat-actions-v3">
                <button className="btn-icon" title="더보기">
                  <MoreVertical size={20} />
                </button>
              </div>
            </div>

            <div className="chat-messages-v3">
              {messages.length === 0 ? (
                <div className="messages-empty">
                  <p>첫 메시지를 보내보세요! 👋</p>
                </div>
              ) : (
                <>
                  {messages.map((msg, index) => {
                    const isMine = msg.senderId === user.uid;
                    const showAvatar = index === 0 || 
                      messages[index - 1].senderId !== msg.senderId;

                    return (
                      <div
                        key={msg.id}
                        className={`message-v3 ${isMine ? 'mine' : 'theirs'} ${showAvatar ? 'show-avatar' : ''}`}
                      >
                        {!isMine && showAvatar && (
                          <div className="message-avatar-v3">
                            <User size={24} />
                          </div>
                        )}
                        
                        <div className="message-content-v3">
                          {!isMine && showAvatar && (
                            <span className="message-sender">{msg.senderName}</span>
                          )}
                          <div className="message-bubble-v3">
                            <p>{msg.message}</p>
                          </div>
                          <div className="message-footer">
                            <span className="message-time">{formatTime(msg.timestamp)}</span>
                            <button
                              className="btn-thread-v3"
                              onClick={() => openThread(msg)}
                            >
                              <MessageSquare size={14} />
                              {msg.replyCount > 0 && (
                                <span>{msg.replyCount}</span>
                              )}
                            </button>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                  <div ref={messagesEndRef} />
                </>
              )}
            </div>

            <div className="chat-input-v3">
              <button className="btn-icon" title="이모지">
                <Smile size={20} />
              </button>
              <button className="btn-icon" title="파일 첨부">
                <Paperclip size={20} />
              </button>
              
              <input
                ref={messageInputRef}
                type="text"
                placeholder="메시지 입력..."
                value={newMessage}
                onChange={e => setNewMessage(e.target.value)}
                onKeyPress={handleKeyPress}
              />
              
              <button 
                className="btn-send-v3"
                onClick={sendMessage}
                disabled={!newMessage.trim()}
              >
                <Send size={20} />
              </button>
            </div>
          </>
        )}
      </div>

      {/* 우측 스레드 패널 */}
      {showThreadPanel && selectedThread && (
        <div className="thread-panel-v3">
          <div className="thread-header-v3">
            <h3>
              <MessageSquare size={20} />
              <span>스레드</span>
            </h3>
            <button className="btn-icon" onClick={closeThread}>
              <X size={20} />
            </button>
          </div>

          <div className="thread-content-v3">
            {/* 원본 메시지 */}
            <div className="thread-parent-v3">
              <div className="message-avatar-v3">
                <User size={24} />
              </div>
              <div>
                <div className="message-sender">{selectedThread.senderName}</div>
                <div className="message-bubble-v3">
                  <p>{selectedThread.message}</p>
                </div>
                <span className="message-time">{formatTime(selectedThread.timestamp)}</span>
              </div>
            </div>

            <div className="thread-divider-v3">
              <span>{threadMessages.length} 답글</span>
            </div>

            {/* 답글 목록 */}
            <div className="thread-messages-v3">
              {threadMessages.map(reply => (
                <div key={reply.id} className="thread-reply-v3">
                  <div className="message-avatar-v3">
                    <User size={20} />
                  </div>
                  <div>
                    <div className="message-sender">{reply.senderName}</div>
                    <div className="message-bubble-v3">
                      <p>{reply.message}</p>
                    </div>
                    <span className="message-time">{formatTime(reply.timestamp)}</span>
                  </div>
                </div>
              ))}
              <div ref={threadEndRef} />
            </div>
          </div>

          {/* 스레드 입력 영역 */}
          <div className="thread-input-v3">
            <input
              ref={threadInputRef}
              type="text"
              placeholder="스레드에 답글..."
              value={threadReply}
              onChange={e => setThreadReply(e.target.value)}
              onKeyPress={handleThreadKeyPress}
            />
            <button 
              className="btn-send-v3"
              onClick={sendThreadReply}
              disabled={!threadReply.trim()}
            >
              <Send size={18} />
            </button>
          </div>
        </div>
      )}

      {/* 채널 생성 모달 */}
      {showCreateChannel && (
        <div className="modal-overlay" onClick={() => setShowCreateChannel(false)}>
          <div className="modal-content" onClick={e => e.stopPropagation()}>
            <div className="modal-header">
              <h2>새 채널 만들기</h2>
              <button onClick={() => setShowCreateChannel(false)}>
                <X size={24} />
              </button>
            </div>

            <div className="modal-body">
              <div className="form-group">
                <label>채널 이름 *</label>
                <input
                  type="text"
                  placeholder="예: 공지사항"
                  value={newChannelName}
                  onChange={e => setNewChannelName(e.target.value)}
                  autoFocus
                />
              </div>

              <div className="form-group">
                <label>채널 설명</label>
                <textarea
                  placeholder="이 채널의 용도를 설명하세요"
                  value={newChannelDesc}
                  onChange={e => setNewChannelDesc(e.target.value)}
                  rows={3}
                />
              </div>

              <div className="form-group-checkbox">
                <input
                  type="checkbox"
                  id="private-channel"
                  checked={newChannelPrivate}
                  onChange={e => setNewChannelPrivate(e.target.checked)}
                />
                <label htmlFor="private-channel">
                  <Lock size={16} />
                  <span>비공개 채널로 만들기</span>
                </label>
              </div>
            </div>

            <div className="modal-footer">
              <button
                className="btn-secondary"
                onClick={() => setShowCreateChannel(false)}
              >
                취소
              </button>
              <button
                className="btn-primary"
                onClick={createChannel}
              >
                채널 만들기
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Messenger;
