import React, { useState, useEffect } from 'react';
import { db, storage } from '../config/firebase';
import { 
  collection, 
  query, 
  where, 
  getDocs, 
  addDoc, 
  updateDoc,
  doc,
  serverTimestamp,
  limit
} from 'firebase/firestore';
import { ref, uploadBytes, getDownloadURL } from 'firebase/storage';
import { useAuthStore } from '../stores/authStore';
import LeaveRegisterModal from '../components/annualLeave/LeaveRegisterModal';
import Pagination from '../components/common/Pagination';
import './AnnualLeave.css';

function AnnualLeave() {
  const { user, userData } = useAuthStore();
  
  // 상태 관리
  const [employeeInfo, setEmployeeInfo] = useState(null);
  const [leaveUsage, setLeaveUsage] = useState([]);
  const [showRegisterModal, setShowRegisterModal] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [loading, setLoading] = useState(true);
  
  const itemsPerPage = 10;

  // 데이터 로드
  useEffect(() => {
    if (user) {
      loadEmployeeInfo();
      loadLeaveUsage();
    }
  }, [user]);

  const loadEmployeeInfo = async () => {
    try {
      const q = query(collection(db, 'employees'), where('__name__', '==', user.uid));
      const snapshot = await getDocs(q);
      
      if (!snapshot.empty) {
        setEmployeeInfo(snapshot.docs[0].data());
      }
      setLoading(false);
    } catch (error) {
      console.error('사원 정보 로드 실패:', error);
      setLoading(false);
    }
  };

  const loadLeaveUsage = async () => {
    try {
      const q = query(
        collection(db, 'leaveUsage'),
        where('userId', '==', user.uid)
      );
      const snapshot = await getDocs(q);

      const allUsage = [];
      snapshot.forEach(docSnapshot => {
        allUsage.push({ id: docSnapshot.id, ...docSnapshot.data() });
      });

      // groupId로 그룹핑
      const groupedUsage = {};
      const ungroupedUsage = [];

      allUsage.forEach(item => {
        if (item.groupId) {
          if (!groupedUsage[item.groupId]) {
            groupedUsage[item.groupId] = {
              id: item.groupId,
              ids: [item.id],
              userId: item.userId,
              startDate: item.startDate || item.date,
              endDate: item.endDate || item.date,
              date: item.startDate || item.date,
              type: item.type,
              amount: item.amount,
              totalDays: item.totalDays || 1,
              note: item.note,
              approvalStatus: item.approvalStatus,
              createdAt: item.createdAt,
              groupId: item.groupId,
              isGrouped: true
            };
          } else {
            groupedUsage[item.groupId].ids.push(item.id);
          }
        } else {
          ungroupedUsage.push(item);
        }
      });

      const usage = [...Object.values(groupedUsage), ...ungroupedUsage];
      usage.sort((a, b) => (b.startDate || b.date).localeCompare(a.startDate || a.date));

      setLeaveUsage(usage);
    } catch (error) {
      console.error('연차 내역 로드 실패:', error);
    }
  };

  // 총 연차 계산
  const calculateTotalLeave = (joinDate) => {
    if (!joinDate) return 0;
    const join = new Date(joinDate);
    const now = new Date();
    const years = now.getFullYear() - join.getFullYear();

    if (years < 1) {
      const months = (now.getFullYear() - join.getFullYear()) * 12 + (now.getMonth() - join.getMonth());
      return Math.floor(months);
    }

    return 15 + Math.min(years - 1, 10);
  };

  // 사용 연차 계산
  const calculateUsedLeave = () => {
    return leaveUsage
      .filter(usage => usage.approvalStatus === 'approved' || usage.approvalStatus === 'pending')
      .reduce((sum, usage) => sum + usage.amount, 0);
  };

  // 파일 업로드
  const uploadFiles = async (leaveId, files) => {
    if (files.length === 0) return [];

    const uploadedUrls = [];

    try {
      for (const file of files) {
        const storageRef = ref(storage, `leave-attachments/${user.uid}/${leaveId}/${file.name}`);
        await uploadBytes(storageRef, file);
        const url = await getDownloadURL(storageRef);
        uploadedUrls.push({
          name: file.name,
          url: url,
          size: file.size
        });
      }
      return uploadedUrls;
    } catch (error) {
      console.error('파일 업로드 실패:', error);
      alert('파일 업로드 중 오류가 발생했습니다.');
      return [];
    }
  };

  // 연차 등록
  const handleRegisterLeave = async (formData) => {
    try {
      const { startDate, endDate, leaveType, note, files } = formData;
      
      const start = new Date(startDate);
      const end = new Date(endDate);

      if (start > end) {
        alert('종료일은 시작일보다 늦어야 합니다.');
        return;
      }

      const approvalStatus = employeeInfo?.isApprover ? 'approved' : 'pending';

      // 연차 수량 계산
      let leaveAmount = 0;
      if (leaveType === '연차') {
        leaveAmount = 1;
      } else if (leaveType === '반차(오전)' || leaveType === '반차(오후)') {
        leaveAmount = 0.5;
      } else if (leaveType === '여름휴가' || leaveType === '겨울휴가') {
        leaveAmount = 0;
      }

      // 여름휴가, 겨울휴가가 아닌 경우 연차 수량 체크
      if (leaveType !== '여름휴가' && leaveType !== '겨울휴가') {
        let requestedDays = 0;
        const tempDate = new Date(start);
        while (tempDate <= end) {
          const dayOfWeek = tempDate.getDay();
          if (dayOfWeek !== 0 && dayOfWeek !== 6) {
            requestedDays += leaveAmount;
          }
          tempDate.setDate(tempDate.getDate() + 1);
        }

        const totalLeave = calculateTotalLeave(employeeInfo?.joinDate);
        const usedLeave = calculateUsedLeave();
        const remainingLeave = totalLeave - usedLeave;

        if (remainingLeave <= 0) {
          alert(`사용 가능한 연차가 없습니다.\n\n총 연차: ${totalLeave}일\n사용한 연차: ${usedLeave}일\n남은 연차: ${remainingLeave}일`);
          return;
        }

        if (requestedDays > remainingLeave) {
          alert(`사용 가능한 연차를 초과했습니다.\n\n요청한 연차: ${requestedDays}일\n사용 가능한 연차: ${remainingLeave}일\n\n${remainingLeave}일 이하로 신청해주세요.`);
          return;
        }
      }

      const groupId = `${user.uid}_${Date.now()}`;

      // 근무일 수 계산
      let workDayCount = 0;
      const tempDate = new Date(start);
      while (tempDate <= end) {
        const dayOfWeek = tempDate.getDay();
        if (dayOfWeek !== 0 && dayOfWeek !== 6) {
          workDayCount++;
        }
        tempDate.setDate(tempDate.getDate() + 1);
      }

      // 시작일부터 종료일까지 연차 등록
      const currentDate = new Date(start);
      while (currentDate <= end) {
        const dateStr = currentDate.toISOString().split('T')[0];
        const dayOfWeek = currentDate.getDay();

        if (dayOfWeek !== 0 && dayOfWeek !== 6) {
          // 연차 사용 기록
          await addDoc(collection(db, 'leaveUsage'), {
            userId: user.uid,
            date: dateStr,
            startDate,
            endDate,
            type: leaveType,
            amount: leaveAmount,
            totalDays: workDayCount,
            groupId,
            approvalStatus,
            createdAt: serverTimestamp()
          });

          // 스케줄에 등록
          const scheduleQuery = query(
            collection(db, 'schedules'),
            where('userId', '==', user.uid),
            where('date', '==', dateStr)
          );
          const existingSchedule = await getDocs(scheduleQuery);

          if (!existingSchedule.empty) {
            const docId = existingSchedule.docs[0].id;
            await updateDoc(doc(db, 'schedules', docId), {
              type: leaveType,
              groupId,
              approvalStatus,
              updatedAt: serverTimestamp()
            });
          } else {
            await addDoc(collection(db, 'schedules'), {
              userId: user.uid,
              userName: user.displayName || user.email,
              date: dateStr,
              type: leaveType,
              groupId,
              approvalStatus,
              createdAt: serverTimestamp()
            });
          }
        }

        currentDate.setDate(currentDate.getDate() + 1);
      }

      // 파일 업로드
      if (files && files.length > 0) {
        const uploadedFiles = await uploadFiles(groupId, files);

        const firstLeaveQuery = query(
          collection(db, 'leaveUsage'),
          where('groupId', '==', groupId),
          limit(1)
        );
        const firstLeaveDoc = await getDocs(firstLeaveQuery);

        if (!firstLeaveDoc.empty) {
          await updateDoc(doc(db, 'leaveUsage', firstLeaveDoc.docs[0].id), {
            attachments: uploadedFiles,
            note: note || ''
          });
        }
      }

      const message = employeeInfo?.isApprover
        ? '연차 등록이 완료되었습니다.'
        : '연차 신청이 완료되었습니다. 승인을 기다려주세요.';
      alert(message);

      setShowRegisterModal(false);
      loadLeaveUsage();
    } catch (error) {
      console.error('연차 등록 실패:', error);
      alert('연차 등록 중 오류가 발생했습니다.');
    }
  };

  // 연차 취소/삭제
  const handleDeleteLeave = async (usageIdOrGroup, usageDate, usageType, approvalStatus, groupId) => {
    try {
      const isApprover = employeeInfo?.isApprover;
      const usageIds = groupId ? usageIdOrGroup.ids : [usageIdOrGroup];
      const isGrouped = !!groupId;

      if (approvalStatus === 'approved') {
        if (isApprover) {
          const confirmMsg = isGrouped
            ? `${usageIdOrGroup.startDate} ~ ${usageIdOrGroup.endDate} (${usageIdOrGroup.totalDays}일) 연차를 모두 취소하시겠습니까?`
            : '이 연차를 취소하시겠습니까?';
          if (!confirm(confirmMsg)) return;

          for (const id of usageIds) {
            await updateDoc(doc(db, 'leaveUsage', id), {
              approvalStatus: 'cancelled',
              cancelledAt: serverTimestamp()
            });
          }

          // 스케줄도 취소
          const scheduleQuery = groupId
            ? query(collection(db, 'schedules'), where('userId', '==', user.uid), where('groupId', '==', groupId))
            : query(collection(db, 'schedules'), where('userId', '==', user.uid), where('date', '==', usageDate), where('type', '==', usageType));

          const scheduleSnapshot = await getDocs(scheduleQuery);
          scheduleSnapshot.forEach(async (docSnapshot) => {
            await updateDoc(doc(db, 'schedules', docSnapshot.id), {
              approvalStatus: 'cancelled',
              cancelledAt: serverTimestamp()
            });
          });

          alert('연차가 취소되었습니다.');
        } else {
          const confirmMsg = isGrouped
            ? `승인 완료된 연차입니다. ${usageIdOrGroup.startDate} ~ ${usageIdOrGroup.endDate} (${usageIdOrGroup.totalDays}일) 연차의 취소 승인을 요청하시겠습니까?`
            : '승인 완료된 연차입니다. 취소 승인을 요청하시겠습니까?';
          if (!confirm(confirmMsg)) return;

          for (const id of usageIds) {
            await updateDoc(doc(db, 'leaveUsage', id), {
              approvalStatus: 'deletion-pending',
              deletionRequestedAt: serverTimestamp()
            });
          }

          const scheduleQuery = groupId
            ? query(collection(db, 'schedules'), where('userId', '==', user.uid), where('groupId', '==', groupId))
            : query(collection(db, 'schedules'), where('userId', '==', user.uid), where('date', '==', usageDate), where('type', '==', usageType));

          const scheduleSnapshot = await getDocs(scheduleQuery);
          scheduleSnapshot.forEach(async (docSnapshot) => {
            await updateDoc(doc(db, 'schedules', docSnapshot.id), {
              approvalStatus: 'deletion-pending',
              deletionRequestedAt: serverTimestamp()
            });
          });

          alert('취소 승인 요청이 완료되었습니다.');
        }
      } else if (approvalStatus === 'pending') {
        const confirmMsg = isGrouped
          ? `${usageIdOrGroup.startDate} ~ ${usageIdOrGroup.endDate} (${usageIdOrGroup.totalDays}일) 연차 신청을 모두 취소하시겠습니까?`
          : '이 연차 신청을 취소하시겠습니까?';
        if (!confirm(confirmMsg)) return;

        for (const id of usageIds) {
          await updateDoc(doc(db, 'leaveUsage', id), {
            approvalStatus: 'cancelled',
            cancelledAt: serverTimestamp()
          });
        }

        const scheduleQuery = groupId
          ? query(collection(db, 'schedules'), where('userId', '==', user.uid), where('groupId', '==', groupId))
          : query(collection(db, 'schedules'), where('userId', '==', user.uid), where('date', '==', usageDate), where('type', '==', usageType));

        const scheduleSnapshot = await getDocs(scheduleQuery);
        scheduleSnapshot.forEach(async (docSnapshot) => {
          await updateDoc(doc(db, 'schedules', docSnapshot.id), {
            approvalStatus: 'cancelled',
            cancelledAt: serverTimestamp()
          });
        });

        alert('연차 신청이 취소되었습니다.');
      } else {
        const confirmMsg = isGrouped
          ? `${usageIdOrGroup.startDate} ~ ${usageIdOrGroup.endDate} (${usageIdOrGroup.totalDays}일) 연차 내역을 취소하시겠습니까?`
          : '이 연차 내역을 취소하시겠습니까?';
        if (!confirm(confirmMsg)) return;

        for (const id of usageIds) {
          await updateDoc(doc(db, 'leaveUsage', id), {
            approvalStatus: 'cancelled',
            cancelledAt: serverTimestamp()
          });
        }

        alert('취소되었습니다.');
      }

      loadLeaveUsage();
      loadEmployeeInfo();
    } catch (error) {
      console.error('처리 실패:', error);
      alert('처리 실패: ' + error.message);
    }
  };

  // 로딩 상태
  if (loading) {
    return <div className="annual-leave-container">사원 정보를 불러오는 중...</div>;
  }

  if (!employeeInfo) {
    return <div className="annual-leave-container">사원 정보를 찾을 수 없습니다.</div>;
  }

  const totalLeave = employeeInfo.customTotalLeave || calculateTotalLeave(employeeInfo.joinDate);
  const usedLeave = calculateUsedLeave();
  const remainingLeave = totalLeave - usedLeave;

  // 페이지네이션
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const currentItems = leaveUsage.slice(startIndex, endIndex);

  return (
    <div className="annual-leave-container">
      <div className="section-header">
        <h2>연차 관리</h2>
        <button className="register-btn" onClick={() => setShowRegisterModal(true)}>
          연차 등록
        </button>
      </div>

      <div className="annual-leave-info">
        <div className="info-card">
          <h4>입사일</h4>
          <div className="value">{employeeInfo.joinDate}</div>
        </div>
        <div className="info-card">
          <h4>총 연차</h4>
          <div className="value">{totalLeave}일</div>
        </div>
        <div className="info-card">
          <h4>사용 연차</h4>
          <div className="value">{usedLeave}일</div>
        </div>
        <div className="info-card">
          <h4>남은 연차</h4>
          <div className="value remaining">{remainingLeave}일</div>
        </div>
      </div>

      <div className="leave-history">
        <h3>연차 사용 내역</h3>
        {leaveUsage.length === 0 ? (
          <div className="empty-state">
            <p>연차 사용 내역이 없습니다.</p>
          </div>
        ) : (
          <>
            <div className="table-wrapper">
              <table>
                <thead>
                  <tr>
                    <th>날짜</th>
                    <th>유형</th>
                    <th>사용일수</th>
                    <th>승인 상태</th>
                    <th>관리</th>
                  </tr>
                </thead>
                <tbody>
                  {currentItems.map(usage => (
                    <tr key={usage.id}>
                      <td>
                        {usage.isGrouped && usage.startDate !== usage.endDate
                          ? `${usage.startDate} ~ ${usage.endDate}`
                          : (usage.startDate || usage.date)}
                      </td>
                      <td>{usage.type}</td>
                      <td>
                        {usage.isGrouped && usage.totalDays > 1
                          ? `${usage.totalDays}일`
                          : (usage.amount > 0 ? `${usage.amount}일` : '-')}
                      </td>
                      <td>
                        <span className={`status-badge status-${usage.approvalStatus}`}>
                          {usage.approvalStatus === 'approved' ? '승인완료' :
                           usage.approvalStatus === 'rejected' ? '반려' :
                           usage.approvalStatus === 'deletion-pending' ? '취소대기' :
                           usage.approvalStatus === 'cancelled' ? '취소됨' : '승인대기'}
                        </span>
                      </td>
                      <td>
                        {(usage.approvalStatus !== 'deletion-pending' && usage.approvalStatus !== 'cancelled') && (
                          <button
                            className="delete-btn"
                            onClick={() => handleDeleteLeave(
                              usage.isGrouped ? usage : usage.id,
                              usage.date,
                              usage.type,
                              usage.approvalStatus,
                              usage.groupId
                            )}
                          >
                            취소
                          </button>
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>

            {leaveUsage.length > itemsPerPage && (
              <Pagination
                currentPage={currentPage}
                totalPages={Math.ceil(leaveUsage.length / itemsPerPage)}
                onPageChange={setCurrentPage}
              />
            )}
          </>
        )}
      </div>

      {showRegisterModal && (
        <LeaveRegisterModal
          onClose={() => setShowRegisterModal(false)}
          onSubmit={handleRegisterLeave}
          remainingLeave={remainingLeave}
        />
      )}
    </div>
  );
}

export default AnnualLeave;
