import React, { useState, useEffect } from 'react';
import { db } from '../config/firebase';
import { collection, getDocs, doc, updateDoc, deleteDoc, setDoc } from 'firebase/firestore';
import { 
  Users, 
  Shield, 
  Edit, 
  Trash2, 
  Plus, 
  Save, 
  X,
  AlertCircle,
  CheckCircle,
  UserCheck
} from 'lucide-react';
import './AdminEmployees.css';

function AdminEmployees() {
  const [employees, setEmployees] = useState([]);
  const [filteredEmployees, setFilteredEmployees] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [editingId, setEditingId] = useState(null);
  const [editForm, setEditForm] = useState({});
  const [showAddModal, setShowAddModal] = useState(false);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadEmployees();
  }, []);

  useEffect(() => {
    filterEmployees();
  }, [searchTerm, employees]);

  const loadEmployees = async () => {
    setLoading(true);
    try {
      const snapshot = await getDocs(collection(db, 'employees'));
      const employeeList = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      
      // 이름순 정렬
      employeeList.sort((a, b) => (a.name || '').localeCompare(b.name || ''));
      
      setEmployees(employeeList);
    } catch (error) {
      console.error('사원 목록 로드 실패:', error);
      alert('사원 목록을 불러오는데 실패했습니다.');
    }
    setLoading(false);
  };

  const filterEmployees = () => {
    if (!searchTerm.trim()) {
      setFilteredEmployees(employees);
      return;
    }

    const term = searchTerm.toLowerCase();
    const filtered = employees.filter(emp => 
      (emp.name || '').toLowerCase().includes(term) ||
      (emp.email || '').toLowerCase().includes(term) ||
      (emp.department || '').toLowerCase().includes(term)
    );
    setFilteredEmployees(filtered);
  };

  // 수정 시작
  const startEdit = (employee) => {
    setEditingId(employee.id);
    setEditForm({ ...employee });
  };

  // 수정 취소
  const cancelEdit = () => {
    setEditingId(null);
    setEditForm({});
  };

  // 수정 저장
  const saveEdit = async () => {
    if (!editForm.name || !editForm.email) {
      alert('이름과 이메일은 필수입니다.');
      return;
    }

    try {
      const employeeRef = doc(db, 'employees', editingId);
      await updateDoc(employeeRef, {
        name: editForm.name,
        email: editForm.email,
        department: editForm.department || '',
        position: editForm.position || '',
        phone: editForm.phone || '',
        joinDate: editForm.joinDate || '',
        customTotalLeave: Number(editForm.customTotalLeave) || null,
        isAdmin: editForm.isAdmin || false,
        isSubAdmin: editForm.isSubAdmin || false,
        isApprover: editForm.isApprover || false,
        isPTScheduleEditor: editForm.isPTScheduleEditor || false,
      });

      alert('수정되었습니다.');
      setEditingId(null);
      setEditForm({});
      loadEmployees();
    } catch (error) {
      console.error('수정 실패:', error);
      alert('수정에 실패했습니다: ' + error.message);
    }
  };

  // 사원 삭제
  const deleteEmployee = async (id, name) => {
    if (!window.confirm(`${name} 사원을 정말 삭제하시겠습니까?\n\n삭제하면 되돌릴 수 없습니다.`)) {
      return;
    }

    try {
      await deleteDoc(doc(db, 'employees', id));
      alert('삭제되었습니다.');
      loadEmployees();
    } catch (error) {
      console.error('삭제 실패:', error);
      alert('삭제에 실패했습니다: ' + error.message);
    }
  };

  // 사원 추가 모달
  const AddEmployeeModal = () => {
    const [newEmployee, setNewEmployee] = useState({
      id: '',
      name: '',
      email: '',
      department: '',
      position: '',
      phone: '',
      joinDate: '',
      customTotalLeave: '',
      isAdmin: false,
      isSubAdmin: false,
      isApprover: false,
      isPTScheduleEditor: false,
    });

    const handleAdd = async () => {
      if (!newEmployee.id || !newEmployee.name || !newEmployee.email) {
        alert('사원 ID, 이름, 이메일은 필수입니다.');
        return;
      }

      try {
        await setDoc(doc(db, 'employees', newEmployee.id), {
          name: newEmployee.name,
          email: newEmployee.email,
          department: newEmployee.department || '',
          position: newEmployee.position || '',
          phone: newEmployee.phone || '',
          joinDate: newEmployee.joinDate || '',
          customTotalLeave: newEmployee.customTotalLeave ? Number(newEmployee.customTotalLeave) : null,
          isAdmin: newEmployee.isAdmin,
          isSubAdmin: newEmployee.isSubAdmin,
          isApprover: newEmployee.isApprover,
          isPTScheduleEditor: newEmployee.isPTScheduleEditor,
          createdAt: new Date().toISOString(),
        });

        alert('사원이 추가되었습니다.');
        setShowAddModal(false);
        loadEmployees();
      } catch (error) {
        console.error('추가 실패:', error);
        alert('추가에 실패했습니다: ' + error.message);
      }
    };

    return (
      <div className="modal-overlay" onClick={() => setShowAddModal(false)}>
        <div className="modal-content large" onClick={e => e.stopPropagation()}>
          <div className="modal-header">
            <h2>사원 추가</h2>
            <button className="close-btn" onClick={() => setShowAddModal(false)}>
              <X size={20} />
            </button>
          </div>

          <div className="modal-body">
            <div className="form-grid">
              <div className="form-group">
                <label>사원 ID (Firebase Auth UID) *</label>
                <input
                  type="text"
                  value={newEmployee.id}
                  onChange={e => setNewEmployee({ ...newEmployee, id: e.target.value })}
                  placeholder="Firebase Auth UID"
                />
                <small>Firebase Authentication의 사용자 UID를 입력하세요</small>
              </div>

              <div className="form-group">
                <label>이름 *</label>
                <input
                  type="text"
                  value={newEmployee.name}
                  onChange={e => setNewEmployee({ ...newEmployee, name: e.target.value })}
                />
              </div>

              <div className="form-group">
                <label>이메일 *</label>
                <input
                  type="email"
                  value={newEmployee.email}
                  onChange={e => setNewEmployee({ ...newEmployee, email: e.target.value })}
                />
              </div>

              <div className="form-group">
                <label>부서</label>
                <input
                  type="text"
                  value={newEmployee.department}
                  onChange={e => setNewEmployee({ ...newEmployee, department: e.target.value })}
                />
              </div>

              <div className="form-group">
                <label>직급</label>
                <select
                  value={newEmployee.position}
                  onChange={e => setNewEmployee({ ...newEmployee, position: e.target.value })}
                >
                  <option value="">선택</option>
                  <option value="대표">대표</option>
                  <option value="이사">이사</option>
                  <option value="팀장">팀장</option>
                  <option value="매니저">매니저</option>
                  <option value="사원">사원</option>
                </select>
              </div>

              <div className="form-group">
                <label>연락처</label>
                <input
                  type="text"
                  value={newEmployee.phone}
                  onChange={e => setNewEmployee({ ...newEmployee, phone: e.target.value })}
                  placeholder="010-1234-5678"
                />
              </div>

              <div className="form-group">
                <label>입사일</label>
                <input
                  type="date"
                  value={newEmployee.joinDate}
                  onChange={e => setNewEmployee({ ...newEmployee, joinDate: e.target.value })}
                />
              </div>

              <div className="form-group">
                <label>연차 (커스텀)</label>
                <input
                  type="number"
                  value={newEmployee.customTotalLeave}
                  onChange={e => setNewEmployee({ ...newEmployee, customTotalLeave: e.target.value })}
                  placeholder="미입력시 입사일 기준 자동 계산"
                />
              </div>
            </div>

            <div className="permissions-section">
              <h3>권한 설정</h3>
              <div className="permission-checks">
                <label className="permission-check">
                  <input
                    type="checkbox"
                    checked={newEmployee.isAdmin}
                    onChange={e => setNewEmployee({ ...newEmployee, isAdmin: e.target.checked })}
                  />
                  <span>관리자 (Admin)</span>
                  <small>전체 권한</small>
                </label>

                <label className="permission-check">
                  <input
                    type="checkbox"
                    checked={newEmployee.isSubAdmin}
                    onChange={e => setNewEmployee({ ...newEmployee, isSubAdmin: e.target.checked })}
                  />
                  <span>부관리자 (Sub Admin)</span>
                  <small>사원 관리 권한</small>
                </label>

                <label className="permission-check">
                  <input
                    type="checkbox"
                    checked={newEmployee.isApprover}
                    onChange={e => setNewEmployee({ ...newEmployee, isApprover: e.target.checked })}
                  />
                  <span>승인자 (Approver)</span>
                  <small>연차 승인 권한</small>
                </label>

                <label className="permission-check">
                  <input
                    type="checkbox"
                    checked={newEmployee.isPTScheduleEditor}
                    onChange={e => setNewEmployee({ ...newEmployee, isPTScheduleEditor: e.target.checked })}
                  />
                  <span>PT 스케줄 편집자</span>
                  <small>PT 스케줄 수정 권한</small>
                </label>
              </div>
            </div>
          </div>

          <div className="modal-footer">
            <button className="btn-secondary" onClick={() => setShowAddModal(false)}>
              취소
            </button>
            <button className="btn-primary" onClick={handleAdd}>
              <Plus size={18} />
              추가
            </button>
          </div>
        </div>
      </div>
    );
  };

  if (loading) {
    return (
      <div className="admin-employees-page">
        <div className="loading-state">사원 목록을 불러오는 중...</div>
      </div>
    );
  }

  return (
    <div className="admin-employees-page">
      {/* 헤더 */}
      <div className="page-header">
        <div className="header-left">
          <Users size={28} />
          <div>
            <h1>사원 관리</h1>
            <p>권한 설정 및 사원 정보 관리</p>
          </div>
        </div>

        <div className="header-right">
          <input
            type="text"
            className="search-input"
            placeholder="이름, 이메일, 부서 검색..."
            value={searchTerm}
            onChange={e => setSearchTerm(e.target.value)}
          />
          <button className="btn-primary" onClick={() => setShowAddModal(true)}>
            <Plus size={18} />
            사원 추가
          </button>
        </div>
      </div>

      {/* 안내 메시지 */}
      <div className="info-banner">
        <AlertCircle size={20} />
        <div>
          <strong>권한 안내:</strong>
          <ul>
            <li><strong>관리자:</strong> 모든 메뉴 접근 가능 (사원 관리, 연차 관리, 결재선 관리, Slack 설정)</li>
            <li><strong>부관리자:</strong> 관리자와 동일하지만 최고 권한은 없음</li>
            <li><strong>승인자:</strong> 연차 승인 메뉴 표시, 담당 사원의 연차 승인/반려</li>
            <li><strong>PT 스케줄 편집자:</strong> PT 스케줄 수정 권한</li>
          </ul>
        </div>
      </div>

      {/* 사원 테이블 */}
      <div className="employees-table-container">
        <table className="employees-table">
          <thead>
            <tr>
              <th>이름</th>
              <th>이메일</th>
              <th>부서</th>
              <th>직급</th>
              <th>연락처</th>
              <th>입사일</th>
              <th>연차</th>
              <th>권한</th>
              <th>작업</th>
            </tr>
          </thead>
          <tbody>
            {filteredEmployees.map(emp => (
              <tr key={emp.id}>
                {editingId === emp.id ? (
                  // 수정 모드
                  <>
                    <td>
                      <input
                        type="text"
                        value={editForm.name || ''}
                        onChange={e => setEditForm({ ...editForm, name: e.target.value })}
                        className="table-input"
                      />
                    </td>
                    <td>
                      <input
                        type="email"
                        value={editForm.email || ''}
                        onChange={e => setEditForm({ ...editForm, email: e.target.value })}
                        className="table-input"
                      />
                    </td>
                    <td>
                      <input
                        type="text"
                        value={editForm.department || ''}
                        onChange={e => setEditForm({ ...editForm, department: e.target.value })}
                        className="table-input"
                      />
                    </td>
                    <td>
                      <select
                        value={editForm.position || ''}
                        onChange={e => setEditForm({ ...editForm, position: e.target.value })}
                        className="table-input"
                      >
                        <option value="">선택</option>
                        <option value="대표">대표</option>
                        <option value="이사">이사</option>
                        <option value="팀장">팀장</option>
                        <option value="매니저">매니저</option>
                        <option value="사원">사원</option>
                      </select>
                    </td>
                    <td>
                      <input
                        type="text"
                        value={editForm.phone || ''}
                        onChange={e => setEditForm({ ...editForm, phone: e.target.value })}
                        className="table-input"
                      />
                    </td>
                    <td>
                      <input
                        type="date"
                        value={editForm.joinDate || ''}
                        onChange={e => setEditForm({ ...editForm, joinDate: e.target.value })}
                        className="table-input"
                      />
                    </td>
                    <td>
                      <input
                        type="number"
                        value={editForm.customTotalLeave || ''}
                        onChange={e => setEditForm({ ...editForm, customTotalLeave: e.target.value })}
                        className="table-input small"
                        placeholder="자동"
                      />
                    </td>
                    <td>
                      <div className="permission-checkboxes">
                        <label title="관리자">
                          <input
                            type="checkbox"
                            checked={editForm.isAdmin || false}
                            onChange={e => setEditForm({ ...editForm, isAdmin: e.target.checked })}
                          />
                          <span>관리자</span>
                        </label>
                        <label title="부관리자">
                          <input
                            type="checkbox"
                            checked={editForm.isSubAdmin || false}
                            onChange={e => setEditForm({ ...editForm, isSubAdmin: e.target.checked })}
                          />
                          <span>부관리자</span>
                        </label>
                        <label title="승인자">
                          <input
                            type="checkbox"
                            checked={editForm.isApprover || false}
                            onChange={e => setEditForm({ ...editForm, isApprover: e.target.checked })}
                          />
                          <span>승인자</span>
                        </label>
                        <label title="PT 편집자">
                          <input
                            type="checkbox"
                            checked={editForm.isPTScheduleEditor || false}
                            onChange={e => setEditForm({ ...editForm, isPTScheduleEditor: e.target.checked })}
                          />
                          <span>PT</span>
                        </label>
                      </div>
                    </td>
                    <td>
                      <div className="action-buttons">
                        <button className="btn-success small" onClick={saveEdit} title="저장">
                          <Save size={16} />
                        </button>
                        <button className="btn-secondary small" onClick={cancelEdit} title="취소">
                          <X size={16} />
                        </button>
                      </div>
                    </td>
                  </>
                ) : (
                  // 일반 모드
                  <>
                    <td className="name-cell">
                      <strong>{emp.name || '-'}</strong>
                    </td>
                    <td>{emp.email || '-'}</td>
                    <td>{emp.department || '-'}</td>
                    <td>{emp.position || '-'}</td>
                    <td>{emp.phone || '-'}</td>
                    <td>{emp.joinDate || '-'}</td>
                    <td className="center">
                      {emp.customTotalLeave ? (
                        <span className="custom-leave">{emp.customTotalLeave}일</span>
                      ) : (
                        <span className="auto-leave">자동</span>
                      )}
                    </td>
                    <td>
                      <div className="permission-badges">
                        {emp.isAdmin && <span className="badge admin">관리자</span>}
                        {emp.isSubAdmin && <span className="badge sub-admin">부관리자</span>}
                        {emp.isApprover && <span className="badge approver">승인자</span>}
                        {emp.isPTScheduleEditor && <span className="badge pt-editor">PT</span>}
                        {!emp.isAdmin && !emp.isSubAdmin && !emp.isApprover && !emp.isPTScheduleEditor && (
                          <span className="badge default">일반</span>
                        )}
                      </div>
                    </td>
                    <td>
                      <div className="action-buttons">
                        <button className="btn-secondary small" onClick={() => startEdit(emp)} title="수정">
                          <Edit size={16} />
                        </button>
                        <button className="btn-danger small" onClick={() => deleteEmployee(emp.id, emp.name)} title="삭제">
                          <Trash2 size={16} />
                        </button>
                      </div>
                    </td>
                  </>
                )}
              </tr>
            ))}
          </tbody>
        </table>

        {filteredEmployees.length === 0 && (
          <div className="empty-state">
            <Users size={48} />
            <p>검색 결과가 없습니다.</p>
          </div>
        )}
      </div>

      {/* 사원 추가 모달 */}
      {showAddModal && <AddEmployeeModal />}
    </div>
  );
}

export default AdminEmployees;
