import React, { useState } from 'react';
import './ScheduleModal.css';

function SharedEventModal({ year, month, onSave, onClose }) {
  const [selectedDate, setSelectedDate] = useState('');
  const [title, setTitle] = useState('');

  const getDaysInMonth = (year, month) => {
    return new Date(year, month + 1, 0).getDate();
  };

  const formatDate = (year, month, day) => {
    return `${year}-${String(month + 1).padStart(2, '0')}-${String(day).padStart(2, '0')}`;
  };

  const handleSave = () => {
    if (!selectedDate) {
      alert('날짜를 선택해주세요.');
      return;
    }
    if (!title.trim()) {
      alert('일정 내용을 입력해주세요.');
      return;
    }
    onSave(selectedDate, title);
  };

  const daysInMonth = getDaysInMonth(year, month);
  const dates = Array.from({ length: daysInMonth }, (_, i) => i + 1);

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3>공유일정 등록</h3>
          <button className="modal-close-btn" onClick={onClose}>✕</button>
        </div>

        <div className="modal-body">
          <div className="form-group">
            <label>날짜 선택</label>
            <select
              value={selectedDate}
              onChange={(e) => setSelectedDate(e.target.value)}
              className="form-select"
            >
              <option value="">날짜를 선택하세요</option>
              {dates.map(day => {
                const dateStr = formatDate(year, month, day);
                return (
                  <option key={day} value={dateStr}>
                    {month + 1}월 {day}일
                  </option>
                );
              })}
            </select>
          </div>

          <div className="form-group">
            <label>일정 내용</label>
            <input
              type="text"
              value={title}
              onChange={(e) => setTitle(e.target.value)}
              placeholder="공유일정 내용을 입력하세요"
              className="form-input"
              maxLength={50}
            />
          </div>

          <div className="modal-notice">
            <p>📌 전체 사원에게 공유되는 일정입니다.</p>
          </div>
        </div>

        <div className="modal-footer">
          <button className="modal-btn cancel-btn" onClick={onClose}>
            취소
          </button>
          <button className="modal-btn save-btn" onClick={handleSave}>
            저장
          </button>
        </div>
      </div>
    </div>
  );
}

export default SharedEventModal;
