import React, { useState, useEffect } from 'react';
import { useAuthStore } from '../stores/authStore';
import { 
  Settings as SettingsIcon, Bell, Moon, Sun, Globe, 
  Lock, Shield, Mail, MessageSquare, Save, Check
} from 'lucide-react';
import './Settings.css';

const Settings = () => {
  const { userData } = useAuthStore();
  
  // 알림 설정
  const [emailNotifications, setEmailNotifications] = useState(true);
  const [pushNotifications, setPushNotifications] = useState(true);
  const [messageNotifications, setMessageNotifications] = useState(true);
  const [approvalNotifications, setApprovalNotifications] = useState(true);
  const [leaveNotifications, setLeaveNotifications] = useState(true);
  
  // 테마 설정
  const [theme, setTheme] = useState('light');
  
  // 언어 설정
  const [language, setLanguage] = useState('ko');
  
  // 저장 상태
  const [saveStatus, setSaveStatus] = useState('');

  useEffect(() => {
    // localStorage에서 설정 불러오기
    const savedSettings = localStorage.getItem('userSettings');
    if (savedSettings) {
      const settings = JSON.parse(savedSettings);
      setEmailNotifications(settings.emailNotifications ?? true);
      setPushNotifications(settings.pushNotifications ?? true);
      setMessageNotifications(settings.messageNotifications ?? true);
      setApprovalNotifications(settings.approvalNotifications ?? true);
      setLeaveNotifications(settings.leaveNotifications ?? true);
      setTheme(settings.theme || 'light');
      setLanguage(settings.language || 'ko');
    }
  }, []);

  const handleSaveSettings = () => {
    const settings = {
      emailNotifications,
      pushNotifications,
      messageNotifications,
      approvalNotifications,
      leaveNotifications,
      theme,
      language
    };
    
    localStorage.setItem('userSettings', JSON.stringify(settings));
    
    // 테마 적용
    if (theme === 'dark') {
      document.documentElement.classList.add('dark-theme');
    } else {
      document.documentElement.classList.remove('dark-theme');
    }
    
    setSaveStatus('저장되었습니다!');
    setTimeout(() => setSaveStatus(''), 3000);
  };

  const handleThemeChange = (newTheme) => {
    setTheme(newTheme);
    
    if (newTheme === 'dark') {
      document.documentElement.classList.add('dark-theme');
    } else {
      document.documentElement.classList.remove('dark-theme');
    }
  };

  return (
    <div className="settings-page">
      {/* 설정 헤더 */}
      <div className="settings-header">
        <div className="settings-header-content">
          <h1>
            <SettingsIcon size={28} />
            <span>설정</span>
          </h1>
          <p>앱 설정 및 개인 환경 설정을 관리합니다</p>
        </div>

        <button className="btn-save-settings" onClick={handleSaveSettings}>
          <Save size={18} />
          <span>설정 저장</span>
        </button>
      </div>

      {saveStatus && (
        <div className="save-status-banner">
          <Check size={18} />
          <span>{saveStatus}</span>
        </div>
      )}

      <div className="settings-content">
        {/* 알림 설정 */}
        <div className="settings-section">
          <div className="section-header">
            <Bell size={20} />
            <h2>알림 설정</h2>
          </div>
          <p className="section-description">
            받고 싶은 알림을 선택하세요
          </p>

          <div className="settings-group">
            <div className="setting-item">
              <div className="setting-info">
                <Mail size={18} />
                <div>
                  <h3>이메일 알림</h3>
                  <p>중요한 업데이트를 이메일로 받습니다</p>
                </div>
              </div>
              <label className="toggle-switch">
                <input
                  type="checkbox"
                  checked={emailNotifications}
                  onChange={(e) => setEmailNotifications(e.target.checked)}
                />
                <span className="toggle-slider"></span>
              </label>
            </div>

            <div className="setting-item">
              <div className="setting-info">
                <Bell size={18} />
                <div>
                  <h3>푸시 알림</h3>
                  <p>브라우저 푸시 알림을 받습니다</p>
                </div>
              </div>
              <label className="toggle-switch">
                <input
                  type="checkbox"
                  checked={pushNotifications}
                  onChange={(e) => setPushNotifications(e.target.checked)}
                />
                <span className="toggle-slider"></span>
              </label>
            </div>

            <div className="setting-item">
              <div className="setting-info">
                <MessageSquare size={18} />
                <div>
                  <h3>메시지 알림</h3>
                  <p>새 메시지 도착 시 알림을 받습니다</p>
                </div>
              </div>
              <label className="toggle-switch">
                <input
                  type="checkbox"
                  checked={messageNotifications}
                  onChange={(e) => setMessageNotifications(e.target.checked)}
                />
                <span className="toggle-slider"></span>
              </label>
            </div>

            <div className="setting-item">
              <div className="setting-info">
                <Shield size={18} />
                <div>
                  <h3>승인 요청 알림</h3>
                  <p>승인 요청이 있을 때 알림을 받습니다</p>
                </div>
              </div>
              <label className="toggle-switch">
                <input
                  type="checkbox"
                  checked={approvalNotifications}
                  onChange={(e) => setApprovalNotifications(e.target.checked)}
                />
                <span className="toggle-slider"></span>
              </label>
            </div>

            <div className="setting-item">
              <div className="setting-info">
                <Shield size={18} />
                <div>
                  <h3>연차 알림</h3>
                  <p>연차 승인/반려 시 알림을 받습니다</p>
                </div>
              </div>
              <label className="toggle-switch">
                <input
                  type="checkbox"
                  checked={leaveNotifications}
                  onChange={(e) => setLeaveNotifications(e.target.checked)}
                />
                <span className="toggle-slider"></span>
              </label>
            </div>
          </div>
        </div>

        {/* 테마 설정 */}
        <div className="settings-section">
          <div className="section-header">
            <Moon size={20} />
            <h2>테마 설정</h2>
          </div>
          <p className="section-description">
            앱의 테마를 선택하세요
          </p>

          <div className="theme-options">
            <div 
              className={`theme-card ${theme === 'light' ? 'active' : ''}`}
              onClick={() => handleThemeChange('light')}
            >
              <div className="theme-preview light">
                <Sun size={32} />
              </div>
              <h3>라이트 모드</h3>
              <p>밝은 배경으로 편안한 작업</p>
            </div>

            <div 
              className={`theme-card ${theme === 'dark' ? 'active' : ''}`}
              onClick={() => handleThemeChange('dark')}
            >
              <div className="theme-preview dark">
                <Moon size={32} />
              </div>
              <h3>다크 모드</h3>
              <p>어두운 배경으로 눈의 피로 감소</p>
            </div>
          </div>
        </div>

        {/* 언어 설정 */}
        <div className="settings-section">
          <div className="section-header">
            <Globe size={20} />
            <h2>언어 설정</h2>
          </div>
          <p className="section-description">
            앱에서 사용할 언어를 선택하세요
          </p>

          <div className="language-select">
            <select 
              value={language}
              onChange={(e) => setLanguage(e.target.value)}
              className="select-input"
            >
              <option value="ko">한국어</option>
              <option value="en">English</option>
              <option value="ja">日本語</option>
            </select>
          </div>
        </div>

        {/* 보안 설정 */}
        <div className="settings-section">
          <div className="section-header">
            <Lock size={20} />
            <h2>보안 설정</h2>
          </div>
          <p className="section-description">
            계정 보안을 관리합니다
          </p>

          <div className="security-actions">
            <button className="security-btn">
              <Lock size={18} />
              <div>
                <h3>비밀번호 변경</h3>
                <p>정기적인 비밀번호 변경을 권장합니다</p>
              </div>
            </button>

            <button className="security-btn">
              <Shield size={18} />
              <div>
                <h3>2단계 인증</h3>
                <p>추가 보안 계층으로 계정을 보호합니다</p>
              </div>
            </button>
          </div>
        </div>

        {/* 계정 정보 */}
        <div className="settings-section">
          <div className="section-header">
            <SettingsIcon size={20} />
            <h2>계정 정보</h2>
          </div>

          <div className="account-info">
            <div className="info-row">
              <span className="info-label">계정 ID</span>
              <span className="info-value">{userData?.email}</span>
            </div>
            <div className="info-row">
              <span className="info-label">계정 유형</span>
              <span className="info-value">
                {userData?.isAdmin ? '관리자' : '일반 사용자'}
              </span>
            </div>
            <div className="info-row">
              <span className="info-label">가입일</span>
              <span className="info-value">
                {userData?.createdAt ? 
                  new Date(userData.createdAt.seconds * 1000).toLocaleDateString('ko-KR') :
                  '정보 없음'
                }
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Settings;
