import React, { useState, useEffect, useRef } from 'react';
import { 
  collection, query, where, getDocs, orderBy, limit 
} from 'firebase/firestore';
import { db } from '../../config/firebase';
import { useAuthStore } from '../../stores/authStore';
import { 
  Search, X, FileText, MessageSquare, Calendar, 
  User, Folder, Command, ArrowRight, Clock
} from 'lucide-react';
import './GlobalSearch.css';

const GlobalSearch = ({ isOpen, onClose, onNavigate }) => {
  const { user } = useAuthStore();
  const [searchTerm, setSearchTerm] = useState('');
  const [results, setResults] = useState({
    notes: [],
    messages: [],
    events: [],
    employees: [],
    projects: []
  });
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [isSearching, setIsSearching] = useState(false);
  const [recentSearches, setRecentSearches] = useState([]);
  
  const searchInputRef = useRef(null);
  const searchTimeoutRef = useRef(null);

  // 단축키 감지
  useEffect(() => {
    const handleKeyDown = (e) => {
      // Ctrl+K 또는 Cmd+K
      if ((e.ctrlKey || e.metaKey) && e.key === 'k') {
        e.preventDefault();
        if (!isOpen && onNavigate) {
          // Header에서 검색 모달 열기
          return;
        }
      }
      
      // ESC 키로 닫기
      if (e.key === 'Escape' && isOpen) {
        onClose();
      }
      
      // 위/아래 방향키로 결과 탐색
      if (isOpen && searchTerm) {
        const totalResults = getTotalResultsCount();
        if (e.key === 'ArrowDown') {
          e.preventDefault();
          setSelectedIndex(prev => (prev + 1) % totalResults);
        } else if (e.key === 'ArrowUp') {
          e.preventDefault();
          setSelectedIndex(prev => (prev - 1 + totalResults) % totalResults);
        } else if (e.key === 'Enter') {
          e.preventDefault();
          handleSelectResult();
        }
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [isOpen, searchTerm, selectedIndex]);

  // 검색창 포커스
  useEffect(() => {
    if (isOpen && searchInputRef.current) {
      searchInputRef.current.focus();
    }
  }, [isOpen]);

  // 최근 검색어 로드
  useEffect(() => {
    const saved = localStorage.getItem('recentSearches');
    if (saved) {
      setRecentSearches(JSON.parse(saved));
    }
  }, []);

  // 검색 실행
  useEffect(() => {
    if (searchTerm.length >= 2) {
      if (searchTimeoutRef.current) {
        clearTimeout(searchTimeoutRef.current);
      }
      
      searchTimeoutRef.current = setTimeout(() => {
        performSearch(searchTerm);
      }, 300); // 300ms 디바운스
    } else {
      setResults({
        notes: [],
        messages: [],
        events: [],
        employees: [],
        projects: []
      });
    }
    
    return () => {
      if (searchTimeoutRef.current) {
        clearTimeout(searchTimeoutRef.current);
      }
    };
  }, [searchTerm]);

  const performSearch = async (term) => {
    if (!user) return;
    
    setIsSearching(true);
    try {
      const searchResults = await Promise.all([
        searchNotes(term),
        searchMessages(term),
        searchEvents(term),
        searchEmployees(term),
        searchProjects(term)
      ]);
      
      setResults({
        notes: searchResults[0],
        messages: searchResults[1],
        events: searchResults[2],
        employees: searchResults[3],
        projects: searchResults[4]
      });
      
      setSelectedIndex(0);
    } catch (error) {
      console.error('검색 실패:', error);
    }
    setIsSearching(false);
  };

  // 노트 검색
  const searchNotes = async (term) => {
    try {
      const notesRef = collection(db, 'notes');
      const snapshot = await getDocs(notesRef);
      
      return snapshot.docs
        .map(doc => ({ id: doc.id, ...doc.data() }))
        .filter(note => 
          note.userId === user.uid &&
          (note.title?.toLowerCase().includes(term.toLowerCase()) ||
           note.content?.toLowerCase().includes(term.toLowerCase()))
        )
        .slice(0, 5);
    } catch (error) {
      console.error('노트 검색 실패:', error);
      return [];
    }
  };

  // 메시지 검색
  const searchMessages = async (term) => {
    try {
      const messagesRef = collection(db, 'directMessages');
      const snapshot = await getDocs(query(messagesRef, limit(100)));
      
      return snapshot.docs
        .map(doc => ({ id: doc.id, ...doc.data() }))
        .filter(msg => 
          (msg.senderId === user.uid || msg.receiverId === user.uid) &&
          msg.message?.toLowerCase().includes(term.toLowerCase())
        )
        .slice(0, 5);
    } catch (error) {
      console.error('메시지 검색 실패:', error);
      return [];
    }
  };

  // 일정 검색
  const searchEvents = async (term) => {
    try {
      const eventsRef = collection(db, 'schedules');
      const snapshot = await getDocs(eventsRef);
      
      return snapshot.docs
        .map(doc => ({ id: doc.id, ...doc.data() }))
        .filter(event => 
          event.userId === user.uid &&
          (event.title?.toLowerCase().includes(term.toLowerCase()) ||
           event.description?.toLowerCase().includes(term.toLowerCase()))
        )
        .slice(0, 5);
    } catch (error) {
      console.error('일정 검색 실패:', error);
      return [];
    }
  };

  // 사원 검색
  const searchEmployees = async (term) => {
    try {
      const employeesRef = collection(db, 'employees');
      const snapshot = await getDocs(employeesRef);
      
      return snapshot.docs
        .map(doc => ({ id: doc.id, ...doc.data() }))
        .filter(emp => 
          emp.name?.toLowerCase().includes(term.toLowerCase()) ||
          emp.email?.toLowerCase().includes(term.toLowerCase()) ||
          emp.department?.toLowerCase().includes(term.toLowerCase())
        )
        .slice(0, 5);
    } catch (error) {
      console.error('사원 검색 실패:', error);
      return [];
    }
  };

  // 프로젝트 검색
  const searchProjects = async (term) => {
    try {
      const projectsRef = collection(db, 'projects');
      const snapshot = await getDocs(projectsRef);
      
      return snapshot.docs
        .map(doc => ({ id: doc.id, ...doc.data() }))
        .filter(project => 
          (project.ownerId === user.uid || project.type === 'team') &&
          project.name?.toLowerCase().includes(term.toLowerCase())
        )
        .slice(0, 5);
    } catch (error) {
      console.error('프로젝트 검색 실패:', error);
      return [];
    }
  };

  const getTotalResultsCount = () => {
    return Object.values(results).reduce((sum, arr) => sum + arr.length, 0);
  };

  const handleSelectResult = () => {
    const allResults = [
      ...results.notes.map(r => ({ ...r, type: 'note' })),
      ...results.messages.map(r => ({ ...r, type: 'message' })),
      ...results.events.map(r => ({ ...r, type: 'event' })),
      ...results.employees.map(r => ({ ...r, type: 'employee' })),
      ...results.projects.map(r => ({ ...r, type: 'project' }))
    ];
    
    if (allResults[selectedIndex]) {
      handleResultClick(allResults[selectedIndex]);
    }
  };

  const handleResultClick = (result) => {
    // 최근 검색어에 추가
    const newRecent = [
      searchTerm,
      ...recentSearches.filter(s => s !== searchTerm)
    ].slice(0, 5);
    setRecentSearches(newRecent);
    localStorage.setItem('recentSearches', JSON.stringify(newRecent));
    
    // 결과로 이동
    if (onNavigate) {
      switch (result.type) {
        case 'note':
          onNavigate('notes', result);
          break;
        case 'message':
          onNavigate('messenger', result);
          break;
        case 'event':
          onNavigate('calendar', result);
          break;
        case 'employee':
          onNavigate('department', result);
          break;
        case 'project':
          onNavigate('notes', result);
          break;
      }
    }
    
    onClose();
  };

  const handleRecentSearch = (term) => {
    setSearchTerm(term);
  };

  const clearRecentSearches = () => {
    setRecentSearches([]);
    localStorage.removeItem('recentSearches');
  };

  if (!isOpen) return null;

  return (
    <>
      <div className="search-overlay" onClick={onClose} />
      
      <div className="search-modal">
        <div className="search-header">
          <Search size={20} className="search-icon" />
          <input
            ref={searchInputRef}
            type="text"
            className="search-input"
            placeholder="노트, 메시지, 일정, 사원 검색..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <div className="search-shortcuts">
            <kbd>ESC</kbd>
          </div>
          <button className="btn-close-search" onClick={onClose}>
            <X size={20} />
          </button>
        </div>

        <div className="search-content">
          {!searchTerm && recentSearches.length > 0 && (
            <div className="search-section">
              <div className="search-section-header">
                <h3>
                  <Clock size={16} />
                  <span>최근 검색</span>
                </h3>
                <button className="btn-text-small" onClick={clearRecentSearches}>
                  지우기
                </button>
              </div>
              <div className="recent-searches">
                {recentSearches.map((term, index) => (
                  <button
                    key={index}
                    className="recent-search-item"
                    onClick={() => handleRecentSearch(term)}
                  >
                    <Clock size={14} />
                    <span>{term}</span>
                  </button>
                ))}
              </div>
            </div>
          )}

          {isSearching && (
            <div className="search-loading">
              <div className="loading-spinner-small"></div>
              <p>검색 중...</p>
            </div>
          )}

          {searchTerm && !isSearching && getTotalResultsCount() === 0 && (
            <div className="search-empty">
              <Search size={48} />
              <p>'{searchTerm}'에 대한 결과가 없습니다</p>
            </div>
          )}

          {searchTerm && getTotalResultsCount() > 0 && (
            <>
              {/* 노트 결과 */}
              {results.notes.length > 0 && (
                <div className="search-section">
                  <div className="search-section-header">
                    <h3>
                      <FileText size={16} />
                      <span>노트</span>
                    </h3>
                    <span className="result-count">{results.notes.length}</span>
                  </div>
                  {results.notes.map((note, index) => (
                    <button
                      key={note.id}
                      className={`search-result-item ${selectedIndex === index ? 'selected' : ''}`}
                      onClick={() => handleResultClick({ ...note, type: 'note' })}
                    >
                      <FileText size={18} />
                      <div className="result-content">
                        <h4>{note.title || '제목 없음'}</h4>
                        <p>{note.content?.replace(/<[^>]*>/g, '').substring(0, 60)}...</p>
                      </div>
                      <ArrowRight size={16} className="result-arrow" />
                    </button>
                  ))}
                </div>
              )}

              {/* 메시지 결과 */}
              {results.messages.length > 0 && (
                <div className="search-section">
                  <div className="search-section-header">
                    <h3>
                      <MessageSquare size={16} />
                      <span>메시지</span>
                    </h3>
                    <span className="result-count">{results.messages.length}</span>
                  </div>
                  {results.messages.map((msg, index) => (
                    <button
                      key={msg.id}
                      className={`search-result-item ${selectedIndex === results.notes.length + index ? 'selected' : ''}`}
                      onClick={() => handleResultClick({ ...msg, type: 'message' })}
                    >
                      <MessageSquare size={18} />
                      <div className="result-content">
                        <h4>{msg.senderId === user.uid ? '보낸 메시지' : '받은 메시지'}</h4>
                        <p>{msg.message?.substring(0, 60)}...</p>
                      </div>
                      <ArrowRight size={16} className="result-arrow" />
                    </button>
                  ))}
                </div>
              )}

              {/* 일정 결과 */}
              {results.events.length > 0 && (
                <div className="search-section">
                  <div className="search-section-header">
                    <h3>
                      <Calendar size={16} />
                      <span>일정</span>
                    </h3>
                    <span className="result-count">{results.events.length}</span>
                  </div>
                  {results.events.map((event, index) => (
                    <button
                      key={event.id}
                      className="search-result-item"
                      onClick={() => handleResultClick({ ...event, type: 'event' })}
                    >
                      <Calendar size={18} />
                      <div className="result-content">
                        <h4>{event.title}</h4>
                        <p>{event.date} {event.time || ''}</p>
                      </div>
                      <ArrowRight size={16} className="result-arrow" />
                    </button>
                  ))}
                </div>
              )}

              {/* 사원 결과 */}
              {results.employees.length > 0 && (
                <div className="search-section">
                  <div className="search-section-header">
                    <h3>
                      <User size={16} />
                      <span>사원</span>
                    </h3>
                    <span className="result-count">{results.employees.length}</span>
                  </div>
                  {results.employees.map((emp, index) => (
                    <button
                      key={emp.id}
                      className="search-result-item"
                      onClick={() => handleResultClick({ ...emp, type: 'employee' })}
                    >
                      <User size={18} />
                      <div className="result-content">
                        <h4>{emp.name}</h4>
                        <p>{emp.department} · {emp.position}</p>
                      </div>
                      <ArrowRight size={16} className="result-arrow" />
                    </button>
                  ))}
                </div>
              )}

              {/* 프로젝트 결과 */}
              {results.projects.length > 0 && (
                <div className="search-section">
                  <div className="search-section-header">
                    <h3>
                      <Folder size={16} />
                      <span>프로젝트</span>
                    </h3>
                    <span className="result-count">{results.projects.length}</span>
                  </div>
                  {results.projects.map((project, index) => (
                    <button
                      key={project.id}
                      className="search-result-item"
                      onClick={() => handleResultClick({ ...project, type: 'project' })}
                    >
                      <Folder size={18} />
                      <div className="result-content">
                        <h4>{project.name}</h4>
                        <p>{project.type === 'personal' ? '개인 프로젝트' : '팀 프로젝트'}</p>
                      </div>
                      <ArrowRight size={16} className="result-arrow" />
                    </button>
                  ))}
                </div>
              )}
            </>
          )}
        </div>

        <div className="search-footer">
          <div className="search-tips">
            <span><kbd>↑</kbd><kbd>↓</kbd> 탐색</span>
            <span><kbd>Enter</kbd> 선택</span>
            <span><kbd>ESC</kbd> 닫기</span>
          </div>
        </div>
      </div>
    </>
  );
};

export default GlobalSearch;
