// 유틸리티 함수들
const formatDate = (year, month, day) => {
  return `${year}-${String(month + 1).padStart(2, '0')}-${String(day).padStart(2, '0')}`;
};

const getDaysInMonth = (year, month) => {
  return new Date(year, month + 1, 0).getDate();
};

const getFirstDayOfMonth = (year, month) => {
  return new Date(year, month, 1).getDay();
};

function Calendar({ employees = [] }) {
  const { user, userData } = useAuthStore();
  
  // 상태 관리
  const [currentDate, setCurrentDate] = useState(new Date());
  const [schedules, setSchedules] = useState({});
  const [selectedDate, setSelectedDate] = useState(null);
  const [selectedDaySchedules, setSelectedDaySchedules] = useState([]);
  const [showScheduleModal, setShowScheduleModal] = useState(false);
  const [employeeInfo, setEmployeeInfo] = useState(null);
  const [filterEmployee, setFilterEmployee] = useState('all');
  const [filterLeaveType, setFilterLeaveType] = useState('all');
  const [sharedEvents, setSharedEvents] = useState({});
  const [showSharedEventModal, setShowSharedEventModal] = useState(false);
  const [lastClickedDate, setLastClickedDate] = useState(null);

  const year = currentDate.getFullYear();
  const month = currentDate.getMonth();

  // 사원 정보 로드
  useEffect(() => {
    if (user) {
      loadEmployeeInfo();
      loadSchedules();
      loadSharedEvents();
    }
  }, [user, year, month]);

  // 오늘 날짜 자동 선택
  useEffect(() => {
    const today = new Date();
    const todayStr = formatDate(today.getFullYear(), today.getMonth(), today.getDate());
    setSelectedDate(todayStr);
  }, [schedules]);

  // 선택된 날짜의 일정 표시
  useEffect(() => {
    if (selectedDate && schedules[selectedDate]) {
      const rawDaySchedules = schedules[selectedDate];
      const filteredDaySchedules = rawDaySchedules.filter(
        s => s.approvalStatus !== 'cancelled' && s.approvalStatus !== 'deletion-pending'
      );
      setSelectedDaySchedules(filteredDaySchedules);
    } else if (selectedDate) {
      setSelectedDaySchedules([]);
    }
  }, [selectedDate, schedules]);

  const loadEmployeeInfo = async () => {
    try {
      const docRef = doc(db, 'employees', user.uid);
      const docSnap = await getDocs(query(collection(db, 'employees'), where('__name__', '==', user.uid)));
      if (!docSnap.empty) {
        setEmployeeInfo(docSnap.docs[0].data());
      }
    } catch (error) {
      console.error('사원 정보 로드 실패:', error);
    }
  };

  const loadSchedules = async () => {
    try {
      const startDate = formatDate(year, month, 1);
      const endDate = formatDate(year, month, getDaysInMonth(year, month));

      const q = query(
        collection(db, 'schedules'),
        where('date', '>=', startDate),
        where('date', '<=', endDate)
      );
      const snapshot = await getDocs(q);

      const scheduleData = {};
      snapshot.forEach(docSnapshot => {
        const data = docSnapshot.data();
        if (!scheduleData[data.date]) {
          scheduleData[data.date] = [];
        }
        scheduleData[data.date].push({
          id: docSnapshot.id,
          ...data
        });
      });

      setSchedules(scheduleData);
    } catch (error) {
      console.error('일정 로드 실패:', error);
    }
  };

  const loadSharedEvents = async () => {
    try {
      const startDate = formatDate(year, month, 1);
      const endDate = formatDate(year, month, getDaysInMonth(year, month));

      const q = query(
        collection(db, 'sharedEvents'),
        where('date', '>=', startDate),
        where('date', '<=', endDate)
      );
      const snapshot = await getDocs(q);

      const eventData = {};
      snapshot.forEach(docSnapshot => {
        const data = docSnapshot.data();
        if (!eventData[data.date]) {
          eventData[data.date] = [];
        }
        eventData[data.date].push({
          id: docSnapshot.id,
          ...data
        });
      });

      setSharedEvents(eventData);
    } catch (error) {
      console.error('공유 일정 로드 실패:', error);
    }
  };

  const handlePrevMonth = () => {
    setCurrentDate(new Date(year, month - 1));
  };

  const handleNextMonth = () => {
    setCurrentDate(new Date(year, month + 1));
  };

  const handleDateClick = (day) => {
    const dateStr = formatDate(year, month, day);
    const isMobile = window.innerWidth <= 768;

    if (isMobile) {
      if (lastClickedDate === dateStr) {
        setShowScheduleModal(true);
      } else {
        setSelectedDate(dateStr);
        setLastClickedDate(dateStr);
      }
    } else {
      setSelectedDate(dateStr);
      setShowScheduleModal(true);
    }
  };

  const handleSaveSchedule = async (date, type) => {
    try {
      const schedulesRef = collection(db, 'schedules');

      // 기존 스케줄 확인
      const q = query(
        schedulesRef,
        where('userId', '==', user.uid),
        where('date', '==', date)
      );
      const existingSchedule = await getDocs(q);

      const isLeaveType = ['연차', '반차(오전)', '반차(오후)', '여름휴가', '겨울휴가'].includes(type);
      const approvalStatus = isLeaveType ? (employeeInfo?.isApprover ? 'approved' : 'pending') : null;

      if (!existingSchedule.empty) {
        // 업데이트
        const docId = existingSchedule.docs[0].id;
        const updateData = {
          type: type,
          updatedAt: serverTimestamp()
        };
        if (isLeaveType) {
          updateData.approvalStatus = approvalStatus;
        } else {
          updateData.approvalStatus = null;
        }
        await updateDoc(doc(db, 'schedules', docId), updateData);
      } else {
        // 새로 생성
        const newScheduleData = {
          userId: user.uid,
          userName: user.displayName || user.email,
          date: date,
          type: type,
          createdAt: serverTimestamp()
        };
        if (isLeaveType) {
          newScheduleData.approvalStatus = approvalStatus;
        }
        await addDoc(schedulesRef, newScheduleData);
      }

      alert('일정이 등록되었습니다.');
      setShowScheduleModal(false);
      loadSchedules();
    } catch (error) {
      console.error('일정 저장 실패:', error);
      alert('일정 저장에 실패했습니다.');
    }
  };

  const handleDeleteSchedule = async (scheduleId, date, type) => {
    if (!confirm('이 일정을 삭제하시겠습니까?')) return;

    try {
      const isLeaveType = ['연차', '반차(오전)', '반차(오후)', '여름휴가', '겨울휴가'].includes(type);
      
      if (isLeaveType) {
        // 연차는 승인 대기 상태로 변경
        await updateDoc(doc(db, 'schedules', scheduleId), {
          approvalStatus: 'deletion-pending',
          cancelledAt: serverTimestamp()
        });
        alert('취소 요청이 접수되었습니다.');
      } else {
        // 일반 일정은 즉시 삭제
        await deleteDoc(doc(db, 'schedules', scheduleId));
        alert('일정이 삭제되었습니다.');
      }

      loadSchedules();
    } catch (error) {
      console.error('일정 삭제 실패:', error);
      alert('삭제 실패: ' + error.message);
    }
  };

  const handleSaveSharedEvent = async (date, title) => {
    try {
      await addDoc(collection(db, 'sharedEvents'), {
        date: date,
        title: title,
        createdBy: user.uid,
        createdByName: user.displayName || user.email,
        createdAt: serverTimestamp()
      });

      alert('공유일정이 등록되었습니다.');
      setShowSharedEventModal(false);
      loadSharedEvents();
    } catch (error) {
      console.error('공유일정 등록 실패:', error);
      alert('등록 실패: ' + error.message);
    }
  };

  const handleDeleteSharedEvent = async (eventId) => {
    if (!confirm('이 공유일정을 삭제하시겠습니까?')) return;

    try {
      await deleteDoc(doc(db, 'sharedEvents', eventId));
      alert('공유일정이 삭제되었습니다.');
      loadSharedEvents();
    } catch (error) {
      console.error('공유일정 삭제 실패:', error);
      alert('삭제 실패: ' + error.message);
    }
  };

  // 캘린더 렌더링 (PC)
  const renderCalendar = () => {
    const daysInMonth = getDaysInMonth(year, month);
    const firstDay = getFirstDayOfMonth(year, month);
    const weeks = [];
    let days = [];

    // 이전 달의 빈 칸
    for (let i = 0; i < firstDay; i++) {
      days.push(<td key={`empty-${i}`} className="other-month"></td>);
    }

    // 현재 달의 날짜
    const today = new Date();
    const isCurrentMonth = today.getFullYear() === year && today.getMonth() === month;

    for (let day = 1; day <= daysInMonth; day++) {
      const dateStr = formatDate(year, month, day);
      const rawDaySchedules = schedules[dateStr] || [];
      let daySchedules = rawDaySchedules.filter(
        s => s.approvalStatus !== 'cancelled' && s.approvalStatus !== 'deletion-pending'
      );

      // 필터 적용
      if (filterEmployee !== 'all') {
        daySchedules = daySchedules.filter(s => s.userId === filterEmployee);
      }
      if (filterLeaveType !== 'all') {
        daySchedules = daySchedules.filter(s => s.type === filterLeaveType);
      }

      const isToday = isCurrentMonth && today.getDate() === day;
      const isHoliday = holidays[dateStr];
      const dayOfWeek = new Date(year, month, day).getDay();
      const isSunday = dayOfWeek === 0;
      const isSaturday = dayOfWeek === 6;

      days.push(
        <td
          key={day}
          className={`
            ${isToday ? 'today' : ''} 
            ${isHoliday ? 'holiday' : ''} 
            ${isSunday ? 'sunday' : ''} 
            ${isSaturday ? 'saturday' : ''}
          `}
          onClick={() => handleDateClick(day)}
        >
          <div className="date-number">{day}</div>
          {isHoliday && <div className="holiday-name">{isHoliday}</div>}

          {/* 공유일정 표시 */}
          {sharedEvents[dateStr] && sharedEvents[dateStr].map((event, idx) => (
            <div
              key={`shared-${idx}`}
              className="schedule-item schedule-shared"
              onClick={(e) => e.stopPropagation()}
            >
              <span>📌 {event.title}</span>
              {(employeeInfo?.isSubAdmin || employeeInfo?.isApprover) && (
                <button
                  className="delete-schedule-btn"
                  onClick={(e) => {
                    e.stopPropagation();
                    handleDeleteSharedEvent(event.id);
                  }}
                >
                  ✕
                </button>
              )}
            </div>
          ))}

          {/* 개인 일정 표시 */}
          {daySchedules.map((schedule, idx) => {
            const isLeaveType = ['연차', '반차(오전)', '반차(오후)', '여름휴가', '겨울휴가'].includes(schedule.type);
            const statusText = schedule.approvalStatus === 'approved' ? ' (승인완료)' :
                              schedule.approvalStatus === 'rejected' ? ' (반려)' :
                              schedule.approvalStatus === 'pending' ? ' (승인대기)' :
                              schedule.approvalStatus === 'deletion-pending' ? ' (취소대기)' :
                              schedule.approvalStatus === 'cancelled' ? ' (취소됨)' : '';

            const scheduleMonth = schedule.date.slice(0, 7);
            const currentCalendarMonth = `${year}-${String(month + 1).padStart(2, '0')}`;
            const todayMonth = `${today.getFullYear()}-${String(today.getMonth() + 1).padStart(2, '0')}`;

            return (
              <div
                key={idx}
                className={`schedule-item schedule-${schedule.type.replace(/[()]/g, '')}`}
                onClick={(e) => e.stopPropagation()}
              >
                <span>
                  {schedule.userName.split('@')[0]} - {schedule.type}
                  {isLeaveType ? statusText : ''}
                </span>
                {schedule.userId === user.uid && currentCalendarMonth >= todayMonth && (
                  <button
                    className="delete-schedule-btn"
                    onClick={(e) => {
                      e.stopPropagation();
                      handleDeleteSchedule(schedule.id, schedule.date, schedule.type);
                    }}
                  >
                    ✕
                  </button>
                )}
              </div>
            );
          })}
        </td>
      );

      if (days.length === 7) {
        weeks.push(<tr key={weeks.length}>{days}</tr>);
        days = [];
      }
    }

    // 남은 빈 칸
    if (days.length > 0) {
      while (days.length < 7) {
        days.push(<td key={`empty-end-${days.length}`} className="other-month"></td>);
      }
      weeks.push(<tr key={weeks.length}>{days}</tr>);
    }

    return weeks;
  };

  // 모바일 캘린더 렌더링
  const renderMobileCalendar = () => {
    const daysInMonth = getDaysInMonth(year, month);
    const firstDay = getFirstDayOfMonth(year, month);
    const cells = [];

    const today = new Date();
    const isCurrentMonth = today.getFullYear() === year && today.getMonth() === month;

    // 이전 달의 빈 칸
    for (let i = 0; i < firstDay; i++) {
      cells.push(
        <div key={`empty-${i}`} className="mobile-date-cell other-month">
          <span className="mobile-date-number"></span>
        </div>
      );
    }

    // 현재 달의 날짜
    for (let day = 1; day <= daysInMonth; day++) {
      const dateStr = formatDate(year, month, day);
      const rawDaySchedules = schedules[dateStr] || [];
      let daySchedules = rawDaySchedules.filter(
        s => s.approvalStatus !== 'cancelled' && s.approvalStatus !== 'deletion-pending'
      );

      // 필터 적용
      if (filterEmployee !== 'all') {
        daySchedules = daySchedules.filter(s => s.userId === filterEmployee);
      }
      if (filterLeaveType !== 'all') {
        daySchedules = daySchedules.filter(s => s.type === filterLeaveType);
      }

      const sharedEventsList = sharedEvents[dateStr] || [];
      const isToday = isCurrentMonth && today.getDate() === day;
      const isHoliday = holidays[dateStr];
      const dayOfWeek = new Date(year, month, day).getDay();
      const isSunday = dayOfWeek === 0;
      const isSaturday = dayOfWeek === 6;

      let cellClass = 'mobile-date-cell';
      if (isToday) cellClass += ' today';
      if (isHoliday) cellClass += ' holiday';
      if (isSunday) cellClass += ' sunday';
      if (isSaturday) cellClass += ' saturday';

      cells.push(
        <div key={day} className={cellClass} onClick={() => handleDateClick(day)}>
          <span className="mobile-date-number">{day}</span>
          {isHoliday && <div className="mobile-holiday-name">{isHoliday}</div>}
          <div className="mobile-schedule-dots">
            {sharedEventsList.map((_, idx) => (
              <div key={`shared-${idx}`} className="mobile-schedule-dot shared"></div>
            ))}
            {daySchedules.slice(0, 3).map((schedule, idx) => {
              const isLeaveType = ['연차', '반차(오전)', '반차(오후)', '여름휴가', '겨울휴가'].includes(schedule.type);
              const isRemote = schedule.type === '재택' || schedule.type === '재택근무';
              let dotClass = 'mobile-schedule-dot';
              if (isLeaveType) dotClass += ' leave';
              else if (isRemote) dotClass += ' remote';
              return <div key={idx} className={dotClass}></div>;
            })}
            {(sharedEventsList.length + daySchedules.length) > 3 && (
              <span style={{ fontSize: '8px', color: '#999' }}>+</span>
            )}
          </div>
        </div>
      );
    }

    return cells;
  };

  const monthNames = ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월'];

  return (
    <div className="calendar-container">
      <div className="calendar-header">
        <h2>{year}년 {monthNames[month]}</h2>
        <div className="calendar-nav">
          <button
            className="nav-btn shared-event-btn"
            onClick={() => setShowSharedEventModal(true)}
          >
            공유일정등록
          </button>
          <button className="nav-btn" onClick={handlePrevMonth}>이전</button>
          <button className="nav-btn" onClick={handleNextMonth}>다음</button>
        </div>
      </div>

      <div className="filter-bar">
        <select
          className="filter-select"
          value={filterEmployee}
          onChange={(e) => setFilterEmployee(e.target.value)}
        >
          <option value="all">전체 사원</option>
          {employees.map(emp => (
            <option key={emp.id} value={emp.id}>{emp.name}</option>
          ))}
        </select>
        <select
          className="filter-select"
          value={filterLeaveType}
          onChange={(e) => setFilterLeaveType(e.target.value)}
        >
          <option value="all">전체 유형</option>
          <option value="출근">출근</option>
          <option value="재택">재택</option>
          <option value="연차">연차</option>
          <option value="반차(오전)">반차(오전)</option>
          <option value="반차(오후)">반차(오후)</option>
          <option value="병가">병가</option>
          <option value="경조사">경조사</option>
          <option value="공가">공가</option>
          <option value="여름휴가">여름휴가</option>
          <option value="겨울휴가">겨울휴가</option>
        </select>
      </div>

      {/* PC 캘린더 */}
      <div className="calendar desktop-calendar">
        <table>
          <thead>
            <tr>
              <th className="sunday">일</th>
              <th>월</th>
              <th>화</th>
              <th>수</th>
              <th>목</th>
              <th>금</th>
              <th className="saturday">토</th>
            </tr>
          </thead>
          <tbody>{renderCalendar()}</tbody>
        </table>
      </div>

      {/* 모바일 캘린더 */}
      <div className="calendar mobile-calendar">
        <div className="mobile-weekdays">
          <div className="mobile-weekday sunday">일</div>
          <div className="mobile-weekday">월</div>
          <div className="mobile-weekday">화</div>
          <div className="mobile-weekday">수</div>
          <div className="mobile-weekday">목</div>
          <div className="mobile-weekday">금</div>
          <div className="mobile-weekday saturday">토</div>
        </div>
        <div className="mobile-calendar-grid">{renderMobileCalendar()}</div>
      </div>

      {/* 선택된 날짜 일정 (PC) */}
      {selectedDate && selectedDaySchedules.length > 0 && (
        <div className="daily-schedules">
          <h3>{selectedDate} 일정</h3>
          <div className="daily-schedule-list">
            {selectedDaySchedules.map((schedule, idx) => (
              <div key={idx} className={`daily-schedule-card ${schedule.type.replace(/[()]/g, '')}`}>
                <div className="name">{schedule.userName.split('@')[0]}</div>
                <div className="type">{schedule.type}</div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* 모달들 */}
      {showScheduleModal && selectedDate && (
        <ScheduleModal
          date={selectedDate}
          currentSchedule={selectedDaySchedules.find(s => s.userId === user.uid)?.type}
          onSave={handleSaveSchedule}
          onClose={() => setShowScheduleModal(false)}
          userName={user.displayName || user.email}
        />
      )}

      {showSharedEventModal && (
        <SharedEventModal
          year={year}
          month={month}
          onSave={handleSaveSharedEvent}
          onClose={() => setShowSharedEventModal(false)}
        />
      )}
    </div>
  );
}

export default Calendar;
