import React, { useState, useEffect } from 'react';
import { db } from '../config/firebase';
import { collection, getDocs, doc, updateDoc, deleteDoc, query, where } from 'firebase/firestore';
import { 
  Calendar, 
  TrendingUp, 
  Users, 
  Download, 
  RefreshCw,
  Edit,
  Trash2,
  Gift,
  Filter,
  Search
} from 'lucide-react';
import './AdminLeaveManagement.css';

function AdminLeaveManagement() {
  const [employees, setEmployees] = useState([]);
  const [leaveData, setLeaveData] = useState({});
  const [filteredEmployees, setFilteredEmployees] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [departmentFilter, setDepartmentFilter] = useState('all');
  const [loading, setLoading] = useState(true);
  const [showBulkModal, setShowBulkModal] = useState(false);

  useEffect(() => {
    loadData();
  }, []);

  useEffect(() => {
    filterEmployees();
  }, [searchTerm, departmentFilter, employees, leaveData]);

  const loadData = async () => {
    setLoading(true);
    try {
      // 사원 목록 로드
      const empSnapshot = await getDocs(collection(db, 'employees'));
      const empList = empSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));

      // 연차 사용 내역 로드
      const leaveSnapshot = await getDocs(collection(db, 'leaveUsage'));
      const leaveUsage = {};

      // 사원별로 연차 사용량 집계
      empList.forEach(emp => {
        leaveUsage[emp.id] = {
          total: calculateTotalLeave(emp),
          used: 0,
          pending: 0,
          approved: 0,
          details: []
        };
      });

      leaveSnapshot.docs.forEach(doc => {
        const leave = { id: doc.id, ...doc.data() };
        const userId = leave.userId;

        if (leaveUsage[userId]) {
          leaveUsage[userId].details.push(leave);

          // 상태별 집계
          if (leave.approvalStatus === 'approved') {
            leaveUsage[userId].approved += leave.amount || 0;
            leaveUsage[userId].used += leave.amount || 0;
          } else if (leave.approvalStatus === 'pending') {
            leaveUsage[userId].pending += leave.amount || 0;
            leaveUsage[userId].used += leave.amount || 0;
          }
        }
      });

      setEmployees(empList);
      setLeaveData(leaveUsage);
    } catch (error) {
      console.error('데이터 로드 실패:', error);
      alert('데이터를 불러오는데 실패했습니다.');
    }
    setLoading(false);
  };

  // 총 연차 계산
  const calculateTotalLeave = (employee) => {
    // customTotalLeave가 있으면 우선
    if (employee.customTotalLeave) {
      return employee.customTotalLeave;
    }

    // 입사일 기준 계산
    if (!employee.joinDate) return 15;

    const joinDate = new Date(employee.joinDate);
    const now = new Date();
    const years = now.getFullYear() - joinDate.getFullYear();

    if (years < 1) {
      const months = (now.getFullYear() - joinDate.getFullYear()) * 12 + 
                     (now.getMonth() - joinDate.getMonth());
      return Math.floor(months);
    } else {
      return 15 + Math.min(years - 1, 10);
    }
  };

  // 필터링
  const filterEmployees = () => {
    let filtered = employees;

    // 검색
    if (searchTerm.trim()) {
      const term = searchTerm.toLowerCase();
      filtered = filtered.filter(emp => 
        (emp.name || '').toLowerCase().includes(term) ||
        (emp.department || '').toLowerCase().includes(term) ||
        (emp.email || '').toLowerCase().includes(term)
      );
    }

    // 부서 필터
    if (departmentFilter !== 'all') {
      filtered = filtered.filter(emp => emp.department === departmentFilter);
    }

    setFilteredEmployees(filtered);
  };

  // 부서 목록 추출
  const getDepartments = () => {
    const depts = new Set();
    employees.forEach(emp => {
      if (emp.department) depts.add(emp.department);
    });
    return Array.from(depts).sort();
  };

  // 연차 조정
  const adjustLeave = async (employeeId, employeeName) => {
    const currentTotal = leaveData[employeeId]?.total || 15;
    const newTotal = prompt(
      `${employeeName}님의 연차를 조정합니다.\n현재: ${currentTotal}일\n새로운 연차 일수:`,
      currentTotal
    );

    if (newTotal === null) return;

    const parsedTotal = parseInt(newTotal);
    if (isNaN(parsedTotal) || parsedTotal < 0) {
      alert('올바른 숫자를 입력하세요.');
      return;
    }

    try {
      await updateDoc(doc(db, 'employees', employeeId), {
        customTotalLeave: parsedTotal
      });

      alert('연차가 조정되었습니다.');
      loadData();
    } catch (error) {
      console.error('연차 조정 실패:', error);
      alert('연차 조정에 실패했습니다: ' + error.message);
    }
  };

  // 연차 내역 삭제
  const deleteLeave = async (leaveId, employeeName, date) => {
    if (!window.confirm(`${employeeName}님의 ${date} 연차 내역을 삭제하시겠습니까?`)) {
      return;
    }

    try {
      // leaveUsage 삭제
      await deleteDoc(doc(db, 'leaveUsage', leaveId));

      // schedules에서도 찾아서 삭제
      const schedulesQuery = query(
        collection(db, 'schedules'),
        where('date', '==', date)
      );
      const schedulesSnapshot = await getDocs(schedulesQuery);
      
      for (const scheduleDoc of schedulesSnapshot.docs) {
        const schedule = scheduleDoc.data();
        if (schedule.userId === leaveData[employeeName]?.details?.find(d => d.id === leaveId)?.userId) {
          await deleteDoc(doc(db, 'schedules', scheduleDoc.id));
        }
      }

      alert('연차 내역이 삭제되었습니다.');
      loadData();
    } catch (error) {
      console.error('연차 삭제 실패:', error);
      alert('연차 삭제에 실패했습니다: ' + error.message);
    }
  };

  // 일괄 부여 모달
  const BulkGrantModal = () => {
    const [grantType, setGrantType] = useState('all'); // all, department, individual
    const [selectedDepartment, setSelectedDepartment] = useState('');
    const [leaveAmount, setLeaveAmount] = useState(15);

    const handleBulkGrant = async () => {
      let targetEmployees = [];

      if (grantType === 'all') {
        targetEmployees = employees;
      } else if (grantType === 'department') {
        if (!selectedDepartment) {
          alert('부서를 선택하세요.');
          return;
        }
        targetEmployees = employees.filter(emp => emp.department === selectedDepartment);
      }

      if (targetEmployees.length === 0) {
        alert('대상 사원이 없습니다.');
        return;
      }

      const confirmMsg = `${targetEmployees.length}명에게 ${leaveAmount}일의 연차를 일괄 부여하시겠습니까?`;
      if (!window.confirm(confirmMsg)) return;

      try {
        const updates = targetEmployees.map(emp => 
          updateDoc(doc(db, 'employees', emp.id), {
            customTotalLeave: leaveAmount
          })
        );

        await Promise.all(updates);
        alert('연차가 일괄 부여되었습니다.');
        setShowBulkModal(false);
        loadData();
      } catch (error) {
        console.error('일괄 부여 실패:', error);
        alert('일괄 부여에 실패했습니다: ' + error.message);
      }
    };

    return (
      <div className="modal-overlay" onClick={() => setShowBulkModal(false)}>
        <div className="modal-content" onClick={e => e.stopPropagation()}>
          <div className="modal-header">
            <h2>연차 일괄 부여</h2>
            <button className="close-btn" onClick={() => setShowBulkModal(false)}>×</button>
          </div>

          <div className="modal-body">
            <div className="form-group">
              <label>부여 대상</label>
              <select value={grantType} onChange={e => setGrantType(e.target.value)}>
                <option value="all">전체 사원</option>
                <option value="department">부서별</option>
              </select>
            </div>

            {grantType === 'department' && (
              <div className="form-group">
                <label>부서 선택</label>
                <select 
                  value={selectedDepartment} 
                  onChange={e => setSelectedDepartment(e.target.value)}
                >
                  <option value="">선택하세요</option>
                  {getDepartments().map(dept => (
                    <option key={dept} value={dept}>{dept}</option>
                  ))}
                </select>
              </div>
            )}

            <div className="form-group">
              <label>연차 일수</label>
              <input
                type="number"
                value={leaveAmount}
                onChange={e => setLeaveAmount(parseInt(e.target.value))}
                min="0"
              />
            </div>

            <div className="info-box">
              <strong>안내:</strong>
              <ul>
                <li>기존 연차 설정을 덮어씁니다</li>
                <li>이미 사용한 연차는 영향받지 않습니다</li>
                <li>입사일 기준 자동 계산을 무시합니다</li>
              </ul>
            </div>
          </div>

          <div className="modal-footer">
            <button className="btn-secondary" onClick={() => setShowBulkModal(false)}>
              취소
            </button>
            <button className="btn-primary" onClick={handleBulkGrant}>
              <Gift size={18} />
              일괄 부여
            </button>
          </div>
        </div>
      </div>
    );
  };

  // 엑셀 다운로드 (간단 버전)
  const downloadExcel = () => {
    const csvData = [];
    csvData.push(['이름', '부서', '직급', '총 연차', '사용 연차', '잔여 연차', '승인 대기']);

    filteredEmployees.forEach(emp => {
      const leave = leaveData[emp.id] || { total: 0, used: 0, pending: 0 };
      csvData.push([
        emp.name || '',
        emp.department || '',
        emp.position || '',
        leave.total,
        leave.approved,
        leave.total - leave.used,
        leave.pending
      ]);
    });

    const csv = csvData.map(row => row.join(',')).join('\n');
    const blob = new Blob(['\uFEFF' + csv], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = `연차현황_${new Date().toISOString().split('T')[0]}.csv`;
    link.click();
  };

  // 통계 계산
  const getStats = () => {
    const stats = {
      totalEmployees: filteredEmployees.length,
      totalLeave: 0,
      usedLeave: 0,
      remainingLeave: 0,
      pendingLeave: 0
    };

    filteredEmployees.forEach(emp => {
      const leave = leaveData[emp.id] || { total: 0, used: 0, pending: 0 };
      stats.totalLeave += leave.total;
      stats.usedLeave += leave.approved;
      stats.pendingLeave += leave.pending;
    });

    stats.remainingLeave = stats.totalLeave - stats.usedLeave - stats.pendingLeave;

    return stats;
  };

  if (loading) {
    return (
      <div className="admin-leave-page">
        <div className="loading-state">데이터를 불러오는 중...</div>
      </div>
    );
  }

  const stats = getStats();

  return (
    <div className="admin-leave-page">
      {/* 헤더 */}
      <div className="page-header">
        <div className="header-left">
          <Calendar size={28} />
          <div>
            <h1>사원 연차 관리</h1>
            <p>전체 사원의 연차 현황을 조회하고 관리합니다</p>
          </div>
        </div>

        <div className="header-actions">
          <button className="btn-secondary" onClick={loadData}>
            <RefreshCw size={18} />
            새로고침
          </button>
          <button className="btn-secondary" onClick={downloadExcel}>
            <Download size={18} />
            엑셀 다운로드
          </button>
          <button className="btn-primary" onClick={() => setShowBulkModal(true)}>
            <Gift size={18} />
            일괄 부여
          </button>
        </div>
      </div>

      {/* 통계 카드 */}
      <div className="stats-grid">
        <div className="stat-card">
          <div className="stat-icon blue">
            <Users size={24} />
          </div>
          <div className="stat-content">
            <h3>전체 사원</h3>
            <div className="stat-value">{stats.totalEmployees}</div>
            <p className="stat-label">명</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon green">
            <TrendingUp size={24} />
          </div>
          <div className="stat-content">
            <h3>총 연차</h3>
            <div className="stat-value">{stats.totalLeave}</div>
            <p className="stat-label">일</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon orange">
            <Calendar size={24} />
          </div>
          <div className="stat-content">
            <h3>사용 연차</h3>
            <div className="stat-value">{stats.usedLeave}</div>
            <p className="stat-label">일</p>
          </div>
        </div>

        <div className="stat-card">
          <div className="stat-icon purple">
            <TrendingUp size={24} />
          </div>
          <div className="stat-content">
            <h3>잔여 연차</h3>
            <div className="stat-value">{stats.remainingLeave}</div>
            <p className="stat-label">일</p>
          </div>
        </div>
      </div>

      {/* 필터 */}
      <div className="filter-section">
        <div className="search-box">
          <Search size={18} />
          <input
            type="text"
            placeholder="이름, 부서, 이메일 검색..."
            value={searchTerm}
            onChange={e => setSearchTerm(e.target.value)}
          />
        </div>

        <div className="filter-box">
          <Filter size={18} />
          <select value={departmentFilter} onChange={e => setDepartmentFilter(e.target.value)}>
            <option value="all">전체 부서</option>
            {getDepartments().map(dept => (
              <option key={dept} value={dept}>{dept}</option>
            ))}
          </select>
        </div>
      </div>

      {/* 사원 연차 테이블 */}
      <div className="leave-table-container">
        <table className="leave-table">
          <thead>
            <tr>
              <th>이름</th>
              <th>부서</th>
              <th>직급</th>
              <th>총 연차</th>
              <th>사용</th>
              <th>대기</th>
              <th>잔여</th>
              <th>사용률</th>
              <th>작업</th>
            </tr>
          </thead>
          <tbody>
            {filteredEmployees.map(emp => {
              const leave = leaveData[emp.id] || { total: 0, used: 0, pending: 0, approved: 0 };
              const remaining = leave.total - leave.used;
              const usageRate = leave.total > 0 ? ((leave.approved / leave.total) * 100).toFixed(1) : 0;

              return (
                <tr key={emp.id}>
                  <td className="name-cell">
                    <strong>{emp.name || '이름 없음'}</strong>
                  </td>
                  <td>{emp.department || '-'}</td>
                  <td>{emp.position || '-'}</td>
                  <td className="number-cell">{leave.total}일</td>
                  <td className="number-cell used">{leave.approved}일</td>
                  <td className="number-cell pending">{leave.pending}일</td>
                  <td className="number-cell remaining">
                    <span className={remaining < 3 ? 'warning' : ''}>
                      {remaining}일
                    </span>
                  </td>
                  <td className="number-cell">
                    <div className="progress-bar">
                      <div 
                        className="progress-fill" 
                        style={{ width: `${Math.min(usageRate, 100)}%` }}
                      />
                      <span className="progress-text">{usageRate}%</span>
                    </div>
                  </td>
                  <td>
                    <div className="action-buttons">
                      <button 
                        className="btn-icon" 
                        onClick={() => adjustLeave(emp.id, emp.name)}
                        title="연차 조정"
                      >
                        <Edit size={16} />
                      </button>
                    </div>
                  </td>
                </tr>
              );
            })}
          </tbody>
        </table>

        {filteredEmployees.length === 0 && (
          <div className="empty-state">
            <Users size={48} />
            <p>검색 결과가 없습니다.</p>
          </div>
        )}
      </div>

      {/* 일괄 부여 모달 */}
      {showBulkModal && <BulkGrantModal />}
    </div>
  );
}

export default AdminLeaveManagement;
