import React, { useState, useEffect } from 'react';
import { useAuthStore } from '../../stores/authStore';
import { db } from '../../config/firebase';
import { collection, getDocs } from 'firebase/firestore';
import Header from './Header';
import Navigation from './Navigation';
import Dashboard from '../../pages/Dashboard';
import Calendar from '../../pages/Calendar';
import AnnualLeave from '../../pages/AnnualLeave';
import ApprovalManagement from '../../pages/ApprovalManagement';
import PTSchedule from '../../pages/PTSchedule';
import Department from '../../pages/Department';
import AdminEmployees from '../../pages/AdminEmployees';
import ApprovalLine from '../../pages/ApprovalLine';
import AdminLeaveManagement from '../../pages/AdminLeaveManagement';
import Notes from '../../pages/Notes';
import Messenger from '../../pages/Messenger';
import Profile from '../../pages/Profile';
import Settings from '../../pages/Settings';
import './MainLayout.css';

function MainLayout() {
  const [activeTab, setActiveTab] = useState('dashboard');
  const [employees, setEmployees] = useState([]);
  // 모바일에서는 기본 닫힘, PC에서는 기본 열림
  const [isMenuOpen, setIsMenuOpen] = useState(window.innerWidth > 768);
  const { userData, isAdmin, isSubAdmin, isApprover } = useAuthStore();

  // 사원 목록 로드
  useEffect(() => {
    loadEmployees();
  }, []);

  // 화면 크기 변경 감지
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth > 768) {
        setIsMenuOpen(true);
      }
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const loadEmployees = async () => {
    try {
      const snapshot = await getDocs(collection(db, 'employees'));
      const employeeList = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setEmployees(employeeList);
    } catch (error) {
      console.error('사원 목록 로드 실패:', error);
    }
  };

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  // 검색에서 탭 이동
  const handleNavigateFromSearch = (tab, data) => {
    setActiveTab(tab);
    // TODO: 데이터를 이용한 세부 탐색 (예: 특정 노트 열기)
  };

  return (
    <div className="app-container">
      <Header onMenuToggle={toggleMenu} onNavigate={handleNavigateFromSearch} />
      <div className="main-content">
        <Navigation 
          activeTab={activeTab} 
          setActiveTab={setActiveTab}
          isOpen={isMenuOpen}
          onClose={() => setIsMenuOpen(false)}
        />
      
      <div className={`content ${isMenuOpen ? 'menu-open' : 'menu-closed'}`}>
        {activeTab === 'dashboard' && <Dashboard setActiveTab={setActiveTab} />}
        {activeTab === 'calendar' && <Calendar employees={employees} />}
        {activeTab === 'annualLeave' && <AnnualLeave />}
        {activeTab === 'approval' && <ApprovalManagement />}
        {activeTab === 'ptSchedule' && <PTSchedule />}
        {activeTab === 'department' && <Department />}
        {activeTab === 'notes' && <Notes />}
        {activeTab === 'messenger' && <Messenger />}
        {activeTab === 'admin' && <AdminEmployees />}
        {activeTab === 'approval-line' && <ApprovalLine />}
        {activeTab === 'admin-leave' && <AdminLeaveManagement />}
        {activeTab === 'profile' && <Profile />}
        {activeTab === 'settings' && <Settings />}
        
        {/* 다른 탭들은 추후 추가 */}
        {activeTab !== 'dashboard' && 
         activeTab !== 'calendar' && 
         activeTab !== 'annualLeave' && 
         activeTab !== 'approval' && 
         activeTab !== 'ptSchedule' && 
         activeTab !== 'department' &&
         activeTab !== 'notes' &&
         activeTab !== 'messenger' &&
         activeTab !== 'admin' &&
         activeTab !== 'approval-line' &&
         activeTab !== 'admin-leave' &&
         activeTab !== 'profile' &&
         activeTab !== 'settings' && (
          <div className="content-placeholder">
            <h2>{activeTab} 페이지</h2>
            <p>개발 중입니다...</p>
          </div>
        )}
      </div>
      </div>
    </div>
  );
}

export default MainLayout;
