import React, { useState } from 'react';
import './LeaveRegisterModal.css';

function LeaveRegisterModal({ onClose, onSubmit, remainingLeave }) {
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [leaveType, setLeaveType] = useState('연차');
  const [note, setNote] = useState('');
  const [files, setFiles] = useState([]);
  const [uploading, setUploading] = useState(false);

  const handleFileSelect = (event) => {
    const selectedFiles = Array.from(event.target.files);
    const validFiles = selectedFiles.filter(file => {
      const maxSize = 5 * 1024 * 1024; // 5MB
      if (file.size > maxSize) {
        alert(`${file.name}은(는) 5MB를 초과합니다.`);
        return false;
      }
      return true;
    });
    setFiles([...files, ...validFiles]);
  };

  const removeFile = (index) => {
    setFiles(files.filter((_, i) => i !== index));
  };

  const handleSubmit = () => {
    if (!startDate || !endDate) {
      alert('시작일과 종료일을 모두 선택해주세요.');
      return;
    }

    onSubmit({
      startDate,
      endDate,
      leaveType,
      note,
      files
    });
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content leave-register-modal" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3>연차 등록</h3>
          <button className="modal-close-btn" onClick={onClose}>✕</button>
        </div>

        <div className="modal-body">
          <div className="remaining-leave-info">
            <span>남은 연차: <strong>{remainingLeave}일</strong></span>
          </div>

          <div className="form-group">
            <label>시작일</label>
            <input
              type="date"
              value={startDate}
              onChange={(e) => setStartDate(e.target.value)}
              className="form-input"
            />
          </div>

          <div className="form-group">
            <label>종료일</label>
            <input
              type="date"
              value={endDate}
              onChange={(e) => setEndDate(e.target.value)}
              className="form-input"
            />
          </div>

          <div className="form-group">
            <label>유형</label>
            <select
              value={leaveType}
              onChange={(e) => setLeaveType(e.target.value)}
              className="form-select"
            >
              <option value="연차">연차</option>
              <option value="반차(오전)">반차(오전) - 0.5일</option>
              <option value="반차(오후)">반차(오후) - 0.5일</option>
              <option value="병가">병가</option>
              <option value="경조사">경조사 휴가</option>
              <option value="공가">공가</option>
              <option value="여름휴가">여름휴가</option>
              <option value="겨울휴가">겨울휴가</option>
            </select>
          </div>

          <div className="form-group">
            <label>사유 (선택)</label>
            <textarea
              value={note}
              onChange={(e) => setNote(e.target.value)}
              placeholder="휴가 사유를 입력하세요"
              className="form-textarea"
              rows="3"
            />
          </div>

          <div className="form-group">
            <label>첨부 파일 (선택) - 병가 진단서 등</label>
            <div
              className="file-upload-area"
              onClick={() => document.getElementById('leave-file-input').click()}
            >
              <input
                id="leave-file-input"
                type="file"
                multiple
                onChange={handleFileSelect}
                style={{ display: 'none' }}
                accept="image/*,.pdf,.doc,.docx"
              />
              <div className="file-upload-icon">📎</div>
              <div className="file-upload-text">
                클릭하여 파일 선택 (최대 5MB)
              </div>
            </div>

            {files.length > 0 && (
              <div className="file-list">
                {files.map((file, index) => (
                  <div key={index} className="file-item">
                    <span className="file-name">📄 {file.name}</span>
                    <span className="file-size">
                      {(file.size / 1024).toFixed(1)}KB
                    </span>
                    <button
                      className="file-remove-btn"
                      onClick={() => removeFile(index)}
                    >
                      ✕
                    </button>
                  </div>
                ))}
              </div>
            )}
          </div>

          <div className="modal-notice">
            <p>💡 주말(토,일)은 자동으로 제외됩니다.</p>
            <p>💡 반차는 0.5일로 계산됩니다.</p>
          </div>

          {uploading && (
            <div className="uploading-message">
              파일 업로드 중...
            </div>
          )}
        </div>

        <div className="modal-footer">
          <button 
            className="modal-btn cancel-btn" 
            onClick={() => {
              setFiles([]);
              setNote('');
              onClose();
            }}
          >
            취소
          </button>
          <button 
            className="modal-btn save-btn" 
            onClick={handleSubmit}
            disabled={uploading}
          >
            등록
          </button>
        </div>
      </div>
    </div>
  );
}

export default LeaveRegisterModal;
