import React, { useState, useEffect, useRef } from 'react';
import { db } from '../config/firebase';
import { doc, updateDoc, getDoc } from 'firebase/firestore';
import { useAuthStore } from '../stores/authStore';
import { 
  User, Mail, Building, Briefcase, Calendar, Edit2, 
  Camera, Save, X, Phone, MapPin, Cake
} from 'lucide-react';
import './Profile.css';

const Profile = () => {
  const { user, userData, setUserData } = useAuthStore();
  
  const [isEditing, setIsEditing] = useState(false);
  const [isSaving, setIsSaving] = useState(false);
  const [profileImage, setProfileImage] = useState('');
  
  // 편집 가능한 필드
  const [editData, setEditData] = useState({
    name: '',
    phone: '',
    bio: '',
    location: '',
    birthDate: ''
  });

  const fileInputRef = useRef(null);

  useEffect(() => {
    if (userData) {
      setEditData({
        name: userData.name || '',
        phone: userData.phone || '',
        bio: userData.bio || '',
        location: userData.location || '',
        birthDate: userData.birthDate || ''
      });
      setProfileImage(userData.profileImage || '');
    }
  }, [userData]);

  const handleEdit = () => {
    setIsEditing(true);
  };

  const handleCancel = () => {
    setIsEditing(false);
    // 원래 데이터로 복원
    if (userData) {
      setEditData({
        name: userData.name || '',
        phone: userData.phone || '',
        bio: userData.bio || '',
        location: userData.location || '',
        birthDate: userData.birthDate || ''
      });
    }
  };

  const handleSave = async () => {
    if (!user) return;

    try {
      setIsSaving(true);

      const userRef = doc(db, 'employees', user.uid);
      await updateDoc(userRef, {
        ...editData,
        profileImage
      });

      // 로컬 상태 업데이트
      setUserData({
        ...userData,
        ...editData,
        profileImage
      });

      setIsEditing(false);
      alert('프로필이 업데이트되었습니다!');
    } catch (error) {
      console.error('프로필 업데이트 실패:', error);
      alert('프로필 업데이트에 실패했습니다.');
    } finally {
      setIsSaving(false);
    }
  };

  const handleImageClick = () => {
    if (isEditing) {
      fileInputRef.current?.click();
    }
  };

  const handleImageChange = (e) => {
    const file = e.target.files?.[0];
    if (!file) return;

    // 이미지 미리보기 (Base64로 변환)
    const reader = new FileReader();
    reader.onloadend = () => {
      setProfileImage(reader.result);
    };
    reader.readAsDataURL(file);
  };

  const getInitials = () => {
    if (!editData.name) return '?';
    const names = editData.name.split(' ');
    if (names.length >= 2) {
      return names[0][0] + names[1][0];
    }
    return editData.name.substring(0, 2);
  };

  const formatDate = (dateString) => {
    if (!dateString) return '미등록';
    
    const date = new Date(dateString);
    return date.toLocaleDateString('ko-KR', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  return (
    <div className="profile-page">
      {/* 프로필 헤더 */}
      <div className="profile-header">
        <div className="profile-header-content">
          <h1>내 프로필</h1>
          <p>개인 정보를 관리하고 수정할 수 있습니다</p>
        </div>

        {!isEditing ? (
          <button className="btn-edit" onClick={handleEdit}>
            <Edit2 size={18} />
            <span>수정</span>
          </button>
        ) : (
          <div className="edit-actions">
            <button className="btn-cancel" onClick={handleCancel} disabled={isSaving}>
              <X size={18} />
              <span>취소</span>
            </button>
            <button className="btn-save" onClick={handleSave} disabled={isSaving}>
              <Save size={18} />
              <span>{isSaving ? '저장 중...' : '저장'}</span>
            </button>
          </div>
        )}
      </div>

      <div className="profile-content">
        {/* 좌측: 프로필 사진 & 기본 정보 */}
        <div className="profile-sidebar">
          <div className="profile-card">
            {/* 프로필 사진 */}
            <div className="profile-image-section">
              <div 
                className={`profile-image-wrapper ${isEditing ? 'editable' : ''}`}
                onClick={handleImageClick}
              >
                {profileImage ? (
                  <img src={profileImage} alt="프로필" className="profile-image" />
                ) : (
                  <div className="profile-image-placeholder">
                    <span className="profile-initials">{getInitials()}</span>
                  </div>
                )}
                
                {isEditing && (
                  <div className="profile-image-overlay">
                    <Camera size={24} />
                    <span>사진 변경</span>
                  </div>
                )}
              </div>
              
              <input
                ref={fileInputRef}
                type="file"
                accept="image/*"
                onChange={handleImageChange}
                style={{ display: 'none' }}
              />
            </div>

            {/* 이름 */}
            <div className="profile-name-section">
              {isEditing ? (
                <input
                  type="text"
                  className="profile-name-input"
                  value={editData.name}
                  onChange={(e) => setEditData({ ...editData, name: e.target.value })}
                  placeholder="이름"
                />
              ) : (
                <h2>{editData.name || '이름 없음'}</h2>
              )}
              <p className="profile-email">{userData?.email || user?.email}</p>
            </div>

            {/* 기본 정보 카드 */}
            <div className="profile-info-cards">
              <div className="info-card">
                <div className="info-icon">
                  <Building size={20} />
                </div>
                <div className="info-content">
                  <span className="info-label">부서</span>
                  <span className="info-value">{userData?.department || '미지정'}</span>
                </div>
              </div>

              <div className="info-card">
                <div className="info-icon">
                  <Briefcase size={20} />
                </div>
                <div className="info-content">
                  <span className="info-label">직급</span>
                  <span className="info-value">{userData?.position || '미지정'}</span>
                </div>
              </div>

              <div className="info-card">
                <div className="info-icon">
                  <Calendar size={20} />
                </div>
                <div className="info-content">
                  <span className="info-label">입사일</span>
                  <span className="info-value">
                    {userData?.joinDate ? formatDate(userData.joinDate) : '미등록'}
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* 우측: 상세 정보 */}
        <div className="profile-main">
          {/* 연락처 정보 */}
          <div className="profile-section">
            <h3>연락처 정보</h3>
            
            <div className="form-group">
              <label>
                <Mail size={16} />
                <span>이메일</span>
              </label>
              <input
                type="email"
                value={userData?.email || user?.email}
                disabled
                className="form-input disabled"
              />
              <small>이메일은 변경할 수 없습니다</small>
            </div>

            <div className="form-group">
              <label>
                <Phone size={16} />
                <span>전화번호</span>
              </label>
              <input
                type="tel"
                value={editData.phone}
                onChange={(e) => setEditData({ ...editData, phone: e.target.value })}
                placeholder="010-1234-5678"
                disabled={!isEditing}
                className={`form-input ${!isEditing ? 'disabled' : ''}`}
              />
            </div>

            <div className="form-group">
              <label>
                <MapPin size={16} />
                <span>지역</span>
              </label>
              <input
                type="text"
                value={editData.location}
                onChange={(e) => setEditData({ ...editData, location: e.target.value })}
                placeholder="예: 서울특별시 강남구"
                disabled={!isEditing}
                className={`form-input ${!isEditing ? 'disabled' : ''}`}
              />
            </div>

            <div className="form-group">
              <label>
                <Cake size={16} />
                <span>생년월일</span>
              </label>
              <input
                type="date"
                value={editData.birthDate}
                onChange={(e) => setEditData({ ...editData, birthDate: e.target.value })}
                disabled={!isEditing}
                className={`form-input ${!isEditing ? 'disabled' : ''}`}
              />
            </div>
          </div>

          {/* 자기소개 */}
          <div className="profile-section">
            <h3>자기소개</h3>
            
            <div className="form-group">
              <textarea
                value={editData.bio}
                onChange={(e) => setEditData({ ...editData, bio: e.target.value })}
                placeholder="간단한 자기소개를 작성해주세요..."
                disabled={!isEditing}
                className={`form-textarea ${!isEditing ? 'disabled' : ''}`}
                rows={6}
              />
            </div>
          </div>

          {/* 권한 정보 */}
          <div className="profile-section">
            <h3>권한 정보</h3>
            
            <div className="permissions-grid">
              <div className="permission-item">
                <span className="permission-label">관리자 권한</span>
                <span className={`permission-badge ${userData?.isAdmin ? 'active' : ''}`}>
                  {userData?.isAdmin ? '있음' : '없음'}
                </span>
              </div>

              <div className="permission-item">
                <span className="permission-label">승인자 권한</span>
                <span className={`permission-badge ${userData?.isApprover ? 'active' : ''}`}>
                  {userData?.isApprover ? '있음' : '없음'}
                </span>
              </div>

              <div className="permission-item">
                <span className="permission-label">계정 상태</span>
                <span className="permission-badge active">활성화</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Profile;
