import React, { useState, useEffect } from 'react';
import { db } from '../config/firebase';
import { collection, getDocs } from 'firebase/firestore';
import { Users, Phone, Mail, Calendar, Search, Building2, Grid, List } from 'lucide-react';
import './Department.css';

function Department() {
  const [employees, setEmployees] = useState([]);
  const [filteredEmployees, setFilteredEmployees] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [viewMode, setViewMode] = useState('grid'); // 'grid' or 'list'
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadEmployees();
  }, []);

  useEffect(() => {
    filterEmployees();
  }, [searchTerm, employees]);

  const loadEmployees = async () => {
    setLoading(true);
    try {
      const snapshot = await getDocs(collection(db, 'employees'));
      const employeeList = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      
      // 정렬: 부서 > 직급 > 이름
      employeeList.sort((a, b) => {
        if (a.department !== b.department) {
          return (a.department || '').localeCompare(b.department || '');
        }
        const positionOrder = { '대표': 0, '이사': 1, '팀장': 2, '매니저': 3, '사원': 4 };
        const aOrder = positionOrder[a.position] ?? 999;
        const bOrder = positionOrder[b.position] ?? 999;
        if (aOrder !== bOrder) {
          return aOrder - bOrder;
        }
        return (a.name || '').localeCompare(b.name || '');
      });
      
      setEmployees(employeeList);
    } catch (error) {
      console.error('사원 목록 로드 실패:', error);
    }
    setLoading(false);
  };

  const filterEmployees = () => {
    if (!searchTerm.trim()) {
      setFilteredEmployees(employees);
      return;
    }

    const term = searchTerm.toLowerCase();
    const filtered = employees.filter(emp => 
      (emp.name || '').toLowerCase().includes(term) ||
      (emp.department || '').toLowerCase().includes(term) ||
      (emp.position || '').toLowerCase().includes(term) ||
      (emp.email || '').toLowerCase().includes(term) ||
      (emp.phone || '').toLowerCase().includes(term)
    );
    setFilteredEmployees(filtered);
  };

  const getDepartments = () => {
    const depts = {};
    employees.forEach(emp => {
      const dept = emp.department || '미지정';
      if (!depts[dept]) {
        depts[dept] = [];
      }
      depts[dept].push(emp);
    });
    return depts;
  };

  const getPositionColor = (position) => {
    const colors = {
      '대표': '#9C27B0',
      '이사': '#E91E63',
      '팀장': '#2196F3',
      '매니저': '#4CAF50',
      '사원': '#FF9800'
    };
    return colors[position] || '#757575';
  };

  if (loading) {
    return (
      <div className="department-page">
        <div className="loading-state">사원 정보를 불러오는 중...</div>
      </div>
    );
  }

  const departments = getDepartments();

  return (
    <div className="department-page">
      {/* 헤더 */}
      <div className="department-header">
        <div className="header-left">
          <Building2 size={28} />
          <div>
            <h1>조직도</h1>
            <p>{employees.length}명의 사원</p>
          </div>
        </div>
        
        <div className="header-right">
          <div className="search-box">
            <Search size={18} />
            <input
              type="text"
              placeholder="이름, 부서, 직급, 연락처 검색..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
          </div>
          
          <div className="view-toggle">
            <button
              className={viewMode === 'grid' ? 'active' : ''}
              onClick={() => setViewMode('grid')}
              title="카드 보기"
            >
              <Grid size={18} />
            </button>
            <button
              className={viewMode === 'list' ? 'active' : ''}
              onClick={() => setViewMode('list')}
              title="목록 보기"
            >
              <List size={18} />
            </button>
          </div>
        </div>
      </div>

      {/* 검색 결과가 없을 때 */}
      {filteredEmployees.length === 0 && (
        <div className="empty-state">
          <Users size={48} />
          <p>검색 결과가 없습니다.</p>
        </div>
      )}

      {/* 부서별 사원 목록 */}
      {viewMode === 'grid' ? (
        <div className="department-sections">
          {Object.entries(departments).map(([dept, empList]) => {
            const visibleEmp = empList.filter(emp => 
              !searchTerm || filteredEmployees.some(f => f.id === emp.id)
            );
            
            if (visibleEmp.length === 0) return null;

            return (
              <div key={dept} className="department-section">
                <div className="section-header">
                  <h2>{dept}</h2>
                  <span className="employee-count">{visibleEmp.length}명</span>
                </div>
                
                <div className="employee-grid">
                  {visibleEmp.map(emp => (
                    <div key={emp.id} className="employee-card">
                      <div className="card-header">
                        <div className="avatar">
                          {(emp.name || '?')[0]}
                        </div>
                        <div 
                          className="position-badge"
                          style={{ backgroundColor: getPositionColor(emp.position) }}
                        >
                          {emp.position || '직급 미지정'}
                        </div>
                      </div>
                      
                      <div className="card-body">
                        <h3 className="employee-name">{emp.name || '이름 없음'}</h3>
                        <p className="employee-dept">{emp.department || '부서 미지정'}</p>
                        
                        <div className="contact-info">
                          {emp.phone && (
                            <div className="contact-item">
                              <Phone size={14} />
                              <span>{emp.phone}</span>
                            </div>
                          )}
                          {emp.email && (
                            <div className="contact-item">
                              <Mail size={14} />
                              <span>{emp.email}</span>
                            </div>
                          )}
                          {emp.joinDate && (
                            <div className="contact-item">
                              <Calendar size={14} />
                              <span>입사: {emp.joinDate}</span>
                            </div>
                          )}
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            );
          })}
        </div>
      ) : (
        /* 리스트 뷰 */
        <div className="employee-table-container">
          <table className="employee-table">
            <thead>
              <tr>
                <th>이름</th>
                <th>부서</th>
                <th>직급</th>
                <th>연락처</th>
                <th>이메일</th>
                <th>입사일</th>
              </tr>
            </thead>
            <tbody>
              {filteredEmployees.map(emp => (
                <tr key={emp.id}>
                  <td>
                    <div className="name-cell">
                      <div className="avatar small">
                        {(emp.name || '?')[0]}
                      </div>
                      <span className="name-text">{emp.name || '이름 없음'}</span>
                    </div>
                  </td>
                  <td>{emp.department || '-'}</td>
                  <td>
                    <span 
                      className="position-tag"
                      style={{ backgroundColor: getPositionColor(emp.position) }}
                    >
                      {emp.position || '-'}
                    </span>
                  </td>
                  <td>{emp.phone || '-'}</td>
                  <td>{emp.email || '-'}</td>
                  <td>{emp.joinDate || '-'}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}

export default Department;
