import React, { useState } from 'react';
import { useAuthStore } from '../../stores/authStore';
import { Bell, Menu, Search, Command } from 'lucide-react';
import GlobalSearch from '../common/GlobalSearch';
import './Header.css';

function Header({ onMenuToggle, onNavigate }) {
  const { userData, isAdmin, isSubAdmin, isApprover, isPTScheduleEditor, logout } = useAuthStore();
  const [showNotifications, setShowNotifications] = useState(false);
  const [showSearch, setShowSearch] = useState(false);

  const handleLogout = async () => {
    if (confirm('로그아웃 하시겠습니까?')) {
      await logout();
    }
  };

  const userName = userData?.name || userData?.email;

  // Ctrl+K 단축키 감지
  React.useEffect(() => {
    const handleKeyDown = (e) => {
      if ((e.ctrlKey || e.metaKey) && e.key === 'k') {
        e.preventDefault();
        setShowSearch(true);
      }
    };
    
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, []);

  return (
    <header className="header">
      <div className="header-left">
        <button className="menu-toggle-btn" onClick={onMenuToggle} title="메뉴 열기/닫기">
          <Menu size={24} />
        </button>
        <div className="header-title">
          <h1>ONFL 그룹웨어</h1>
          <p className="header-subtitle">캘린더 · 근태 · 연차 관리</p>
        </div>
      </div>

      <div className="header-center">
        <button 
          className="search-trigger"
          onClick={() => setShowSearch(true)}
          title="검색 (Ctrl+K)"
        >
          <Search size={18} />
          <span>검색...</span>
          <kbd className="search-shortcut">
            <Command size={12} />
            <span>K</span>
          </kbd>
        </button>
      </div>
      
      <div className="user-info">
        <button
          className="notification-bell"
          onClick={() => setShowNotifications(!showNotifications)}
          title="알림"
        >
          <Bell size={20} />
          {/* 알림 개수 표시 - 추후 구현 */}
        </button>
        
        <span className="user-name">{userName}</span>
        
        {/* PC에서만 뱃지 표시 - 모바일은 Navigation에 표시 */}
        <span className="user-badges">
          {isAdmin && <span className="badge badge-admin">관리자</span>}
          {isSubAdmin && !isAdmin && <span className="badge badge-subadmin">부관리자</span>}
          {isApprover && !isAdmin && <span className="badge badge-approver">승인자</span>}
          {isPTScheduleEditor && <span className="badge badge-pt">PT편집자</span>}
        </span>
      </div>

      {/* 통합 검색 모달 */}
      <GlobalSearch 
        isOpen={showSearch}
        onClose={() => setShowSearch(false)}
        onNavigate={onNavigate}
      />
    </header>
  );
}

export default Header;
