import React, { useState, useEffect } from 'react';
import { useAuthStore } from '../stores/authStore';
import { db } from '../config/firebase';
import { collection, query, where, getDocs, orderBy, limit } from 'firebase/firestore';
import { 
  Calendar as CalendarIcon, Clock, AlertCircle, TrendingUp, 
  Users, CheckCircle, XCircle, MessageSquare, FileText, 
  Plus, Send, CalendarPlus, UserPlus, Activity, Award,
  BarChart3, PieChart, ArrowUp, ArrowDown
} from 'lucide-react';
import './Dashboard.css';

function Dashboard({ setActiveTab }) {
  const { user, userData, isApprover, isAdmin } = useAuthStore();
  
  // 기본 통계
  const [todaySchedules, setTodaySchedules] = useState([]);
  const [leaveStats, setLeaveStats] = useState({ total: 0, used: 0, remaining: 0 });
  const [pendingApprovals, setPendingApprovals] = useState(0);
  
  // 새로운 통계
  const [teamStats, setTeamStats] = useState({
    totalEmployees: 0,
    onLeave: 0,
    activeProjects: 0,
    messagesCount: 0
  });
  
  const [recentActivities, setRecentActivities] = useState([]);
  const [monthlyData, setMonthlyData] = useState({
    labels: [],
    leaves: [],
    approvals: []
  });
  
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (user) {
      loadDashboardData();
    }
  }, [user]);

  const loadDashboardData = async () => {
    setLoading(true);
    try {
      await Promise.all([
        loadTodaySchedules(),
        loadLeaveStats(),
        isApprover && loadPendingApprovals(),
        loadTeamStats(),
        loadRecentActivities(),
        loadMonthlyData()
      ]);
    } catch (error) {
      console.error('대시보드 데이터 로드 실패:', error);
    }
    setLoading(false);
  };

  // 오늘의 일정 로드
  const loadTodaySchedules = async () => {
    try {
      const today = new Date().toISOString().split('T')[0];
      
      const q = query(
        collection(db, 'schedules'),
        where('date', '==', today)
      );
      const snapshot = await getDocs(q);
      
      const schedules = snapshot.docs
        .map(doc => ({ id: doc.id, ...doc.data() }))
        .filter(schedule => 
          schedule.approvalStatus === 'approved' || 
          schedule.userId === user.uid
        )
        .slice(0, 5);
      
      setTodaySchedules(schedules);
    } catch (error) {
      console.error('일정 로드 실패:', error);
    }
  };

  // 연차 통계 로드
  const loadLeaveStats = async () => {
    try {
      const q = query(
        collection(db, 'annualLeaves'),
        where('userId', '==', user.uid)
      );
      const snapshot = await getDocs(q);
      
      const leaves = snapshot.docs.map(doc => doc.data());
      const approvedLeaves = leaves.filter(leave => leave.status === 'approved');
      
      const usedDays = approvedLeaves.reduce((sum, leave) => sum + (leave.days || 0), 0);
      const totalDays = userData?.totalAnnualLeave || 15;
      
      setLeaveStats({
        total: totalDays,
        used: usedDays,
        remaining: totalDays - usedDays
      });
    } catch (error) {
      console.error('연차 통계 로드 실패:', error);
    }
  };

  // 승인 대기 로드
  const loadPendingApprovals = async () => {
    try {
      const q = query(
        collection(db, 'annualLeaves'),
        where('status', '==', 'pending')
      );
      const snapshot = await getDocs(q);
      setPendingApprovals(snapshot.size);
    } catch (error) {
      console.error('승인 대기 로드 실패:', error);
    }
  };

  // 팀 통계 로드
  const loadTeamStats = async () => {
    try {
      // 전체 사원 수
      const employeesSnapshot = await getDocs(collection(db, 'employees'));
      const totalEmployees = employeesSnapshot.size;
      
      // 오늘 연차 중인 사원
      const today = new Date().toISOString().split('T')[0];
      const leavesQuery = query(
        collection(db, 'annualLeaves'),
        where('status', '==', 'approved')
      );
      const leavesSnapshot = await getDocs(leavesQuery);
      const onLeaveToday = leavesSnapshot.docs.filter(doc => {
        const leave = doc.data();
        return leave.startDate <= today && leave.endDate >= today;
      }).length;
      
      // 활성 프로젝트 (노트 프로젝트 수)
      const projectsSnapshot = await getDocs(collection(db, 'projects'));
      const activeProjects = projectsSnapshot.size;
      
      // 오늘 메시지 수
      const messagesQuery = query(
        collection(db, 'directMessages'),
        limit(100)
      );
      const messagesSnapshot = await getDocs(messagesQuery);
      const todayMessages = messagesSnapshot.docs.filter(doc => {
        const msg = doc.data();
        if (!msg.timestamp) return false;
        const msgDate = msg.timestamp.toDate().toISOString().split('T')[0];
        return msgDate === today;
      }).length;
      
      setTeamStats({
        totalEmployees,
        onLeave: onLeaveToday,
        activeProjects,
        messagesCount: todayMessages
      });
    } catch (error) {
      console.error('팀 통계 로드 실패:', error);
    }
  };

  // 최근 활동 로드
  const loadRecentActivities = async () => {
    try {
      const activities = [];
      
      // 최근 승인된 연차
      const leavesQuery = query(
        collection(db, 'annualLeaves'),
        where('userId', '==', user.uid),
        orderBy('createdAt', 'desc'),
        limit(3)
      );
      const leavesSnapshot = await getDocs(leavesQuery);
      leavesSnapshot.docs.forEach(doc => {
        const leave = doc.data();
        activities.push({
          id: doc.id,
          type: 'leave',
          title: `연차 ${leave.status === 'approved' ? '승인' : leave.status === 'rejected' ? '반려' : '신청'}`,
          description: `${leave.startDate} ~ ${leave.endDate}`,
          time: leave.createdAt?.toDate(),
          status: leave.status,
          icon: leave.status === 'approved' ? 'check' : leave.status === 'rejected' ? 'x' : 'clock'
        });
      });
      
      // 최근 메시지 (간단히)
      const messagesQuery = query(
        collection(db, 'directMessages'),
        limit(5)
      );
      const messagesSnapshot = await getDocs(messagesQuery);
      const myMessages = messagesSnapshot.docs
        .filter(doc => {
          const msg = doc.data();
          return msg.senderId === user.uid || msg.receiverId === user.uid;
        })
        .slice(0, 2);
      
      myMessages.forEach(doc => {
        const msg = doc.data();
        activities.push({
          id: doc.id,
          type: 'message',
          title: msg.senderId === user.uid ? '메시지 전송' : '메시지 수신',
          description: msg.message?.substring(0, 30) + '...',
          time: msg.timestamp?.toDate(),
          icon: 'message'
        });
      });
      
      // 시간순 정렬
      activities.sort((a, b) => {
        if (!a.time) return 1;
        if (!b.time) return -1;
        return b.time - a.time;
      });
      
      setRecentActivities(activities.slice(0, 8));
    } catch (error) {
      console.error('활동 내역 로드 실패:', error);
    }
  };

  // 월별 데이터 로드
  const loadMonthlyData = async () => {
    try {
      const months = ['1월', '2월', '3월', '4월', '5월', '6월'];
      const leavesData = [2, 1, 3, 2, 4, 1]; // 임시 데이터
      const approvalsData = [5, 7, 4, 8, 6, 9]; // 임시 데이터
      
      setMonthlyData({
        labels: months,
        leaves: leavesData,
        approvals: approvalsData
      });
    } catch (error) {
      console.error('월별 데이터 로드 실패:', error);
    }
  };

  const getActivityIcon = (iconType) => {
    switch (iconType) {
      case 'check': return <CheckCircle size={18} className="activity-icon success" />;
      case 'x': return <XCircle size={18} className="activity-icon error" />;
      case 'clock': return <Clock size={18} className="activity-icon warning" />;
      case 'message': return <MessageSquare size={18} className="activity-icon info" />;
      default: return <Activity size={18} className="activity-icon" />;
    }
  };

  const formatTime = (date) => {
    if (!date) return '';
    const now = new Date();
    const diff = now - date;
    const minutes = Math.floor(diff / 60000);
    const hours = Math.floor(minutes / 60);
    const days = Math.floor(hours / 24);
    
    if (days > 0) return `${days}일 전`;
    if (hours > 0) return `${hours}시간 전`;
    if (minutes > 0) return `${minutes}분 전`;
    return '방금';
  };

  if (loading) {
    return (
      <div className="dashboard-loading">
        <div className="loading-spinner"></div>
        <p>대시보드를 불러오는 중...</p>
      </div>
    );
  }

  return (
    <div className="dashboard-page">
      {/* 헤더 */}
      <div className="dashboard-header">
        <div>
          <h1>안녕하세요, {userData?.name || user?.email}님! 👋</h1>
          <p>오늘도 좋은 하루 되세요</p>
        </div>
        <div className="quick-actions">
          <button 
            className="quick-action-btn primary"
            onClick={() => setActiveTab && setActiveTab('calendar')}
          >
            <CalendarPlus size={18} />
            <span>일정 추가</span>
          </button>
          <button 
            className="quick-action-btn"
            onClick={() => setActiveTab && setActiveTab('annualLeave')}
          >
            <Plus size={18} />
            <span>연차 신청</span>
          </button>
        </div>
      </div>

      {/* 주요 통계 카드 */}
      <div className="stats-grid">
        <div className="stat-card primary">
          <div className="stat-icon">
            <CalendarIcon size={24} />
          </div>
          <div className="stat-content">
            <p className="stat-label">남은 연차</p>
            <h2 className="stat-value">{leaveStats.remaining}일</h2>
            <p className="stat-detail">
              총 {leaveStats.total}일 중 {leaveStats.used}일 사용
            </p>
          </div>
          <div className="stat-progress">
            <div 
              className="stat-progress-bar"
              style={{ width: `${(leaveStats.used / leaveStats.total) * 100}%` }}
            />
          </div>
        </div>

        <div className="stat-card success">
          <div className="stat-icon">
            <CheckCircle size={24} />
          </div>
          <div className="stat-content">
            <p className="stat-label">오늘 일정</p>
            <h2 className="stat-value">{todaySchedules.length}개</h2>
            <p className="stat-detail">
              {todaySchedules.length > 0 ? '일정이 있습니다' : '일정이 없습니다'}
            </p>
          </div>
        </div>

        {isApprover && (
          <div className="stat-card warning">
            <div className="stat-icon">
              <AlertCircle size={24} />
            </div>
            <div className="stat-content">
              <p className="stat-label">승인 대기</p>
              <h2 className="stat-value">{pendingApprovals}건</h2>
              <p className="stat-detail">확인이 필요합니다</p>
            </div>
          </div>
        )}

        <div className="stat-card info">
          <div className="stat-icon">
            <Users size={24} />
          </div>
          <div className="stat-content">
            <p className="stat-label">팀 현황</p>
            <h2 className="stat-value">{teamStats.totalEmployees}명</h2>
            <p className="stat-detail">
              연차 {teamStats.onLeave}명
            </p>
          </div>
        </div>
      </div>

      {/* 메인 컨텐츠 */}
      <div className="dashboard-content">
        {/* 좌측: 차트 & 일정 */}
        <div className="dashboard-main">
          {/* 월별 트렌드 차트 */}
          <div className="dashboard-section">
            <div className="section-header">
              <h2>
                <BarChart3 size={20} />
                <span>월별 트렌드</span>
              </h2>
            </div>
            
            <div className="chart-container">
              <div className="chart-legend">
                <div className="legend-item">
                  <span className="legend-dot blue"></span>
                  <span>연차 사용</span>
                </div>
                <div className="legend-item">
                  <span className="legend-dot green"></span>
                  <span>승인 처리</span>
                </div>
              </div>
              
              <div className="bar-chart">
                {monthlyData.labels.map((label, index) => (
                  <div key={label} className="bar-group">
                    <div className="bars">
                      <div 
                        className="bar blue"
                        style={{ height: `${(monthlyData.leaves[index] / 5) * 100}%` }}
                        title={`연차: ${monthlyData.leaves[index]}일`}
                      />
                      <div 
                        className="bar green"
                        style={{ height: `${(monthlyData.approvals[index] / 10) * 100}%` }}
                        title={`승인: ${monthlyData.approvals[index]}건`}
                      />
                    </div>
                    <span className="bar-label">{label}</span>
                  </div>
                ))}
              </div>
            </div>
          </div>

          {/* 오늘의 일정 */}
          <div className="dashboard-section">
            <div className="section-header">
              <h2>
                <CalendarIcon size={20} />
                <span>오늘의 일정</span>
              </h2>
              <button className="btn-text">전체 보기</button>
            </div>
            
            <div className="schedule-list">
              {todaySchedules.length === 0 ? (
                <div className="empty-state">
                  <CalendarIcon size={48} />
                  <p>오늘 일정이 없습니다</p>
                </div>
              ) : (
                todaySchedules.map(schedule => (
                  <div key={schedule.id} className="schedule-item">
                    <div className="schedule-time">
                      <Clock size={16} />
                      <span>{schedule.time || '종일'}</span>
                    </div>
                    <div className="schedule-content">
                      <h4>{schedule.title}</h4>
                      {schedule.description && (
                        <p>{schedule.description}</p>
                      )}
                    </div>
                    <div className={`schedule-badge ${schedule.type || 'default'}`}>
                      {schedule.type === 'meeting' && '회의'}
                      {schedule.type === 'deadline' && '마감'}
                      {schedule.type === 'event' && '이벤트'}
                      {!schedule.type && '일정'}
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>
        </div>

        {/* 우측: 활동 & 팀 통계 */}
        <div className="dashboard-sidebar">
          {/* 최근 활동 */}
          <div className="dashboard-section">
            <div className="section-header">
              <h2>
                <Activity size={20} />
                <span>최근 활동</span>
              </h2>
            </div>
            
            <div className="activity-list">
              {recentActivities.length === 0 ? (
                <div className="empty-state-small">
                  <p>활동 내역이 없습니다</p>
                </div>
              ) : (
                recentActivities.map(activity => (
                  <div key={activity.id} className="activity-item">
                    {getActivityIcon(activity.icon)}
                    <div className="activity-content">
                      <h4>{activity.title}</h4>
                      <p>{activity.description}</p>
                      <span className="activity-time">{formatTime(activity.time)}</span>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>

          {/* 팀 통계 */}
          <div className="dashboard-section">
            <div className="section-header">
              <h2>
                <PieChart size={20} />
                <span>팀 통계</span>
              </h2>
            </div>
            
            <div className="team-stats-list">
              <div className="team-stat-item">
                <div className="team-stat-icon blue">
                  <Users size={20} />
                </div>
                <div className="team-stat-content">
                  <p className="team-stat-label">전체 사원</p>
                  <h3 className="team-stat-value">{teamStats.totalEmployees}명</h3>
                </div>
              </div>

              <div className="team-stat-item">
                <div className="team-stat-icon orange">
                  <CalendarIcon size={20} />
                </div>
                <div className="team-stat-content">
                  <p className="team-stat-label">연차 중</p>
                  <h3 className="team-stat-value">{teamStats.onLeave}명</h3>
                </div>
              </div>

              <div className="team-stat-item">
                <div className="team-stat-icon purple">
                  <FileText size={20} />
                </div>
                <div className="team-stat-content">
                  <p className="team-stat-label">활성 프로젝트</p>
                  <h3 className="team-stat-value">{teamStats.activeProjects}개</h3>
                </div>
              </div>

              <div className="team-stat-item">
                <div className="team-stat-icon green">
                  <MessageSquare size={20} />
                </div>
                <div className="team-stat-content">
                  <p className="team-stat-label">오늘 메시지</p>
                  <h3 className="team-stat-value">{teamStats.messagesCount}개</h3>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Dashboard;
