import React, { useState, useEffect } from 'react';
import { db } from '../config/firebase';
import { doc, getDoc, setDoc, getDocs, collection } from 'firebase/firestore';
import { useAuthStore } from '../stores/authStore';
import './PTSchedule.css';

function PTSchedule() {
  const { isPTScheduleEditor } = useAuthStore();
  
  const [currentDate, setCurrentDate] = useState(new Date());
  const [weekSchedules, setWeekSchedules] = useState([]);
  const [loading, setLoading] = useState(false);
  const [editingWeek, setEditingWeek] = useState(null);
  const [tempSlots, setTempSlots] = useState({});
  const [tempTimeSlots, setTempTimeSlots] = useState([]);
  const [employees, setEmployees] = useState([]);

  const defaultTimeSlots = ['12:00', '13:00', '14:00', '15:00', '16:00'];
  const weekDays = ['월', '화', '수', '목', '금'];

  useEffect(() => {
    loadEmployees();
    loadMonthSchedules();
  }, [currentDate]);

  // 사원 목록 로드
  const loadEmployees = async () => {
    try {
      const snapshot = await getDocs(collection(db, 'employees'));
      const employeeList = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setEmployees(employeeList);
    } catch (error) {
      console.error('사원 목록 로드 실패:', error);
    }
  };

  // 해당 월의 주차별 날짜 계산
  const getWeeksInMonth = (year, month) => {
    const weeks = [];
    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);

    let weekStart = new Date(firstDay);
    const firstDayOfWeek = firstDay.getDay();

    if (firstDayOfWeek === 0) {
      weekStart.setDate(weekStart.getDate() - 6);
    } else if (firstDayOfWeek !== 1) {
      weekStart.setDate(weekStart.getDate() - (firstDayOfWeek - 1));
    }

    let weekNumber = 1;
    let currentWeekStart = new Date(weekStart);

    while (currentWeekStart <= lastDay) {
      const dates = [];

      for (let i = 0; i < 5; i++) {
        const date = new Date(currentWeekStart);
        date.setDate(currentWeekStart.getDate() + i);
        dates.push({
          date: new Date(date),
          isCurrentMonth: date.getMonth() === month
        });
      }

      const hasCurrentMonthDate = dates.some(d => d.isCurrentMonth);

      if (hasCurrentMonthDate) {
        const currentMonthDates = dates.filter(d => d.isCurrentMonth).map(d => d.date);
        const weekStartDate = currentMonthDates[0];
        const weekEndDate = currentMonthDates[currentMonthDates.length - 1];

        weeks.push({
          weekNumber: weekNumber,
          startDate: new Date(weekStartDate),
          endDate: new Date(weekEndDate),
          dates: dates
        });
        weekNumber++;
      }

      currentWeekStart.setDate(currentWeekStart.getDate() + 7);
    }

    return weeks;
  };

  const formatDate = (date) => {
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}-${month}-${day}`;
  };

  // 월별 스케줄 로드
  const loadMonthSchedules = async () => {
    setLoading(true);
    try {
      const year = currentDate.getFullYear();
      const month = currentDate.getMonth();
      const weeks = getWeeksInMonth(year, month);

      const yearMonth = `${year}-${String(month + 1).padStart(2, '0')}`;

      const docRef = doc(db, 'ptSchedules', yearMonth);
      const docSnapshot = await getDoc(docRef);
      const scheduleData = docSnapshot.exists() ? docSnapshot.data() : {};

      const schedulesWithData = weeks.map(week => {
        const weekKey = `week${week.weekNumber}`;
        const weekData = scheduleData[weekKey] || {};

        const timeSlots = weekData.timeSlots || defaultTimeSlots;
        const slots = weekData.slots || {};

        return {
          ...week,
          timeSlots: timeSlots,
          slots: slots
        };
      });

      setWeekSchedules(schedulesWithData);
    } catch (error) {
      console.error('스케줄 로드 실패:', error);
    }
    setLoading(false);
  };

  // 편집 시작
  const handleStartEdit = (weekNumber) => {
    const week = weekSchedules.find(w => w.weekNumber === weekNumber);
    if (week) {
      setEditingWeek(weekNumber);
      
      const slotsWithStatus = {};
      Object.keys(week.slots).forEach(key => {
        const value = week.slots[key];
        if (typeof value === 'string') {
          slotsWithStatus[key] = { name: value, cancelled: false };
        } else {
          slotsWithStatus[key] = value;
        }
      });
      setTempSlots(slotsWithStatus);
      setTempTimeSlots([...week.timeSlots]);
    }
  };

  // 편집 취소
  const handleCancelEdit = () => {
    setEditingWeek(null);
    setTempSlots({});
    setTempTimeSlots([]);
  };

  // 저장
  const handleSaveEdit = async (weekNumber) => {
    try {
      const year = currentDate.getFullYear();
      const month = currentDate.getMonth();
      const yearMonth = `${year}-${String(month + 1).padStart(2, '0')}`;
      const weekKey = `week${weekNumber}`;

      const slotsToSave = {};

      Object.keys(tempSlots).forEach(key => {
        const slot = tempSlots[key];
        const name = slot.name || '';
        if (name && name !== '' && name !== '-') {
          slotsToSave[key] = {
            name: name,
            cancelled: slot.cancelled || false
          };
        }
      });

      const docRef = doc(db, 'ptSchedules', yearMonth);
      const docSnapshot = await getDoc(docRef);
      const data = docSnapshot.exists() ? docSnapshot.data() : {};

      data[weekKey] = {
        timeSlots: tempTimeSlots,
        slots: slotsToSave
      };

      await setDoc(docRef, data);

      alert('저장되었습니다.');
      setEditingWeek(null);
      setTempSlots({});
      setTempTimeSlots([]);
      loadMonthSchedules();
    } catch (error) {
      console.error('저장 실패:', error);
      alert('저장 실패: ' + error.message);
    }
  };

  // 슬롯 변경
  const handleSlotChange = (slotKey, value) => {
    setTempSlots(prev => ({
      ...prev,
      [slotKey]: { name: value, cancelled: false }
    }));
  };

  // 슬롯 취소
  const handleCancelSlot = (slotKey) => {
    setTempSlots(prev => {
      const current = prev[slotKey] || { name: '', cancelled: false };
      return {
        ...prev,
        [slotKey]: { ...current, cancelled: true }
      };
    });
  };

  // 슬롯 재활성화
  const handleReactivateSlot = (slotKey, value) => {
    if (value === '오기입') {
      setTempSlots(prev => {
        const newSlots = { ...prev };
        delete newSlots[slotKey];
        return newSlots;
      });
    } else if (value) {
      setTempSlots(prev => ({
        ...prev,
        [slotKey]: { name: value, cancelled: false }
      }));
    }
  };

  // 시간 슬롯 추가
  const handleAddTimeSlot = () => {
    const newTime = prompt('추가할 시간을 입력하세요 (예: 17:00, 09:30)');
    if (!newTime) return;

    if (!/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]$/.test(newTime)) {
      alert('올바른 시간 형식이 아닙니다.\n예시: 17:00, 09:30, 14:15');
      return;
    }

    if (tempTimeSlots.includes(newTime)) {
      alert('이미 존재하는 시간입니다.');
      return;
    }

    setTempTimeSlots(prev => [...prev, newTime].sort());
  };

  // 시간 슬롯 삭제
  const handleRemoveTimeSlot = (timeToRemove) => {
    if (!confirm(`${timeToRemove} 시간대를 삭제하시겠습니까?\n해당 시간의 모든 데이터가 삭제됩니다.`)) return;

    setTempTimeSlots(prev => prev.filter(t => t !== timeToRemove));

    const newSlots = { ...tempSlots };
    weekDays.forEach(day => {
      delete newSlots[`${day}-${timeToRemove}`];
    });
    setTempSlots(newSlots);
  };

  // 시간 슬롯 변경
  const handleTimeSlotChange = (oldTime, newTime) => {
    if (!newTime) return;

    if (!/^([0-1]?[0-9]|2[0-3]):[0-5][0-9]$/.test(newTime)) {
      alert('올바른 시간 형식이 아닙니다 (예: 17:00)');
      return;
    }

    setTempTimeSlots(prev => prev.map(t => t === oldTime ? newTime : t));

    const newSlots = { ...tempSlots };
    weekDays.forEach(day => {
      const oldKey = `${day}-${oldTime}`;
      const newKey = `${day}-${newTime}`;
      if (newSlots[oldKey]) {
        newSlots[newKey] = newSlots[oldKey];
        delete newSlots[oldKey];
      }
    });
    setTempSlots(newSlots);
  };

  // 월별 통계 계산
  const calculateMonthlyStats = () => {
    return weekSchedules.map(week => {
      let count = 0;
      Object.values(week.slots).forEach(slot => {
        const slotData = typeof slot === 'string' ? { name: slot, cancelled: false } : slot;
        if (slotData.name && !slotData.cancelled) {
          count++;
        }
      });
      return { week, count };
    });
  };

  // 네비게이션
  const handlePrevMonth = () => {
    setCurrentDate(prev => {
      const newDate = new Date(prev);
      newDate.setMonth(prev.getMonth() - 1);
      return newDate;
    });
    setEditingWeek(null);
    setTempSlots({});
    setTempTimeSlots([]);
  };

  const handleNextMonth = () => {
    setCurrentDate(prev => {
      const newDate = new Date(prev);
      newDate.setMonth(prev.getMonth() + 1);
      return newDate;
    });
    setEditingWeek(null);
    setTempSlots({});
    setTempTimeSlots([]);
  };

  const handleToday = () => {
    setCurrentDate(new Date());
    setEditingWeek(null);
    setTempSlots({});
    setTempTimeSlots([]);
  };

  const stats = calculateMonthlyStats();
  const totalPT = stats.reduce((sum, stat) => sum + stat.count, 0);

  return (
    <div className="pt-schedule-container">
      <div className="pt-schedule-header">
        <h2>PT 스케줄</h2>
        <div className="calendar-nav">
          <button className="nav-btn" onClick={handlePrevMonth}>◀ 이전달</button>
          <button className="nav-btn" onClick={handleToday}>오늘</button>
          <button className="nav-btn" onClick={handleNextMonth}>다음달 ▶</button>
        </div>
      </div>

      <div className="pt-schedule-title">
        {currentDate.getFullYear()}년 {currentDate.getMonth() + 1}월 PT 스케줄
      </div>

      {loading ? (
        <div className="loading-state">로딩 중...</div>
      ) : weekSchedules.length === 0 ? (
        <div className="empty-state">이번 달은 표시할 주차가 없습니다.</div>
      ) : (
        <>
          <div className="weeks-container">
            {weekSchedules.map(week => {
              const isEditing = editingWeek === week.weekNumber;
              const displaySlots = isEditing ? tempSlots : week.slots;
              const displayTimeSlots = isEditing ? tempTimeSlots : week.timeSlots;

              return (
                <div key={week.weekNumber} className="week-card">
                  <div className="week-header">
                    <div>
                      <h3>💪 {week.weekNumber}주차</h3>
                      <p className="week-date">
                        {formatDate(week.startDate)} ~ {formatDate(week.endDate)}
                      </p>
                    </div>
                    {isPTScheduleEditor && (
                      <div className="week-actions">
                        {isEditing ? (
                          <>
                            <button className="btn-add-time" onClick={handleAddTimeSlot}>
                              + 시간 추가
                            </button>
                            <button className="btn-save" onClick={() => handleSaveEdit(week.weekNumber)}>
                              저장
                            </button>
                            <button className="btn-cancel" onClick={handleCancelEdit}>
                              취소
                            </button>
                          </>
                        ) : (
                          <button className="btn-edit" onClick={() => handleStartEdit(week.weekNumber)}>
                            수정
                          </button>
                        )}
                      </div>
                    )}
                  </div>

                  <div className="schedule-table-wrapper">
                    <table className="schedule-table">
                      <thead>
                        <tr>
                          <th>시간</th>
                          {week.dates.map((dateInfo, idx) => (
                            <th key={idx} className={!dateInfo.isCurrentMonth ? 'other-month' : ''}>
                              {weekDays[idx]}<br/>
                              <span className="date-number">{dateInfo.date.getDate()}</span>
                            </th>
                          ))}
                        </tr>
                      </thead>
                      <tbody>
                        {displayTimeSlots.map(time => (
                          <tr key={time}>
                            <td className="time-cell">
                              {isEditing ? (
                                <div className="time-edit">
                                  <input
                                    type="text"
                                    value={time}
                                    onChange={(e) => handleTimeSlotChange(time, e.target.value)}
                                    className="time-input"
                                  />
                                  <button
                                    onClick={() => handleRemoveTimeSlot(time)}
                                    className="time-delete-btn"
                                  >
                                    ✕
                                  </button>
                                </div>
                              ) : time}
                            </td>
                            {weekDays.map((day, idx) => {
                              const slotKey = `${day}-${time}`;
                              const slotData = displaySlots?.[slotKey];
                              const slotValue = typeof slotData === 'string' ? slotData : (slotData?.name || '');
                              const isCancelled = slotData?.cancelled || false;
                              const dateInfo = week.dates[idx];
                              const isCurrentMonth = dateInfo && dateInfo.isCurrentMonth;

                              return (
                                <td 
                                  key={slotKey} 
                                  className={`slot-cell ${!isCurrentMonth ? 'other-month' : ''}`}
                                >
                                  {!isCurrentMonth ? (
                                    <span className="disabled">-</span>
                                  ) : isEditing ? (
                                    isCancelled ? (
                                      <div className="slot-cancelled">
                                        <span className="cancelled-name">{slotValue}</span>
                                        <select
                                          value=""
                                          onChange={(e) => handleReactivateSlot(slotKey, e.target.value)}
                                          className="slot-select"
                                        >
                                          <option value="">변경</option>
                                          <option value="오기입" className="delete-option">🗑️ 오기입</option>
                                          {employees.map(emp => (
                                            <option key={emp.id} value={emp.name}>
                                              {emp.name}
                                            </option>
                                          ))}
                                        </select>
                                      </div>
                                    ) : slotValue ? (
                                      <div className="slot-filled">
                                        <span>{slotValue}</span>
                                        <button
                                          onClick={() => handleCancelSlot(slotKey)}
                                          className="slot-cancel-btn"
                                        >
                                          ✕
                                        </button>
                                      </div>
                                    ) : (
                                      <select
                                        value=""
                                        onChange={(e) => handleSlotChange(slotKey, e.target.value)}
                                        className="slot-select"
                                      >
                                        <option value="">선택</option>
                                        {employees.map(emp => (
                                          <option key={emp.id} value={emp.name}>
                                            {emp.name}
                                          </option>
                                        ))}
                                      </select>
                                    )
                                  ) : (
                                    <span className={`slot-display ${isCancelled ? 'cancelled' : ''}`}>
                                      {slotValue || '-'}
                                    </span>
                                  )}
                                </td>
                              );
                            })}
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              );
            })}
          </div>

          {/* 월별 통계 */}
          <div className="monthly-stats">
            <h3>
              {currentDate.getFullYear()}년 {currentDate.getMonth() + 1}월 PT 이용 통계
            </h3>
            <table className="stats-table">
              <thead>
                <tr>
                  <th>주차</th>
                  <th>기간</th>
                  <th>PT 횟수</th>
                </tr>
              </thead>
              <tbody>
                {stats.map(({ week, count }) => (
                  <tr key={week.weekNumber}>
                    <td>{week.weekNumber}주차</td>
                    <td>{formatDate(week.startDate)} ~ {formatDate(week.endDate)}</td>
                    <td className="count-cell">{count}회</td>
                  </tr>
                ))}
                <tr className="total-row">
                  <td colSpan="2">합계</td>
                  <td className="count-cell">{totalPT}회</td>
                </tr>
              </tbody>
            </table>
          </div>
        </>
      )}
    </div>
  );
}

export default PTSchedule;
