import React, { useState, useEffect, useRef } from 'react';
import { 
  collection, query, where, getDocs, addDoc, updateDoc, 
  doc, orderBy, onSnapshot, serverTimestamp, deleteDoc, getDoc
} from 'firebase/firestore';
import { db } from '../config/firebase';
import { useAuthStore } from '../stores/authStore';
import { 
  FileText, Plus, Search, Star, Trash2, Edit2, 
  FolderPlus, Folder, ChevronDown, ChevronRight, MoreVertical,
  Bold, Italic, Underline, Strikethrough, Link as LinkIcon,
  List, ListOrdered, Image as ImageIcon, Code, ArrowLeft
} from 'lucide-react';
import './Notes.css';

const Notes = () => {
  const { user, userData } = useAuthStore();
  
  // 프로젝트 관련
  const [projects, setProjects] = useState([]);
  const [personalProjects, setPersonalProjects] = useState([]);
  const [teamProjects, setTeamProjects] = useState([]);
  const [selectedProject, setSelectedProject] = useState(null);
  const [showCreateProject, setShowCreateProject] = useState(false);
  const [newProjectName, setNewProjectName] = useState('');
  const [newProjectType, setNewProjectType] = useState('personal');
  const [projectsExpanded, setProjectsExpanded] = useState({ personal: true, team: true });
  
  // 노트 관련
  const [notes, setNotes] = useState([]);
  const [selectedNote, setSelectedNote] = useState(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredNotes, setFilteredNotes] = useState([]);
  const [showFavorites, setShowFavorites] = useState(false);
  
  // 에디터 관련
  const [noteTitle, setNoteTitle] = useState('');
  const [noteContent, setNoteContent] = useState('');
  const [isSaving, setIsSaving] = useState(false);
  const [lastSaved, setLastSaved] = useState(null);
  const [showMobileEditor, setShowMobileEditor] = useState(false);  // 모바일 에디터 표시
  
  const editorRef = useRef(null);
  const saveTimeoutRef = useRef(null);

  // 초기 로드
  useEffect(() => {
    if (user) {
      loadProjects();
    }
  }, [user]);

  // 프로젝트 변경 시 노트 로드
  useEffect(() => {
    if (selectedProject) {
      loadNotes(selectedProject.id);
    } else {
      setNotes([]);
      setFilteredNotes([]);
    }
  }, [selectedProject]);

  // 검색 필터링
  useEffect(() => {
    filterNotes();
  }, [notes, searchTerm, showFavorites]);

  // 에디터 초기화 - 노트 선택 시
  useEffect(() => {
    if (selectedNote) {
      setNoteContent(selectedNote.content || '');
      setNoteTitle(selectedNote.title || '');
      
      // 에디터 내용 설정 (다음 렌더링 사이클에서)
      setTimeout(() => {
        if (editorRef.current && !editorRef.current.textContent) {
          editorRef.current.innerHTML = selectedNote.content || '';
        }
      }, 0);
    }
  }, [selectedNote?.id]);

  // 자동 저장
  useEffect(() => {
    if (selectedNote && (noteTitle !== selectedNote.title || noteContent !== selectedNote.content)) {
      if (saveTimeoutRef.current) {
        clearTimeout(saveTimeoutRef.current);
      }
      
      saveTimeoutRef.current = setTimeout(() => {
        saveNote();
      }, 1000);
    }
    
    return () => {
      if (saveTimeoutRef.current) {
        clearTimeout(saveTimeoutRef.current);
      }
    };
  }, [noteTitle, noteContent]);

  const loadProjects = async () => {
    try {
      const projectsRef = collection(db, 'projects');
      
      const unsubscribe = onSnapshot(projectsRef, (snapshot) => {
        const personal = [];
        const team = [];
        
        snapshot.forEach((doc) => {
          const projectData = { id: doc.id, ...doc.data() };
          
          if (projectData.type === 'personal') {
            if (projectData.userId === user.uid) {
              personal.push(projectData);
            }
          } else if (projectData.type === 'team') {
            team.push(projectData);
          }
        });
        
        personal.sort((a, b) => (a.name || '').localeCompare(b.name || ''));
        team.sort((a, b) => (a.name || '').localeCompare(b.name || ''));
        
        setPersonalProjects(personal);
        setTeamProjects(team);
        setProjects([...personal, ...team]);
        
        // 처음 프로젝트가 있으면 첫 번째 선택
        if (!selectedProject && (personal.length > 0 || team.length > 0)) {
          setSelectedProject(personal[0] || team[0]);
        }
      });
      
      return unsubscribe;
    } catch (error) {
      console.error('프로젝트 로드 실패:', error);
    }
  };

  const loadNotes = async (projectId) => {
    try {
      const notesRef = collection(db, 'notes');
      const q = query(
        notesRef,
        where('projectId', '==', projectId),
        orderBy('updatedAt', 'desc')
      );
      
      const unsubscribe = onSnapshot(q, (snapshot) => {
        const noteList = [];
        snapshot.forEach((doc) => {
          noteList.push({
            id: doc.id,
            ...doc.data()
          });
        });
        
        setNotes(noteList);
        
        // 선택된 노트가 목록에 없으면 첫 번째 선택
        if (selectedNote && !noteList.find(n => n.id === selectedNote.id)) {
          if (noteList.length > 0) {
            selectNote(noteList[0]);
          } else {
            setSelectedNote(null);
            setNoteTitle('');
            setNoteContent('');
          }
        }
      });
      
      return unsubscribe;
    } catch (error) {
      console.error('노트 로드 실패:', error);
      alert('노트 로딩 실패: ' + error.message);
    }
  };

  const filterNotes = () => {
    let filtered = [...notes];
    
    if (showFavorites) {
      filtered = filtered.filter(note => note.favorite);
    }
    
    if (searchTerm) {
      const term = searchTerm.toLowerCase();
      filtered = filtered.filter(note =>
        note.title?.toLowerCase().includes(term) ||
        note.content?.toLowerCase().includes(term)
      );
    }
    
    setFilteredNotes(filtered);
  };

  const createProject = async () => {
    if (!newProjectName.trim()) {
      alert('프로젝트 이름을 입력하세요.');
      return;
    }

    try {
      const projectData = {
        name: newProjectName.trim(),
        type: newProjectType,
        createdBy: user.uid,
        createdByName: userData?.name || userData?.email || user.email,
        createdAt: serverTimestamp()
      };

      if (newProjectType === 'personal') {
        projectData.userId = user.uid;
      }

      const docRef = await addDoc(collection(db, 'projects'), projectData);
      
      setShowCreateProject(false);
      setNewProjectName('');
      setNewProjectType('personal');
      
      // 새 프로젝트 선택
      setSelectedProject({ id: docRef.id, ...projectData });
      
      alert('프로젝트가 생성되었습니다!');
    } catch (error) {
      console.error('프로젝트 생성 실패:', error);
      alert('프로젝트 생성에 실패했습니다.');
    }
  };

  const deleteProject = async (projectId, e) => {
    e.stopPropagation();
    
    if (!confirm('프로젝트를 삭제하면 모든 노트도 함께 삭제됩니다. 계속하시겠습니까?')) {
      return;
    }

    try {
      // 1. 프로젝트 내 모든 노트 삭제
      const notesQuery = query(
        collection(db, 'notes'),
        where('projectId', '==', projectId)
      );
      const notesSnapshot = await getDocs(notesQuery);
      
      const deletePromises = notesSnapshot.docs.map(doc => deleteDoc(doc.ref));
      await Promise.all(deletePromises);
      
      // 2. 프로젝트 삭제
      await deleteDoc(doc(db, 'projects', projectId));
      
      // 3. 선택 해제
      if (selectedProject?.id === projectId) {
        const otherProjects = projects.filter(p => p.id !== projectId);
        setSelectedProject(otherProjects[0] || null);
      }
      
      alert('프로젝트가 삭제되었습니다.');
    } catch (error) {
      console.error('프로젝트 삭제 실패:', error);
      alert('프로젝트 삭제에 실패했습니다.');
    }
  };

  const createNote = async () => {
    if (!selectedProject) {
      alert('프로젝트를 먼저 선택하세요.');
      return;
    }

    try {
      const noteData = {
        projectId: selectedProject.id,
        title: '제목 없음',
        content: '',
        userId: user.uid,
        type: selectedProject.type,
        favorite: false,
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp()
      };

      const docRef = await addDoc(collection(db, 'notes'), noteData);
      
      const newNote = {
        id: docRef.id,
        ...noteData
      };
      
      selectNote(newNote);
    } catch (error) {
      console.error('노트 생성 실패:', error);
      alert('노트 생성에 실패했습니다.');
    }
  };

  const selectNote = (note) => {
    setSelectedNote(note);
    setNoteTitle(note.title || '');
    setNoteContent(note.content || '');
    
    // 에디터 내용 직접 설정 (한 번만)
    if (editorRef.current) {
      editorRef.current.innerHTML = note.content || '';
    }
    
    // 모바일에서는 에디터 보기로 전환
    if (window.innerWidth <= 768) {
      setShowMobileEditor(true);
    }
  };

  const saveNote = async () => {
    if (!selectedNote) return;

    try {
      setIsSaving(true);
      
      const noteRef = doc(db, 'notes', selectedNote.id);
      await updateDoc(noteRef, {
        title: noteTitle,
        content: noteContent,
        updatedAt: serverTimestamp()
      });
      
      setLastSaved(new Date());
      
      setTimeout(() => setIsSaving(false), 500);
    } catch (error) {
      console.error('노트 저장 실패:', error);
      setIsSaving(false);
    }
  };

  const deleteNote = async (noteId, e) => {
    e.stopPropagation();
    
    if (!confirm('이 노트를 삭제하시겠습니까?')) return;

    try {
      await deleteDoc(doc(db, 'notes', noteId));
      
      if (selectedNote?.id === noteId) {
        const otherNotes = notes.filter(n => n.id !== noteId);
        if (otherNotes.length > 0) {
          selectNote(otherNotes[0]);
        } else {
          setSelectedNote(null);
          setNoteTitle('');
          setNoteContent('');
        }
      }
      
      alert('노트가 삭제되었습니다.');
    } catch (error) {
      console.error('노트 삭제 실패:', error);
      alert('노트 삭제에 실패했습니다.');
    }
  };

  const toggleFavorite = async (noteId, currentFavorite, e) => {
    e.stopPropagation();
    
    try {
      const noteRef = doc(db, 'notes', noteId);
      await updateDoc(noteRef, {
        favorite: !currentFavorite
      });
    } catch (error) {
      console.error('즐겨찾기 토글 실패:', error);
    }
  };

  // 리치 텍스트 에디터 명령
  const execCommand = (command, value = null) => {
    document.execCommand(command, false, value);
    editorRef.current?.focus();
  };

  const insertLink = () => {
    const url = prompt('링크 URL을 입력하세요:');
    if (url) {
      execCommand('createLink', url);
    }
  };

  const handleEditorInput = () => {
    if (editorRef.current) {
      setNoteContent(editorRef.current.innerHTML);
    }
  };

  const formatLastSaved = () => {
    if (!lastSaved) return '';
    
    const now = new Date();
    const diff = now - lastSaved;
    
    if (diff < 5000) return '방금 저장됨';
    if (diff < 60000) return '저장됨';
    
    return lastSaved.toLocaleTimeString('ko-KR', { 
      hour: '2-digit', 
      minute: '2-digit' 
    }) + ' 저장됨';
  };

  return (
    <div className={`notes-page-v2 ${showMobileEditor ? 'mobile-editor-active' : ''}`}>
      {/* 좌측: 프로젝트 사이드바 */}
      <div className="projects-sidebar-v2">
        <div className="sidebar-header-v2">
          <h2>📚 노트</h2>
        </div>

        {/* 개인 프로젝트 */}
        <div className="project-section-v2">
          <div 
            className="section-header-v2"
            onClick={() => setProjectsExpanded(prev => ({ ...prev, personal: !prev.personal }))}
          >
            {projectsExpanded.personal ? <ChevronDown size={16} /> : <ChevronRight size={16} />}
            <Folder size={16} />
            <span>개인 프로젝트</span>
            <button
              className="btn-icon-tiny"
              onClick={(e) => {
                e.stopPropagation();
                setNewProjectType('personal');
                setShowCreateProject(true);
              }}
              title="프로젝트 추가"
            >
              <Plus size={14} />
            </button>
          </div>

          {projectsExpanded.personal && (
            <div className="project-list-v2">
              {personalProjects.length === 0 ? (
                <div className="empty-hint-v2">
                  <p>프로젝트가 없습니다</p>
                  <button
                    className="btn-create-hint-v2"
                    onClick={() => {
                      setNewProjectType('personal');
                      setShowCreateProject(true);
                    }}
                  >
                    첫 프로젝트 만들기
                  </button>
                </div>
              ) : (
                personalProjects.map(project => (
                  <div
                    key={project.id}
                    className={`project-item-v2 ${selectedProject?.id === project.id ? 'active' : ''}`}
                    onClick={() => setSelectedProject(project)}
                  >
                    <Folder size={16} />
                    <span className="project-name">{project.name}</span>
                    <button
                      className="btn-delete-tiny"
                      onClick={(e) => deleteProject(project.id, e)}
                      title="프로젝트 삭제"
                    >
                      <Trash2 size={12} />
                    </button>
                  </div>
                ))
              )}
            </div>
          )}
        </div>

        {/* 팀 프로젝트 */}
        <div className="project-section-v2">
          <div 
            className="section-header-v2"
            onClick={() => setProjectsExpanded(prev => ({ ...prev, team: !prev.team }))}
          >
            {projectsExpanded.team ? <ChevronDown size={16} /> : <ChevronRight size={16} />}
            <Folder size={16} />
            <span>팀 프로젝트</span>
            <button
              className="btn-icon-tiny"
              onClick={(e) => {
                e.stopPropagation();
                setNewProjectType('team');
                setShowCreateProject(true);
              }}
              title="프로젝트 추가"
            >
              <Plus size={14} />
            </button>
          </div>

          {projectsExpanded.team && (
            <div className="project-list-v2">
              {teamProjects.length === 0 ? (
                <div className="empty-hint-v2">
                  <p>프로젝트가 없습니다</p>
                  <button
                    className="btn-create-hint-v2"
                    onClick={() => {
                      setNewProjectType('team');
                      setShowCreateProject(true);
                    }}
                  >
                    첫 프로젝트 만들기
                  </button>
                </div>
              ) : (
                teamProjects.map(project => (
                  <div
                    key={project.id}
                    className={`project-item-v2 ${selectedProject?.id === project.id ? 'active' : ''}`}
                    onClick={() => setSelectedProject(project)}
                  >
                    <Folder size={16} />
                    <span className="project-name">{project.name}</span>
                    {project.createdBy === user.uid && (
                      <button
                        className="btn-delete-tiny"
                        onClick={(e) => deleteProject(project.id, e)}
                        title="프로젝트 삭제"
                      >
                        <Trash2 size={12} />
                      </button>
                    )}
                  </div>
                ))
              )}
            </div>
          )}
        </div>

        {/* 프로젝트 추가 버튼 (하단) */}
        <div className="project-add-section-v2">
          <button
            className="btn-add-project-v2"
            onClick={() => setShowCreateProject(true)}
          >
            <FolderPlus size={16} />
            <span>새 프로젝트 만들기</span>
          </button>
        </div>
      </div>

      {/* 중앙: 노트 목록 */}
      <div className="notes-list-v2">
        <div className="notes-list-header-v2">
          <div className="search-box-v2">
            <Search size={16} />
            <input
              type="text"
              placeholder="노트 검색..."
              value={searchTerm}
              onChange={e => setSearchTerm(e.target.value)}
            />
          </div>
          
          <div className="list-actions-v2">
            <button
              className={`btn-filter-v2 ${showFavorites ? 'active' : ''}`}
              onClick={() => setShowFavorites(!showFavorites)}
              title="즐겨찾기만 보기"
            >
              <Star size={16} fill={showFavorites ? 'currentColor' : 'none'} />
            </button>
            
            <button
              className="btn-primary-v2"
              onClick={createNote}
              disabled={!selectedProject}
              title="새 노트"
            >
              <Plus size={16} />
              <span>새 노트</span>
            </button>
          </div>
        </div>

        <div className="notes-list-content-v2">
          {!selectedProject ? (
            <div className="empty-state-v2">
              <Folder size={48} />
              <p>프로젝트를 선택하세요</p>
            </div>
          ) : filteredNotes.length === 0 ? (
            <div className="empty-state-v2">
              <FileText size={48} />
              <p>{searchTerm || showFavorites ? '노트가 없습니다' : '첫 노트를 만들어보세요'}</p>
              {!searchTerm && !showFavorites && (
                <button
                  className="btn-create-large-v2"
                  onClick={createNote}
                >
                  <Plus size={20} />
                  <span>노트 만들기</span>
                </button>
              )}
            </div>
          ) : (
            filteredNotes.map(note => (
              <div
                key={note.id}
                className={`note-item-v2 ${selectedNote?.id === note.id ? 'active' : ''}`}
                onClick={() => selectNote(note)}
              >
                <div className="note-item-header-v2">
                  <h3>{note.title || '제목 없음'}</h3>
                  <div className="note-item-actions-v2">
                    <button
                      className="btn-icon-small"
                      onClick={(e) => toggleFavorite(note.id, note.favorite, e)}
                      title={note.favorite ? '즐겨찾기 해제' : '즐겨찾기'}
                    >
                      <Star size={14} fill={note.favorite ? 'currentColor' : 'none'} />
                    </button>
                    <button
                      className="btn-icon-small"
                      onClick={(e) => deleteNote(note.id, e)}
                      title="노트 삭제"
                    >
                      <Trash2 size={14} />
                    </button>
                  </div>
                </div>
                
                <p className="note-preview-v2">
                  {note.content ? 
                    note.content.replace(/<[^>]*>/g, '').substring(0, 100) : 
                    '내용 없음'}
                </p>
                
                <div className="note-item-footer-v2">
                  <span className="note-date-v2">
                    {note.updatedAt?.toDate ? 
                      note.updatedAt.toDate().toLocaleDateString('ko-KR', { 
                        month: 'short', 
                        day: 'numeric' 
                      }) : 
                      '날짜 없음'}
                  </span>
                </div>
              </div>
            ))
          )}
        </div>
      </div>

      {/* 우측: 에디터 */}
      <div className="note-editor-v2">
        {!selectedNote ? (
          <div className="editor-empty-v2">
            <FileText size={64} />
            <h3>노트를 선택하세요</h3>
            <p>좌측에서 노트를 선택하거나 새로 만들어보세요</p>
          </div>
        ) : (
          <>
            {/* 에디터 헤더 */}
            <div className="editor-header-v2">
              {/* 모바일 뒤로가기 버튼 */}
              <button
                className="mobile-back-btn"
                onClick={() => setShowMobileEditor(false)}
                title="목록으로"
              >
                <ArrowLeft size={20} />
              </button>
              
              <input
                type="text"
                className="note-title-input-v2"
                placeholder="제목을 입력하세요..."
                value={noteTitle}
                onChange={e => setNoteTitle(e.target.value)}
              />
              
              <div className="editor-status-v2">
                {isSaving ? (
                  <span className="saving">저장 중...</span>
                ) : (
                  <span className="saved">{formatLastSaved()}</span>
                )}
              </div>
            </div>

            {/* 도구바 */}
            <div className="editor-toolbar-v2">
              <div className="toolbar-group-v2">
                <select 
                  className="toolbar-select-v2"
                  onChange={(e) => execCommand('formatBlock', e.target.value)}
                  defaultValue="p"
                >
                  <option value="p">본문</option>
                  <option value="h1">제목 1</option>
                  <option value="h2">제목 2</option>
                  <option value="h3">제목 3</option>
                </select>
              </div>

              <div className="toolbar-divider-v2" />

              <div className="toolbar-group-v2">
                <button
                  className="toolbar-btn-v2"
                  onClick={() => execCommand('bold')}
                  title="굵게 (Ctrl+B)"
                >
                  <Bold size={16} />
                </button>
                <button
                  className="toolbar-btn-v2"
                  onClick={() => execCommand('italic')}
                  title="기울임 (Ctrl+I)"
                >
                  <Italic size={16} />
                </button>
                <button
                  className="toolbar-btn-v2"
                  onClick={() => execCommand('underline')}
                  title="밑줄 (Ctrl+U)"
                >
                  <Underline size={16} />
                </button>
                <button
                  className="toolbar-btn-v2"
                  onClick={() => execCommand('strikeThrough')}
                  title="취소선"
                >
                  <Strikethrough size={16} />
                </button>
              </div>

              <div className="toolbar-divider-v2" />

              <div className="toolbar-group-v2">
                <button
                  className="toolbar-btn-v2"
                  onClick={() => execCommand('insertUnorderedList')}
                  title="글머리 기호 목록"
                >
                  <List size={16} />
                </button>
                <button
                  className="toolbar-btn-v2"
                  onClick={() => execCommand('insertOrderedList')}
                  title="번호 매기기 목록"
                >
                  <ListOrdered size={16} />
                </button>
              </div>

              <div className="toolbar-divider-v2" />

              <div className="toolbar-group-v2">
                <button
                  className="toolbar-btn-v2"
                  onClick={insertLink}
                  title="링크 삽입"
                >
                  <LinkIcon size={16} />
                </button>
                <button
                  className="toolbar-btn-v2"
                  onClick={() => execCommand('formatBlock', 'pre')}
                  title="코드 블록"
                >
                  <Code size={16} />
                </button>
              </div>
            </div>

            {/* 리치 텍스트 에디터 */}
            <div
              ref={editorRef}
              className="rich-editor-v2"
              contentEditable
              dir="ltr"
              onInput={handleEditorInput}
              suppressContentEditableWarning
              data-placeholder="내용을 입력하세요..."
            />
          </>
        )}
      </div>

      {/* 프로젝트 생성 모달 */}
      {showCreateProject && (
        <div className="modal-overlay-v2" onClick={() => setShowCreateProject(false)}>
          <div className="modal-content-v2" onClick={e => e.stopPropagation()}>
            <div className="modal-header-v2">
              <h2>새 프로젝트 만들기</h2>
              <button onClick={() => setShowCreateProject(false)}>×</button>
            </div>

            <div className="modal-body-v2">
              <div className="form-group-v2">
                <label>프로젝트 이름 *</label>
                <input
                  type="text"
                  placeholder="예: 마케팅 프로젝트"
                  value={newProjectName}
                  onChange={e => setNewProjectName(e.target.value)}
                  autoFocus
                />
              </div>

              <div className="form-group-v2">
                <label>프로젝트 타입</label>
                <div className="radio-group-v2">
                  <label>
                    <input
                      type="radio"
                      value="personal"
                      checked={newProjectType === 'personal'}
                      onChange={e => setNewProjectType(e.target.value)}
                    />
                    <span>개인 프로젝트</span>
                  </label>
                  <label>
                    <input
                      type="radio"
                      value="team"
                      checked={newProjectType === 'team'}
                      onChange={e => setNewProjectType(e.target.value)}
                    />
                    <span>팀 프로젝트</span>
                  </label>
                </div>
              </div>
            </div>

            <div className="modal-footer-v2">
              <button
                className="btn-secondary-v2"
                onClick={() => setShowCreateProject(false)}
              >
                취소
              </button>
              <button
                className="btn-primary-v2"
                onClick={createProject}
              >
                <FolderPlus size={16} />
                <span>프로젝트 만들기</span>
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Notes;
